import React from 'react';
import { makeStyles } from '@material-ui/styles';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import StarIcon from '@material-ui/icons/Star';
import StarBorderIcon from '@material-ui/icons/StarBorder';
import Tooltip from '@material-ui/core/Tooltip';

import { FixedSizeList } from 'react-window';

// import useTranslation from '../hooks/useTranslation';


const useStyles = makeStyles((theme) => ({
  container: {
    height: 600,
  },
  oddListItem: {
    backgroundColor: theme.palette.action.hover,
  },
  starButton: {
    "&:hover": {
      backgroundColor: theme.palette.primary.light
    }
  },
  submitButton: {
    width: "100%",
    justifyContent: "left",
    "&:hover": {  
      backgroundColor: theme.palette.primary.light
    }
  },
}));


const MdiHistoryList = (props) => {
  const {
    mdiHistory,
    mdiHistoryInFavoritesMap,
    onPopulate,
    onAddFavorite,
    onRemoveFavorite,
  } = props;
  // const { t } = useTranslation();
  const classes = useStyles();

  const handlePopulateCommand = (cmd) => {
    if(onPopulate){
      onPopulate(cmd)
    }
  };  

  const handleAddMdiFavorite = (cmd) => {
    if(onAddFavorite){
      onAddFavorite(cmd)
    }
  };

  const handleRemoveMdiFavorite = (cmd) => {
    if(onRemoveFavorite){
      let cmdSet = new Set();
      cmdSet.add(cmd);
      onRemoveFavorite(cmdSet)
    }
  };

  const Row = ({index, style}) => {
    const newStyle = {
      ...style,
      overflow: 'auto'
    };
    const {cmd, time} = mdiHistory[index];
    return(
      <Grid container direction="row" justify="space-between" alignItems="stretch" overflow="hidden" className={(index % 2 === 0) ? null : classes.oddListItem} style={newStyle}>
        <Grid item container overflow="hidden" xs={9} md={11}>
          <Tooltip title={<span>{new Date(time).toLocaleString("en-US")}</span>}>
          <Button
            onClick={(e) => handlePopulateCommand(cmd)}
            className={classes.submitButton}
            overflow="hidden"
          >
            { cmd }
          </Button>
          </Tooltip>
        </Grid>
        <Grid item container justify="flex-end" xs={3} md={1}>
        {
          (mdiHistoryInFavoritesMap.get(cmd)).isFav ? (
            <Button 
              size="small" 
              onClick={(e) => { handleRemoveMdiFavorite(cmd) }}
              className={classes.starButton}  
            ><StarIcon/></Button>
          ) : (
            <Button 
              size="small" 
              onClick={(e) => { handleAddMdiFavorite(cmd) }}
              className={classes.starButton}
            ><StarBorderIcon/></Button>
          )
        }
        </Grid >
      </Grid>
    )
  };

  return(<>
    <div className={classes.container}>
      <FixedSizeList
        height={560}
        itemCount={mdiHistory.length}
        itemSize={50}
        width={'100%'}
      >
        {Row}
      </FixedSizeList>
    </div>
  </>
  )
};

export default MdiHistoryList
