import { MODULE_ID, DEBUG, INFO, WARNING, ERROR, BASIC } from './constants';
import { createActions } from 'redux-actions';

import { StatusItems, CommandItems } from './api';

import * as cmds from './commands';
import camelCase from 'to-camel-case';
import { v4 as uuidv4 } from 'uuid';

export const rockhopperMessage = (type, payload) => ({ type, payload });
export const addActionEmitterToMeta = (meta={}, actionEmitter) => ({ ...meta, actionEmitters: [ ...(meta.actionEmitter ? meta.actionEmitter : []), actionEmitter ] });

// This function creates a type that mimics how createActions does, but instead of having
// a nested dictionary, it is provided an array of strings.
export const createRockhopperActionType = function() { return [ MODULE_ID, ...arguments ].join("/") ; };

// This function should also match how createActions creates their names
export const createFriendlyName = (name) => camelCase(name)

const watchableItems = StatusItems.filter((item) => item.watchable);

const getActions = StatusItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  const id = "GET_" + type;
  const actionCreator = (opt={}) => {
    const {
      meta = {},
      data = {}
    } = opt;
    return cmds.get(id, name, data, addActionEmitterToMeta(meta, createRockhopperActionType("UPDATE", type))); 
  };
  
  a[type] = actionCreator;

  return a; 
}, {});

const watchActions = watchableItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  const id = "WATCH_" + type;
  a[type] = (opt={}) => {
    const {
      data={},
      meta={}
    } = opt;
    return cmds.watch(id, name, data, addActionEmitterToMeta(meta, createRockhopperActionType("UPDATE", type)));
  };
  return a; 
}, {});

const unwatchActions = watchableItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  const id = "UNWATCH_" + type;
  a[type] = (opt={}) => {
    const {
      meta={}
    } = opt;
    return cmds.unwatch(id, name, addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "UNWATCH", type)));
  };
  return a; 
}, {});

// Update actions occur when a watched status item changes
// These are created so reducers can respond to actions.update.<status item name in camel case>.
// The default rockhopper root reducer will respond to all get actions and store the returned
// data in rockhopper.status.<status item name in camel case>.
const updateActions = watchableItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  a[type] = undefined; 
  return a; 
}, {});
const putActions = CommandItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  const id = "PUT_" + type;
  a[type] = (opt={}) => {
    const {
      data = {},
      meta = {}
    } = opt;
    return cmds.put(id, name, data, addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "PUT", type)))
  }; 
  return a; 
}, {});

// These are created so clients can use actions.response.put.<command item name in camel case> in their own reducers
const putResponseActions = CommandItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  a[type] = undefined; 
  return a; 
}, {});

// These are created so clients can use actions.response.get.<status item name in camel case> in their own reducers
const getResponseActions = StatusItems.reduce((a, item) => {
  const {
    name
  } = item;
  const type = name.toUpperCase();
  a[type] = undefined; 
  return a; 
}, {});

// These are helper actions that aren't directly deduced from the API, but are helpful for a specific use case.
// For example, the FEATURES helper actually gets the config_item StatusItem with the section parameter set to POCKETNC_FEATURES.
// Get helper functions could feasibly be moved to the get section if a StatusItem in Rockhopper is created to handle what the helper
// does.
const getHelpers = {
  FEATURES: (opt={}) => { 
    const { meta, id="getFeatures" } = opt; 
    return cmds.get(id, 
                    "config_item", 
                    { section: "POCKETNC_FEATURES", parameter: "" }, 
                    addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "GET_HELPERS", "FEATURES"))) 
  },
  PROGRAM_PREFIX: (opt={}) => {
    const { meta, id="getProgramPrefix" } = opt; 
    return cmds.get(id, 
                    "config_item", 
                    { section: "DISPLAY", parameter: "PROGRAM_PREFIX" }, 
                    addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "GET_HELPERS", "PROGRAM_PREFIX"))) 
  },
};

// Similar to the getHelpers:
// These are helper actions that aren't directly deduced from the API, but are helpful for a specific use case.
// For example, the JOG_CONTINUOUS and JOG_STOP actions both use the jog_cmd action with different parameters. It's nice to see
// JOG_CONTINUOUS or JOG_STOP in the redux dev tools rather than having to inspect a generic JOG_CMD action.
// Put helper functions could feasibly be moved to the put section if a CommandItem in Rockhopper is created to handle what the helper
// does.
const putHelpers = {
  JOG_INCREMENT: ({ axis, jointFlag=true, speed, increment, id="jogIncrement", meta }) => cmds.put(id, 'jog_cmd', [ "JOG_INCREMENT", jointFlag, axis, speed, increment ], addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "PUT_HELPERS", "JOG_INCREMENT"))),
  JOG_CONTINUOUS: ({ axis, jointFlag=true, speed, id="jogContinuous", meta }) => cmds.put(id, 'jog_cmd', [ "JOG_CONTINUOUS", jointFlag, axis, speed ], addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "PUT_HELPERS", "JOG_CONTINUOUS"))),
  JOG_STOP: ({ axis, jointFlag=true, id="jogStop", meta }) => cmds.put(id, 'jog_cmd', [ "JOG_STOP", jointFlag, axis ], addActionEmitterToMeta(meta, createRockhopperActionType("RESPONSE", "PUT_HELPERS", "JOG_STOP"))),
};

const updateHelpers = {
  COMPENSATION: undefined,
  CONFIG_OVERLAY: undefined,
  TOOL_TABLE_DESCRIPTIONS: undefined,
  TOOL_TABLE: undefined,
  TOOL_DIAMETER: undefined,
  TOOL_LENGTH_OFFSET: undefined,
  TOOL_FLUTES: undefined,
  SET_DISPLAY_UNITS: undefined
}

// These are automatically generated actions from the getHelpers and putHelpers so that reducers can handle
// the responses from rockhopper by reducing actions.response.getHelpers.<helper name> or actions.response.putHelpers.<helper name>
// actions.
const getHelperResponses = Object.keys(getHelpers).reduce((responses, helperName) => { responses[helperName] = undefined; return responses; }, {});
const putHelperResponses = Object.keys(putHelpers).reduce((responses, helperName) => { responses[helperName] = undefined; return responses; }, {});

export const statusItemToRequiresFeature = StatusItems.reduce((all,item) => {
  const {
    name,
    requiresFeature
  } = item;

  all[createFriendlyName(name)] = requiresFeature;
  return all;
}, {});

export default createActions({
  CMDS: {
    RAW: undefined,
    GET: ({ name, data={}, meta, id="rockhopperGet" }) => cmds.get(id, name, data, meta),
    WATCH: ({ name, data={}, meta, id="rockhopperWatch" }) => cmds.watch(id, name, data, meta),
    UNWATCH: (opt={}) => {
      const { meta, id="rockhopperWatch" } = opt;
      return cmds.watch(id, meta);
    },
    PUT: ({ name, data, meta, id="rockhopperPut" }) => cmds.put(id, name, data, meta),
    LIST_GET: ({ meta, id="rockhopperListGet" }) => cmds.listGet(id, meta),
    LIST_PUT: ({ meta, id="rockhopperListPut" }) => cmds.listPut(id, meta),
  },
  SET_CONNECTION_DETAILS: (server, port, user, password, encrypted) => ({ server, port, user, password, encrypted }),
  SET_LOGGED_IN: undefined,
  ADD_ACTION_EMITTER: undefined,
  REMOVE_ACTION_EMITTER: undefined,

  FILE_UPLOAD: {
    QUEUED: undefined,
    ASK_FOR_OVERWRITE: undefined,
    CONFIRM_OVERWRITE: undefined,
    CONFIRM_OVERWRITE_ALL: undefined,
    PROGRESS: undefined,
    FINALIZE: undefined,
    CANCEL: undefined,
    CANCEL_ALL: undefined,
    REMOVE: undefined,
    REMOVE_ALL: undefined
  },
  FILE_DOWNLOAD: {
    QUEUED: undefined,
    PROGRESS: undefined,
    FINALIZE: undefined,
    CANCEL: undefined,
    CANCEL_ALL: undefined,
    REMOVE: undefined,
    REMOVE_ALL: undefined
  },


  FILE_CACHE: {
    SET_PATH_CHECKSUM: (path, checksum) => ({ path, checksum }),
    INIT_PROGRESS: (checksum, size) => ({ checksum, size }),
    UPDATE_PROGRESS: (checksum, bytesLoaded) => ({ checksum, bytesLoaded }),
  },
  
  // put helper actions
  // helper actions that rely on specific get command/parameter combinations
  GET_HELPERS: getHelpers,
  PUT_HELPERS: putHelpers,
  UPDATE_HELPERS: updateHelpers,

  MESSAGES: {
    // message is a string, details is optional but should contain helpful information about the error
    DEBUG: (message, details, dismiss=BASIC) => ({ id: uuidv4(), message, details, type: DEBUG, timestamp: Date.now(), dismiss }),
    WARNING: (message, details, dismiss=BASIC) => ({ id: uuidv4(), message, details, type: WARNING, timestamp: Date.now(), dismiss }),
    INFO: (message, details, dismiss=BASIC) => ({ id: uuidv4(), message, details, type: INFO, timestamp: Date.now(), dismiss }),
    ERROR: (message, details, dismiss=BASIC) => ({ id: uuidv4(), message, details, type: ERROR, timestamp: Date.now(), dismiss })
  },

  WATCH: watchActions,
  UNWATCH: unwatchActions,
  UPDATE: updateActions,
  GET: getActions,
  PUT: putActions,
  RESPONSE: {
    PUT: putResponseActions,
    GET: getResponseActions,
    GET_HELPERS: getHelperResponses,
    PUT_HELPERS: putHelperResponses,
  },

  DELAYED: {
    REGISTER_UPDATE: updateActions,
    UPDATE: updateActions,
    UPDATE_IMMEDIATE: updateActions,
    PUT: putActions
  }
}, { prefix: MODULE_ID });
