import { connect } from 'react-redux';
import Spindle from '../components/Spindle';
import actions from '../modules/rockhopper/actions';

import { selectMeasuredSpindleSpeed,
         selectMaximumSpindleSpeed,
         selectSpindlePower,
         selectSpindleLoad,
         selectSpindleTemperature,
         selectSpindlePressure,
         selectHasHss,
         selectHssWarmUpNeeded,
         selectHssFullWarmUpNeeded,
         selectHssPerformingWarmUp,
         selectReadyForMdi } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  return { 
    rpm: selectMeasuredSpindleSpeed(state),
    maxRPM: selectMaximumSpindleSpeed(state),
    power: selectSpindlePower(state),
    load: selectSpindleLoad(state),
    temperature: selectSpindleTemperature(state),
    pressure: selectSpindlePressure(state),
    needsWarmUp: selectHssWarmUpNeeded(state),
    showWarmUpButton: selectHasHss(state),
    needsLongWarmUp: selectHssFullWarmUpNeeded(state),
    disableWarmUpButton: !(selectReadyForMdi(state).isReady) || selectHssPerformingWarmUp(state),
  }
}, 
(dispatch, ownProps) => ({ 
  onWarmUp: (needsLong) => {
    dispatch(actions.put.mdiCmd({ data: [ needsLong ? "M670" : "M671" ] }));
  }
}),
(stateProps, dispatchProps, ownProps) => {
  const {
    onWarmUp
  } = dispatchProps;
  const {
    needsLongWarmUp
  } = stateProps;
  return {
    ...stateProps,
    ...dispatchProps,
    ...ownProps,
    onWarmUp: () => onWarmUp(needsLongWarmUp)
  }
}
)(Spindle);
