import actions from './actions';
import rockhopperActions from '../rockhopper/actions';
import { selectMessages } from './selectors';
import { MESSAGE_LOG_KEY } from './constants';
import { takeEvery, fork, delay, put, select, call } from 'redux-saga/effects';
import { BASIC } from '../rockhopper/constants';

const persistToSessionStorage = (key, data) => {
  window.sessionStorage.setItem(key, data);
};

export function *persistMessageLog(action) {
  const messages = yield select(selectMessages);
  yield call(persistToSessionStorage, MESSAGE_LOG_KEY, JSON.stringify(messages));
}

export function* handleMessage(action) {
  const msg = action.payload;
  yield fork(persistMessageLog);
  yield put(actions.popup.show(msg));
  if(msg.dismiss === BASIC) {
    yield delay(3000);
    yield put(actions.popup.hide(msg)); // this may be a no op if message has already been dismissed
  }
}

export function* messageSaga() {
  yield takeEvery([
    rockhopperActions.messages.debug,
    rockhopperActions.messages.info,
    rockhopperActions.messages.warning,
    rockhopperActions.messages.error 
  ], handleMessage);
}
