const OverlayRow = (props) => {
  const {
    classes,
    isEditing,
    index,
    rowStyle,
    values,

    onChange,
    onDelete,
    ...other
  } = props;

  const handleChangeField = (field, value) => {
    if(onChange){
      const newValues = produce(values, (draft) => {draft[field] = value})
      onChange(index, newValues);
    }
  };
  const handleDelete = () => {
    onDelete(values.key);
  }

  return(
    <TableRow key={values.key} style={rowStyle} {...other}>
      <TableCell className={classes.tableCell} style={{flexGrow: 2}}>
        <SubmittableTextField
          disabled={!isEditing}
          value={values.name}
          onChange={(newText) => {handleChangeField('name', newText)}}
        />
      </TableCell>
      <TableCell className={classes.tableCell}>
        <SubmittableTextField
          disabled={!isEditing}
          value={values.value}
          onChange={(newText) => { handleChangeField('value', newText)}}
        />
      </TableCell>
      <TableCell className={classes.tableCell}>
        <SubmittableTextField
          disabled={!isEditing}
          value={values.comment}
          onChange={(newText) => { handleChangeField('comment', newText)}}
        />
      </TableCell>
      <TableCell className={classes.tableCell}>
        <SubmittableTextField
          disabled={!isEditing}
          value={values.help}
          onChange={(newText) => { handleChangeField('help', newText)}}
        />
      </TableCell>
      {isEditing && (
        <TableCell style={{flexGrow: 0}}>
          <IconButton onClick={handleDelete}><DeleteIcon/></IconButton>
        </TableCell>
      )}
    </TableRow>
  )
}

const OverlaySectionPanel = (props) => {
  const {
    value,
    index,
    isEditing,
    sectionName,
    params,
    onChange,
  } = props;
  const { t } = useTranslation()
  const classes = useStyles();
  const tableRef = useRef();
  const containerRef = useRef();
  const bodyRef = useRef();

  const handleAddParameter = (addIdx, newParam) => {
    const changedParams = produce(params, (draft) => { draft["new" + Date.now()] = { name: "", comment: "", value: "", help: "", section: sectionName } });
    onChange(sectionName, changedParams)
  };
  const handleDeleteParameter = (paramKey) => {
    const newParams = produce(params, (draft) => { delete draft[paramKey] });
    onChange(sectionName, newParams)
  };
  const handleChangeParam = (paramsArrIdx, changedParam) => {
    const changedParams = produce(params, (draft) => { draft[changedParam.key] = changedParam });
    onChange(sectionName, changedParams)
  }

  return (
    <TabPanel key={sectionName} value={value} index={index}>
      <TableContainer component={Paper} className={classes.tableContainer} ref={containerRef}>
        <Table stickyHeader className={classes.table} aria-label="simple table" id="asdf" ref={tableRef}>
          <TableHead>
            <TableRow>
              <TableCell style={{flexGrow: 2}}>{t("Name")}</TableCell>
              <TableCell>{t("Value")}</TableCell>
              <TableCell>{t("Comment")}</TableCell>
              <TableCell>{t("Help")}</TableCell>
              {isEditing && (<TableCell className={classes.tableButtonCell}/>)}
            </TableRow>
          </TableHead>  
          <TableBody ref={bodyRef}>
          { 
            Object.keys(params).sort().map((key, idx) => {
              return(
                <OverlayRow
                  key={key}
                  index={idx}
                  values={{...params[key], key: key}}
                  classes={classes}
                  isEditing={isEditing}
                  onChange={handleChangeParam} 
                  onDelete={handleDeleteParameter}
                />
              )
            })
          }
          { isEditing && (
            <TableRow key={'add'}>
              <TableCell style={{height: 0, padding: 0, border: 0}}>
                <IconButton onClick={handleAddParameter}>
                  <AddBoxOutlinedIcon/>
                </IconButton>
              </TableCell>
            </TableRow>
          )}
          </TableBody>
        </Table>
      </TableContainer>
    </TabPanel>
  )
}


const OverlayPanel = (props) => {
  const { 
    isEditing,
    clientOverlay,
    diskOverlay,
    children, 
    value, 
    index,
    p,
    xsLayout,
    onEnableEditing,
    onDeleteSection,
    onDisableEditing,
    onAddSection,
    onSave,
    onChange,
    ...other
  } = props;
  const theme = useTheme();
  const classes = useStyles();

  const [tabIndex, setTabIndex] = useState(0);
  const [addSectionDialogOpen, setAddSectionDialogOpen] = useState(false);
  const displayOverlay = isEditing ? clientOverlay : diskOverlay;

  const handleChangeTabs = useCallback((event, newTabIndex) => {
    setTabIndex(newTabIndex);
  },[setTabIndex]);
  const handleAddSection = (newSectionName) => {
    onAddSection(clientOverlay, newSectionName);
    setAddSectionDialogOpen(false);
  };
  const handleChangeSection = (sectionName, sectionParams) => {
    const newOverlay = produce(clientOverlay, (draft) => {
      draft[sectionName] = sectionParams;
      return draft;
    });
    onChange(newOverlay);
  };
  const handleEnableEditing = () =>{ onEnableEditing(diskOverlay) }
  const handleDeleteSection = () => { onDeleteSection(Object.keys(displayOverlay).sort()[tabIndex]) };
  const handleSave = () => { onSave(displayOverlay); }

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <>
          <Box border={1} borderColor={theme.palette.grey[200]} className={classes.iniRoot}>
          <div className={classes.tabs}>
          { 
          isEditing ? (
          <div style={{display: 'flex', justifyContent: 'center', margin: theme.spacing(1)}}>

            <ButtonGroup disabled={!isEditing}>
              <Button
                color='primary'
                variant={'outlined'}
                onClick={() => setAddSectionDialogOpen(true)} label={'clicker'}
              >
                <AddIcon/>
              </Button>
              <Button
                color='primary'
                variant={'outlined'}
                onClick={handleDeleteSection} label={'clicker'}
              >
                <DeleteIcon/>
              </Button>
            </ButtonGroup>
          </div> ) : ""
          }
            <Tabs
              className={classes.tabs}
              style={xsLayout ? {} : { height : isEditing ? 348 : 400} }
              value={tabIndex < Object.keys(displayOverlay).length ? tabIndex : 0}
              onChange={handleChangeTabs}
              orientation={xsLayout ? "horizontal" : "vertical"}
              variant="scrollable"
              scrollButtons="off"
            >
              { displayOverlay ? Object.keys(displayOverlay).sort().map((sectionName, idx) => {
                  return( <Tab key={idx} label={sectionName} {...a11yProps(idx)}/>)
                }) : ""
              }
            </Tabs>

            </div>
            {
              displayOverlay ? (
                Object.keys(displayOverlay).sort().map((sectionName, idx) => {
                  return(
                    <OverlaySectionPanel
                      key={idx}
                      value={tabIndex}
                      index={idx}
                      isEditing={isEditing}
                      sectionName={sectionName}
                      params={displayOverlay[sectionName]}
                      onChange={handleChangeSection}
                    />
                  )
                })
               ) : ""
            }
          </Box>
          <div className={classes.lowerRightButtonDiv}>
          { 
            isEditing ? (
              <ButtonGroup size="large">
                <Tooltip title={"Send changes to server"}>
                  <Button onClick={handleSave}>SAVE</Button>
                </Tooltip>
                <Tooltip title={"Discard changes"}>
                    <Button onClick={onDisableEditing}>DISCARD</Button>
                </Tooltip>
              </ButtonGroup>
            ) : (
              <ButtonGroup  size="large">
                <Tooltip title={"Enable overlay editing"}>
                  <Button
                    onClick={handleEnableEditing}
                  >Edit Overlay</Button>
                </Tooltip>
              </ButtonGroup>
            )
          }
          </div>
          <SubmittableDialog
            open={addSectionDialogOpen}
            value={""}
            onSubmit={handleAddSection}
            onClose={() => { setAddSectionDialogOpen(false) } }
            title={"Add Section"}
            textFieldLabel={"SECTION_NAME"}
            submitBtnLabel={"Add"}
            cancelBtnLabel="Cancel"
          />
        </>
      )}
    </div>
  );
}

const IniPanel = (props) => {
  const { 
    ini,
    xsLayout,

    children, 
    value, 
    index,
    p,
    ...other 
  } = props;
  const { t } = useTranslation();
  const classes = useStyles();
  
  const [tabIndex, setTabIndex] = useState(0);
  const handleChangeTabs = useCallback((event, newTabIndex) => {
    setTabIndex(newTabIndex);
  },[setTabIndex]);

  const iniMap = ini;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <>
          <Box className={classes.iniRoot}>
            <Tabs
              value={tabIndex}
              onChange={handleChangeTabs}
              orientation={xsLayout ? "horizontal" : "vertical"}
              variant="scrollable"
              scrollButtons="off"
              className={classes.tabs}
            >
              {iniMap ? Object.keys(iniMap).map((sectionName, idx) => {
                return(
                <Tab key={idx} label={sectionName} {...a11yProps(idx)}/>
                )
              }) : ""}
            </Tabs>
            {iniMap ? Object.values(iniMap).map((section, idx) => {
              return(
                <TabPanel key={idx} value={tabIndex} index={idx}>
                   <TableContainer component={Paper} className={classes.tableContainer}>
                    <Table stickyHeader className={classes.table} aria-label="simple table">
                      <TableHead>
                        <TableRow>
                          <TableCell>{t("Name")}</TableCell>
                          <TableCell>{t("Value")}</TableCell>
                          <TableCell>{t("Comment")}</TableCell>
                          <TableCell>{t("Help")}</TableCell>
                        </TableRow>
                      </TableHead>
                      <TableBody>
                      {Object.values(section).map((values, paramIdx) => {
                        return(
                        <TableRow key={paramIdx}>
                          <TableCell className={classes.tableCell}>{values.name}</TableCell>
                          <TableCell className={classes.tableCell}>{values.value}</TableCell>
                          <TableCell className={classes.tableCell}>{values.comment}</TableCell>
                          <TableCell className={classes.tableCell}>{values.help}</TableCell>
                        </TableRow>
                        )
                      })}
                      </TableBody>
                    </Table>
                  </TableContainer>
                </TabPanel>
                )
              }) : ""}
          </Box>
        </>
      )}
    </div>
  );
}


const CompensationPanel = (props) => {
  const { 
    isEditing,
    clientCompensation,
    diskCompensation,

    onDisableEditing,
    onEnableEditing,
    onChange,
    onSave,

    xsLayout,

    children, 
    value, 
    index, 
    p, 
    ...other 
  } = props;
  const compensation = isEditing ? clientCompensation : diskCompensation;
  const theme = useTheme();
  const classes = useStyles();
  const { t } = useTranslation();

  const [tabIndex, setTabIndex] = useState(0);
  const handleChangeTabs = useCallback((event, newTabIndex) => {
    setTabIndex(newTabIndex);
  },[setTabIndex]);

  const handleEnableEditing = () => { onEnableEditing(diskCompensation); };
  const handleDisableEditing = () => { onDisableEditing(); };
  const handleAddRow = (axis, idx) => {
    const newCompensation = produce(clientCompensation, (draft) => {
      draft[axis].splice(idx + 1, 0, ["","",""])
    });
    onChange(newCompensation);
  }
  const handleDeleteRow = (axis, idx) => {
    const newCompensation = produce(clientCompensation, (draft) => {
      draft[axis].splice(idx, 1)
    }); 
    onChange(newCompensation);
  }
  const handleChange = (axis, idx, rowIdx, value) => {
    const newCompensation = produce(clientCompensation, (draft) => {
      draft[axis][idx][rowIdx] = value
      return draft;
    });
    onChange(newCompensation);
  };
  const handleSave = () => { onSave(clientCompensation); };

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
      <>
        <Box border={1} borderColor={theme.palette.grey[200]} className={classes.iniRoot}>
          <Tabs
            value={tabIndex}
            onChange={handleChangeTabs}
            orientation={xsLayout ? "horizontal" : "vertical"}
            variant="scrollable"
            scrollButtons="on"
            className={classes.tabs}
          >
            {compensation ? Object.keys(compensation).sort().map((axisName, idx) => {
              return(
              <Tab key={idx} label={axisName} {...a11yProps(idx)}/>
              )
            }) : ""}
          </Tabs>
          {compensation ? Object.keys(compensation).sort().map((axisName, idx) => {
            return(
              <TabPanel key={idx} value={tabIndex} index={idx}>
                <TableContainer component={Paper} className={classes.tableContainer}>
                  <Table stickyHeader className={classes.table} aria-label="simple table">
                    <TableHead>
                      <TableRow>
                        <TableCell>{t("Position")}</TableCell>
                        <TableCell>{t("Forward Trim")}</TableCell>
                        <TableCell>{t("Reverse Trim")}</TableCell>
                        {isEditing && (
                          <>
                          <TableCell className={classes.tableButtonCell}/>
                          <TableCell className={classes.tableButtonCell}/>
                          </>
                        )}
                      </TableRow>
                    </TableHead>
                    <TableBody>
                    {compensation[axisName].map((dataRow, idx) => {
                      return <TableRow key={idx} className={idx % 2 === 0 ? classes.tableAlternateRow : ""}>
                        {dataRow.map((val, rowIdx) => {
                          return <TableCell key={rowIdx}>
                            <SubmittableTextField
                              value={val}
                              disabled={!isEditing} 
                              onChange={(e) => handleChange(axisName, idx, rowIdx, e)}
                            />
                          </TableCell>
                        })}
                        {isEditing && (
                          <>
                          <TableCell>
                            <IconButton onClick={(e) => {handleAddRow(axisName, idx)}}><AddBoxOutlinedIcon/></IconButton>
                          </TableCell>
                          <TableCell>
                            <IconButton onClick={(e) => {handleDeleteRow(axisName, idx)}}><DeleteIcon/></IconButton>
                          </TableCell>
                          </>
                        )}
                        </TableRow>
                    })}
                  </TableBody>
                  </Table>
                </TableContainer>
              </TabPanel>
              )
            }) : ""}
        </Box>
        { 
          <div className={classes.lowerRightButtonDiv}>
          {
          isEditing ? (
            <ButtonGroup size="large">
              <Tooltip title={t("Send changes to server")}>
                <Button onClick={handleSave}>{t("save")}</Button>
              </Tooltip>
              <Tooltip title={t("Discard changes")}>
                  <Button onClick={handleDisableEditing}>{t("cancel")}</Button>
              </Tooltip>
            </ButtonGroup>
          ) : (
            <ButtonGroup  size="large">
              <Tooltip title={t("Enable compensation editing")}>
                <Button
                  onClick={handleEnableEditing}
                >{t("Edit Compensation")}</Button>
              </Tooltip>
            </ButtonGroup>
          )
          }
          </div>
        }
      </>
      )}
    </div>
  );
}


function a11yProps(name, index) {
  return {
    id: `${name}-simple-tab-${index}`,
    'aria-controls': `${name}-simple-tabpanel-${index}`,
  };
}

const MachineConfig = (props) => {
  const {
    ini,

    isEditingCompensation,
    clientCompensation,
    diskCompensation,
    onEnableEditingCompensation,
    onDisableEditingCompensation,
    onChangeCompensation,
    onSaveCompensation,

    isEditingOverlay,
    clientOverlay,
    diskOverlay,
    onAddOverlaySection,
    onDeleteOverlaySection,
    onEnableEditingOverlay,
    onDisableEditingOverlay,
    onChangeOverlay,
    onSaveOverlay,
    onUploadFiles,
    onDownloadFiles,
    onRestartServices,
  } = props;
  
  const xsLayout = useMediaQuery(theme => theme.breakpoints.down("xs"));
  const classes = useStyles();
  const { t } = useTranslation();
  const [downloadTag, setDownloadTag] = useState("");
  const [tabIndex, setTabIndex] = useState(2);
  const handleChangeTabs = useCallback((event, newValue) => {
    setTabIndex(newValue);
  },[setTabIndex]);

  const inputRef = useRef(null);
  const handleClick = useCallback((e) => {
    inputRef.current.click();
  }, [ inputRef ]);
  const handleFileSelect = (e) => {
    for(let file of e.target.files) {
      const path = file.name;
      const data = file;
      onUploadFiles(path, data);
    }
    e.target.value = "";
  };
  const handleDownloadClick = () => {
    onDownloadFiles(downloadTag)
  }
  const handleRestartClick = () => {
    onRestartServices()
  }

  return (
    <Panel id="MachineConfig" label={t("Machine Config")}>
      <div style={{ display: "none" }}><input onChange={handleFileSelect} type="file" multiple ref={inputRef}/></div>
      <Box>
        <Box>
          <Tooltip placement="bottom" title={t("downloadConfigurationFiles")}>
            <Button onClick={() => {handleDownloadClick()}} size="large" variant="outlined" className={classes.joinedButton}>
              {("download")}
            </Button>
          </Tooltip>
          <SubmittableTextField 
            InputProps={{classes: { notchedOutline: classes.joinedTextField, input: classes.joinedInput } }}
            variant="outlined" 
            placeholder={t('serialNumber')}
            value={downloadTag ? downloadTag : ""}
            onChange={(val)=> { setDownloadTag(val) }}
          />
          <Tooltip placement="bottom" title={t("uploadConfigurationFiles")}>
            <Button onClick={handleClick} size="large" variant="outlined" className={classes.button}>
              {t("upload")}
            </Button>
          </Tooltip>
          <Button onClick={() => {handleRestartClick()}} size="large" variant="outlined" className={classes.button}>
            {t("restartServices")}
          </Button>
        </Box>
      </Box>
      <Box>
        <Tabs
          value={tabIndex}
          onChange={handleChangeTabs}
          orientation="horizontal"
          className={classes.tabsBox}
        >
          <Tab label={t("INI Overlay")} {...a11yProps("overlay", 0)}/>
          <Tab label={t("Active INI")} {...a11yProps("ini", 1)}/>
          <Tab label={t("Compensation Tables")} {...a11yProps("compensation", 2)}/>
        </Tabs>
        <Box className={classes.tabContainer}>
            <OverlayPanel 
              value={tabIndex} index={0} xsLayout={xsLayout} 
              clientOverlay={clientOverlay} 
              diskOverlay={diskOverlay}
              isEditing={isEditingOverlay} 
              onAddSection={onAddOverlaySection} 
              onChange={onChangeOverlay} 
              onDeleteSection={onDeleteOverlaySection}
              onDisableEditing={onDisableEditingOverlay}
              onEnableEditing={onEnableEditingOverlay} 
              onSave={onSaveOverlay}
            />
            <IniPanel value={tabIndex} index={1} ini={ini} xsLayout={xsLayout}/>
            <CompensationPanel value={tabIndex} index={2} xsLayout={xsLayout}
              isEditing={isEditingCompensation}
              clientCompensation={clientCompensation} 
              diskCompensation={diskCompensation}
              onEnableEditing={onEnableEditingCompensation} 
              onDisableEditing={onDisableEditingCompensation} 
              onChange={onChangeCompensation} 
              onSave={onSaveCompensation}
            />
        </Box>
      </Box>
    </Panel>
  );
};
export default MachineConfig;
