import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './containers/App';
import * as serviceWorker from './serviceWorker';

import { createStore } from 'redux-dynamic-modules';
import { getSagaExtension } from 'redux-dynamic-modules-saga';
import { Provider } from 'react-redux';
import { composeWithDevTools} from 'redux-devtools-extension';
import produce from 'immer';
import getRockhopperModule from './modules/rockhopper';
import getPentaUIModule from './modules/pentaui';
import getRouterModule from './modules/router';
import './i18n';

const store = createStore({
  initialState: produce({}, () => {}),
  enhancers: [],
  extensions: [ getSagaExtension() ],
  compose: composeWithDevTools
}, getRockhopperModule(), 
   getPentaUIModule(), 
   getRouterModule())

ReactDOM.render(
// Material UI v4.10.2 has findDOMNode calls that trigger warnings in the console
// StrictMode can be commented out to hide them.
  <React.StrictMode>
    <Provider store={store}>
    <App />
    </Provider>
  </React.StrictMode>,
  document.getElementById('root')
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
