import React, { useRef, useState, useEffect } from 'react';

import { Button } from '@material-ui/core';
import ToolTouchOffIcon from '@material-ui/icons/Flare';
import { makeStyles } from '@material-ui/core/styles';
import Tooltip from '@material-ui/core/Tooltip';

import produce from 'immer';
import { FixedSizeList } from 'react-window';

import SubmittableTextField from './SubmittableTextField';
import classnames from 'classnames';
import useTranslation from '../hooks/useTranslation';
import useUnits from '../hooks/useUnits';

import {TOOL_ENTRY_INDICES, UNITS} from '../modules/rockhopper/constants';

//styling constants
const DESCRIPTION_MIN_WIDTH = 200;
const CELL_WIDTH = 120;
const MEASURE_BUTTON_WIDTH = 65;
const ROW_MIN_WIDTH = 727;
const SCROLLBAR_WIDTH = 15;
const TABLE_HEIGHT = 250;
const ROW_HEIGHT = 50
const TOOLNUM_WIDTH = 60;
//other constants
const TOOL_COUNT = 55;
const FIELDS_PER_TOOL = 14;

const useStyles = makeStyles((theme) => ({
  container: {
    display: 'flex',
    flexDirection: 'column',
    overflow: 'scroll',
  },
  cell: {
    display: 'flex',
    flex: `0 0 ${CELL_WIDTH}px`,
    justifyContent: 'center',
    verticalAlign: "top"
  },
  fixedSizeList: {
    display: 'flex',
    width: '100%',
    overflowX: 'visible',
    overflowY: 'scroll',
  },
  measureButton: {
    height: 39
  },
  row: {
    boxSizing: 'border-box',
    display: 'flex',
    flexDirection: 'row',
    justifyContent: 'flex-end',
    width: '100%'
  },
  headerRow: {
    display: 'flex',
    direction: 'rtl',
    justifyContent: 'flex-end',
    width: '100%',
  },
  headerCell: { 
    display: 'flex',
    flex: `0 0 ${CELL_WIDTH}px`,
    justifyContent: 'center',
    textAlign: 'center',
    verticalAlign: 'top',
  },
  textFieldInput: {
    ...theme.typography.dataText,
    border: '2px solid ' + theme.palette.primary.main,
    borderRadius: 4,
    borderWidth: 2,
    direction: 'ltr',
    padding: theme.spacing(1),
    marginLeft: theme.spacing(0.5),
    marginRight: theme.spacing(0.5),
    textAlign: 'left',
    '&:hover': {
      border: "2px solid" + theme.palette.primary.light,
    },
    '&:disabled': {
      border: "2px solid" + theme.palette.grey[200],
    },
  },
  numericalTextFieldInput: {
    ...theme.typography.dataText,
    border: '2px solid ' + theme.palette.primary.main,
    borderRadius: 4,
    borderWidth: 2,
    direction: 'ltr',
    marginLeft: theme.spacing(0.5),
    marginRight: theme.spacing(0.5),
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1),
    '&:hover': {
      border: "2px solid" + theme.palette.primary.light,
    },
    '&:disabled': {
      border: "2px solid" + theme.palette.grey[200],
    },
  },
  textFieldNotchedOutline: {
    border: "none",
  },
  toolNumButton: {
    ...theme.typography.dataText,
    border: "2px solid " + theme.palette.text.primary,
    marginLeft: theme.spacing(1),
    marginRight: theme.spacing(0.5),
    height: 39
  },
  activeToolNumberBox: {
    backgroundColor: theme.palette.text.primary,
    color: theme.palette.primary.main,
  },
}));


const ToolTable = (props) => {
  const {
    toolInSpindle,
    toolTable,
    toolTableDescriptions,
    toolFlutes,
    readyForMdi,
    onMeasureTool,
    onLoadTool,
    onToolDescriptionChanged,
    onToolFlutesChanged,
    onSetToolLengthOffset,
    onSetToolDiameter,
  } = props;

  const classes = useStyles();
  const { t } = useTranslation();
  const { getDisplayUnitStringFromType, toDisplayUnits } = useUnits();
  const decimalPoints = toolTable ? UNITS.DECIMALS(toDisplayUnits(toolTable[0][TOOL_ENTRY_INDICES.Z_OFFSET]).unit) : 0;

  //Save scroll position and restore on re-render
  const scrollRef = useRef(0)
  const listRef = useRef();
  useEffect(() => {
    if(listRef.current && scrollRef.current !== 0){
      listRef.current.scrollTo(scrollRef.current)
    }
  })

  //For narrower screen widths where horizontal scrolling is needed, we will set padding-right on the 
  //react-window FixedSizeList to prevent scrolling within the FixedSizeList div, and instead force the scrolling into 
  //the div which contains both the header row and the FixedSizeList.
  const headerRef = useRef();
  const [pad, setPad] = useState(0);
  
  useEffect(() => {
    function handleResize() {
      setPad(headerRef.current.offsetWidth < ROW_MIN_WIDTH ? (ROW_MIN_WIDTH + SCROLLBAR_WIDTH) - (headerRef.current.offsetWidth) : 0)
    }
    //Set pad value on initial load
    handleResize();
    //Respond to window size changes.
    window.addEventListener("resize", handleResize);

    return () => window.removeEventListener("resize", handleResize);
  }, []);


  const handleMeasureTool = (toolNumber) => {
    if(onMeasureTool) {
      onMeasureTool(toolNumber);
    }
  };
  const handleLoadTool = (toolNumber) => {
    if(onLoadTool) {
      onLoadTool(toolNumber);
    }
  };
  const handleSetToolOffset = (e, oldOffset, toolNumber) => {
    const value = Number(e.target.value);
    if(onSetToolLengthOffset && ! isNaN(value)) {
      onSetToolLengthOffset(toolNumber, { value: value, unit: toDisplayUnits(oldOffset).unit } );
    }
  };
  const handleSetToolDiameter = (e, oldDiameter, toolNumber) => {
    const value = Number(e.target.value);
    if(onSetToolDiameter && ! isNaN(value)) {
      onSetToolDiameter(toolNumber, { value: value, unit: toDisplayUnits(oldDiameter).unit } );
    }
  };
  const handleSetToolDescription = (toolNumber, description) => {
    if(onToolDescriptionChanged) {
      onToolDescriptionChanged(toolNumber, description, toolTableDescriptions)
    }
  };
  const handleSetToolFlutes = (toolNumber, flutes) => {
    if(onToolFlutesChanged) {
      onToolFlutesChanged(produce(toolFlutes ? toolFlutes : new Array(toolTable.length).map(() => 1), (draft) => { draft.splice(toolNumber - 1, 1, flutes) }), flutes)
    }
  };

  const Row = ({ index, style }) => { //This style prop is one coming out of FixedSizeList. I don't understand the details but it is needed.
    const tool = toolTable && toolTable[index] ? toolTable[index] : Array(FIELDS_PER_TOOL).fill({value: 0, unit: ""});
    const offsetQuantity = toDisplayUnits(tool[TOOL_ENTRY_INDICES.Z_OFFSET]);
    const diameterQuantity = toDisplayUnits(tool[TOOL_ENTRY_INDICES.DIAMETER]);
    //Reminder: the row direction is rtl to make the vertical scrollbar visible
    return(
      <div className={classes.row} style={style}>
        <span 
          className={classes.cell} 
          style={{ 
            flexGrow: 1,
            minWidth: DESCRIPTION_MIN_WIDTH,
          }}
        >
          <SubmittableTextField
            disabled={!readyForMdi.isReady}
            fullWidth={true}
            variant='outlined'
            tool={tool}
            value={ 
              toolTableDescriptions && toolTableDescriptions[tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX] - 1] ? (
                toolTableDescriptions[tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX] -1]
              ) : ("")
            }
            onSubmit={
              (e) => {
                handleSetToolDescription(tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX], e.target.value)
              }
            }
            InputProps={{
              classes: {
                input: classes.textFieldInput,
                notchedOutline: classes.textFieldNotchedOutline,
              }
            }}
          />
        </span>
        <span className={classes.cell}>
          <SubmittableTextField
            disabled={!readyForMdi.isReady}
            fullWidth={true}
            variant='outlined'
            value={
              toolFlutes && toolFlutes[tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX] - 1] ? (
                toolFlutes[tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX] -1]
              ) : ("1")
            }
            onSubmit={
              (e) => {
                handleSetToolFlutes(tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX], e.target.value)
              }
            }
            InputProps={{
              classes: {
                input: classes.numericalTextFieldInput,
                notchedOutline: classes.textFieldNotchedOutline,
              }
            }}
          />
        </span>
        <span className={classes.cell} >
          <SubmittableTextField
            disabled={!readyForMdi.isReady}
            fullWidth={true}
            variant='outlined'
            tool={tool}
            value={diameterQuantity.value.toFixed(decimalPoints)}
            onSubmit={
              (e) => {
                handleSetToolDiameter(e, diameterQuantity, tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX]);
              }
            }
            InputProps={{
              classes: {
                input: classes.numericalTextFieldInput,
                notchedOutline: classes.textFieldNotchedOutline,
              }
            }}
          />
        </span>
        <span className={classes.cell} >
          <SubmittableTextField
            disabled={!readyForMdi.isReady}
            fullWidth={true}
            variant='outlined'
            
            tool={tool}
            value={offsetQuantity.value.toFixed(decimalPoints)}
            onSubmit={
              (e) => {
                handleSetToolOffset(e, offsetQuantity, tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX]);
              }
            }
            InputProps={{
              classes: {
                input: classes.numericalTextFieldInput,
                notchedOutline: classes.textFieldNotchedOutline,
              }
            }}
          />
        </span>
        <span className={classes.cell} style={{ flex: `0 0 ${TOOLNUM_WIDTH}px` }}>
          <Button 
            className={classnames(classes.toolNumButton, { [classes.activeToolNumberBox]: index+1 === Number(toolInSpindle) })}
            onClick={() => {handleLoadTool(tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX])}}
            variant="outlined"
            size="small"
          >
            <span >
              {"T " + tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX]  }
              </span>
          </Button>
        </span>

        <span className={classes.cell} style={{flex: `0 0 ${MEASURE_BUTTON_WIDTH}px`}}>
        { readyForMdi.isReady ? 
        <Tooltip title={t("toolTouchOff")}>
          <Button
            className={classes.measureButton}
            color='primary' 
            variant={'contained'} 
            disabled={!readyForMdi.isReady}
            onClick={() => {handleMeasureTool(tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX])}}
            size="small"
          >
            <ToolTouchOffIcon/>
          </Button>
          </Tooltip>
          : 
          <Button
            className={classes.measureButton}
            color='primary' 
            variant={'contained'} 
            disabled={!readyForMdi.isReady}
            onClick={() => {handleMeasureTool(tool[TOOL_ENTRY_INDICES.TOOL_TABLE_INDEX])}}
            size="small"
          >
            <ToolTouchOffIcon/>
          </Button>
        }
        </span>
      </div>
    )
  };

  return(
    <div className={classes.container}>
      <div style={{width: '100%', paddingRight: pad}}>
          <div className={classes.headerRow} ref={headerRef}>
            <span className={classes.headerCell} style={{ flexGrow: 1,minWidth: DESCRIPTION_MIN_WIDTH}}>{t("description")}</span>
            <span className={classes.headerCell}>{t("flutes")}</span>
            <span className={classes.headerCell}>{t("diameter") + " (" + getDisplayUnitStringFromType('length') + ")"}</span>
            <span className={classes.headerCell}>{t("tlo") + " (" + getDisplayUnitStringFromType('length') + ")"}</span>
            <span className={classes.headerCell} style={{flex: `0 0 ${TOOLNUM_WIDTH}px`}}></span>
            <span className={classes.headerCell} style={{ marginLeft: SCROLLBAR_WIDTH, flex: `0 0 ${MEASURE_BUTTON_WIDTH}px`}} ></span>
          </div>
      </div>
    {toolTable ? (
      <FixedSizeList
        ref={listRef}
        height={TABLE_HEIGHT}
        itemCount={toolTable ? toolTable.length : TOOL_COUNT}
        itemSize={ROW_HEIGHT}
        className={classes.fixedSizeList}
        direction='rtl'
        style={{
          paddingRight: pad
        }}
        onScroll={(event) => {
          if(event.scrollOffset !== scrollRef.current && !event.scrollUpdateWasRequested){
            scrollRef.current = event.scrollOffset
          }
        }}
      >
        {Row}
      </FixedSizeList>
      ) : null}
    </div>
  )
};
export default ToolTable;
