import React from 'react';
import Panel from './Panel';
import useTranslation from '../hooks/useTranslation';
import useUnits from '../hooks/useUnits';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';
import Tooltip from '@material-ui/core/Tooltip';
import HomeAllButton from '../containers/HomeAllButton';

import { makeStyles, useTheme } from '@material-ui/styles';
import colorInterpolate from 'color-interpolate';
import classnames from 'classnames';

import { /*G5X_SYSTEMS,*/ UNITS } from '../modules/rockhopper/constants';
import { getDroDisplayData } from '../util/functions';

const useStyles = makeStyles((theme) => ({
  axisRow: {
    marginBottom: theme.spacing(1),
    whiteSpace: "nowrap",
  },
  axisLabel: {
    ...theme.typography.axisLabel,
    display: "inline-block",
    width: 40,
    height: 40,
    borderTopLeftRadius: 4,
    lineHeight: "40px",
    verticalAlign: "middle",
    textAlign: "center",
    backgroundColor: theme.palette.grey[200],
    border: "1px solid " + theme.palette.grey[200]
  },
  axisLabelNoBar: {
    borderBottomLeftRadius: 4
  },
  axisUnit: {
    ...theme.typography.axisValue,
    display: "inline-block",
    height: 40,
    width: 40,
    verticalAlign: "middle",
    borderTopRightRadius: 4,
    borderRight: "1px solid " + theme.palette.grey[200],
    borderTop: "1px solid " + theme.palette.grey[200],
    borderBottom: "1px solid " + theme.palette.grey[200],
    lineHeight: "40px",
  },
  axisUnitFraction: {
    margin: "0 auto",
    fontSize: "18px",
    display: "inline-block",
    position: "relative",
    textAlign: "center",
    lineHeight: 1,
    width: "fit-content"
  },
  axisUnitFractionSuper: {
    display: "block",
    textAlign: "center",
    verticalAlign: "super"
  },
  axisUnitFractionSub: {
    borderTop: "1px solid black",
    display: "block",
    position: "relative",
    verticalAlign: "sub"
  },
  axisUnitWhole: {
    fontSize: "22px",
  },
  axisValue: {
    ...theme.typography.axisValue,
    display: "inline-block",
    height: 40,
    width: "calc(100% - 83px - " + theme.spacing(1) + "px)",
    lineHeight: "40px",
    verticalAlign: "middle",
    textAlign: "right",
    borderTop: "1px solid " + theme.palette.grey[200],
    borderBottom: "1px solid " + theme.palette.grey[200],
    paddingRight: theme.spacing(1)
  },
  axisValueNoBar: {
    borderBottomRightRadius: 4
  },
  bar: {
    position: "relative",
    width: "calc(100% - 2px)",
    height: 15,
    borderBottomLeftRadius: 4,
    borderBottomRightRadius: 4,
    borderRight: "1px solid " + theme.palette.grey[200],
    borderLeft: "1px solid " + theme.palette.grey[200],
    borderBottom: "1px solid " + theme.palette.grey[200],
    textAlign: "right",
    overflow: "none",
    zIndex: 200
  },
  filledBar: {
    position: "absolute",
    height: 15,
    width: "100%",
    borderBottomLeftRadius: 3,
    borderBottomRightRadius: 3,
    backgroundImage: "linear-gradient(to right, " + theme.palette.primary.main + "," + theme.palette.secondary.main + ")",
    zIndex: 0
  },
  menuSpan: {
    width: "100%",
  },
  whiteBar: {
    position: "absolute",
    height: 15,
    right: 0,
    width: "90%",
    borderBottomRightRadius: 3,
    backgroundColor: "white",
    zIndex: 1
  },
  barText: {
    position: "absolute",
    right: 0,
    fontSize: "smaller",
    lineHeight: "14px",
    verticalAlign: "top",
    paddingRight: 2,
    zIndex: 100
  },
  button: {
    marginRight: theme.spacing(1)
  },
  select: {
    height: "30px"
  },
  homeAllButton: {
    marginLeft: theme.spacing(1),
  },
  extras: {
    display: "flex",
    marginBottom: theme.spacing(1)
  }
}));

const DRO = (props) => {
  const {
    display,

    positions,
    workOffsets,
    g5xIndex,

    onSetDisplay,
  } = props;

  const handleChangeDisplay = (event) => {
    onSetDisplay(event.target.value);
  };
  
  const { t } = useTranslation();
  const { toDisplayUnits, getDisplayUnitStringFromUnit } = useUnits();

  const theme = useTheme();
  const classes = useStyles();

  const displayData = getDroDisplayData(display, positions, workOffsets, g5xIndex);
  
  const elements = displayData && positions.map(({ axis, jointIndex, torque, ...data }, index) => {
    const valueDisplayUnits = toDisplayUnits(displayData[index]);// toDisplayUnits(data[display]);
    const displayUnitString = getDisplayUnitStringFromUnit(valueDisplayUnits.unit);
    const decimalPoints = UNITS.DECIMALS(valueDisplayUnits.unit);
    const slashIndex = displayUnitString.indexOf("/");
    const percentage = Math.min(torque*100*5, 100);
    const hasPercentage = typeof(torque) !== "undefined" && torque !== null;
    const colorMap = colorInterpolate([ theme.palette.primary.main, theme.palette.secondary.main ]);
    // const isRotary = jointIndex > 2;
    // data.velocity = data.velocity*60; // this is a dirty hack to get it into units/minute rather than units/second, this should be taken out once we implement display units
    return (<div key={axis} className={classes.axisRow} id={"DRO_" + axis}>
      <div>
        <span className={classnames({ [classes.axisLabel]: true, [classes.axisLabelNoBar]: !hasPercentage })}>{axis}</span>
        <span className={classnames({ [classes.axisValue]: true, [classes.axisValueNoBar]: !hasPercentage })}>
          <span>{ valueDisplayUnits.value.toFixed(decimalPoints)}</span>
        </span>
        <span className={classes.axisUnit}>
        { slashIndex > 0 ?
              <span className={classes.axisUnitFraction}>
                <span className={classnames({ [classes.axisUnitFractionSuper]: true })} >{getDisplayUnitStringFromUnit(valueDisplayUnits.unit).slice(0,slashIndex) }</span>
                <span className={classnames({  [classes.axisUnitFractionSub]: true })}>{getDisplayUnitStringFromUnit(valueDisplayUnits.unit).slice(slashIndex + 1) }</span>
              </span>
              : 
              (<span className={classes.axisUnitWhole}>{getDisplayUnitStringFromUnit(valueDisplayUnits.unit) }</span>)
          }
        </span>
        {/* <span className={classnames({ [classes.axisValue]: true, [classes.axisValueNoBar]: !hasPercentage })}>{ JSON.stringify(data[display])}</span> */}
      </div>
      { hasPercentage ?
      <div className={classes.bar}>
        <div className={classes.filledBar} style={{ width: percentage + "%", backgroundImage: "linear-gradient(to right, " + theme.palette.primary.main + "," + colorMap(percentage/100) + ")"}}></div>
        <span className={classes.barText}>{ Math.round(percentage) }%</span>
      </div> : null }
    </div>);
  });

  return <Panel id="DRO" label={t("dro")}>
    <div className={classes.extras}>  
      <Select variant="outlined" value={display} onChange={handleChangeDisplay} className={classes.select}>
        <MenuItem value="position"><Tooltip title={t('tooltip_OffsetPosition')}><span className={classes.menuSpan}>{ t("position") }</span></Tooltip></MenuItem>
        <MenuItem value="actualPosition"><Tooltip title={t('tooltip_ActualPosition')}><span className={classes.menuSpan}>{ t("actualPosition") }</span></Tooltip></MenuItem>
        <MenuItem value="dtg"><Tooltip title={t('tooltip_DTG')}><span className={classes.menuSpan}>{ t("dtg") }</span></Tooltip></MenuItem>
        <MenuItem value="velocity">{ t("velocity") }</MenuItem>
        {/* {G5X_SYSTEMS.map((sys, idx) => {
          return(
            <MenuItem key={sys} value={sys}><Tooltip title={t('tooltip_G5xPosition')}><span>{ `${t("position")}: ${t("coordinateSystem")} ${idx + 1}`}</span></Tooltip></MenuItem>
          )
        })} */}
      </Select>
      <HomeAllButton classes={ { button: classes.homeAllButton }}/>
    </div>
    { elements }
  </Panel>;
};
export default DRO;
