import React from 'react';
import { useHistory, useLocation } from "react-router-dom";
import { CONFIG_TAB } from "./Tabs";
import ClientTab from './ClientTab';
import ServerTab from './ServerTab';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';

export const CLIENT_TAB = "/client";
export const SERVER_TAB = "/server";
export const tabs = { 
  [CLIENT_TAB]: { id: CLIENT_TAB, label: "Client", component: ClientTab }, 
  [SERVER_TAB]: { id: SERVER_TAB, label: "Server", component: ServerTab }
};
export const tabOrder = [ tabs[CLIENT_TAB], tabs[SERVER_TAB] ];

const ConfigTab = (props) => {
  const location = useLocation();
  const history = useHistory();
  const urlTab = location.pathname.replace(CONFIG_TAB, "");
  const subTab = tabs[urlTab] ? urlTab : tabOrder[0].id;

  const setTab = (id) => {
    history.push(CONFIG_TAB + id);
  };
  
  const handleChange = (event, newValue) => {
    setTab(newValue);
  };

  const TabComponent = tabs[subTab].component;

  return <>
    <Tabs value={subTab} onChange={handleChange}>
      { tabOrder.map((tab) => <Tab label={tab.label} value={tab.id} key={tab.id}/>) }
    </Tabs>
    <TabComponent/>
  </>;
};
export default ConfigTab;
