import { connect } from 'react-redux';
import BottomBar from '../components/BottomBar';
import rockhopperActions from '../modules/rockhopper/actions';
import { selectEstop } from '../modules/rockhopper/selectors';

export default connect(
  (state, ownProps) => ({
    isInEstop: selectEstop(state)
  }), 
  (dispatch, ownProps) => ({ 
    onAbort: () => dispatch(rockhopperActions.put.abortCmd()),
    onDisableEstop: () => dispatch(rockhopperActions.put.estopCmd({ data: [ false ]}))
  }),
  (stateProps, dispatchProps, ownProps) => ({
    buttonLabel: stateProps.isInEstop ? "estop" : "reset",
    buttonColor: stateProps.isInEstop ? "error" : "secondary",
    onButtonClick: stateProps.isInEstop ? dispatchProps.onDisableEstop : dispatchProps.onAbort,
  })
)(BottomBar);
