import React, { useEffect, useState }  from 'react';
import Tabs from './Tabs';
import { useTheme, makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  underlineSVG: {
    position: "fixed",
    top: 78
  }
}));

const TopBar = (props) => {
  const classes = useStyles();
  const [ width, setWidth ] = useState(window.innerWidth);
  const theme = useTheme();
  useEffect(() => {
    const onResize = () => {
      setWidth(window.innerWidth);
    };
    window.addEventListener("resize", onResize);
    window.addEventListener("orientationchange", onResize);
    return () => {
      window.removeEventListener("resize", onResize);
      window.removeEventListener("orientationchange", onResize);
    }
  }, []);

  const height = 8;
  const lineThickness =  1;

  // math for adding extra curve at bottom of buttons
  const rounding = 6;
  const angle = 20*Math.PI/180;
//  const curveHeight = strokeWidth;
  const curveHeight = height;
  const beginCurvePct = .15;
  const beginCurve = beginCurvePct*width;
  const endCurve = (1-beginCurvePct)*width;
  const curveTop = 0;
  const curveBottom = height;
  const curveX = rounding*Math.cos(angle);
  const curveY = rounding-rounding*Math.sin(angle);

  const curvePts = [
    [ "M", 0, curveBottom-lineThickness ],
    [ "L", beginCurve, curveBottom-lineThickness ],
    [ "a", rounding, rounding, 0, 0, 0, curveX, -curveY ],
    [ "L", beginCurve+curveX+(curveHeight-2*curveY-lineThickness)*Math.tan(angle), curveTop+curveY ],
    [ "a", rounding, rounding, 0, 0, 1, curveX, -curveY ],
    [ "L", endCurve-curveX-(curveHeight-2*curveY-lineThickness)*Math.tan(angle)-curveX, curveTop ],
    [ "a", rounding, rounding, 0, 0, 1, curveX, curveY ],
    [ "L", endCurve-curveX, curveBottom-curveY-lineThickness ],
    [ "a", rounding, rounding, 0, 0, 0, curveX, curveY],
    [ "L", width, curveBottom-lineThickness ],
    [ "L", width, curveBottom ],
    [ "L", 0, curveBottom ],
    [ "Z" ]
  ];

return <div>
    <Tabs/>
    <svg width={width} height={height} className={classes.underlineSVG}>
    <path d={curvePts.map((cmd) => cmd.join(" ")).join(" ")}
          fill={theme.palette.text.primary}/>
  </svg>
  </div>
};

export default TopBar;
