import React, { useCallback, useState, useEffect, useRef } from 'react';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  focus: {
    display: "absolute",
    outline: "none"
  }
}));

const KeyboardShortcuts = (props) => {
  const {
    enable,
    onKeyDown,
    onKeyUp,
    onFocus,
    onBlur,
  } = props;

  const [ mouseDown, setMouseDown ] = useState(false);

  const onMouseDown = useCallback((e) => {
    setMouseDown(true);
  }, [ setMouseDown ]);
  const onMouseUp = useCallback((e) => {
    setMouseDown(false);
  }, [ setMouseDown ]);

  const focusRef = useRef(null);
  const classes = useStyles();

  useEffect(() => {
    if(enable) {
      const intervalId = setInterval(() => {
        const currentFocus = document.activeElement;
        if(!mouseDown) { // This allows the user to select text with the mouse
          if(currentFocus.tagName === 'A' ||
            currentFocus.tagName === 'BODY' ||
            currentFocus.tagName === 'BUTTON') {
            focusRef.current.focus();
          }
        }
      }, 100);

      document.addEventListener("mousedown", onMouseDown);
      document.addEventListener("mouseup", onMouseUp);

      return () => {
        clearInterval(intervalId);
        document.removeEventListener("mousedown", onMouseDown);
        document.removeEventListener("mouseup", onMouseUp);
      };
    }
  }, [ enable, mouseDown, onMouseDown, onMouseUp ]);

  return <div className={classes.focus} 
              tabIndex="0" // allows div to take focus
              onKeyDown={onKeyDown} 
              onKeyUp={onKeyUp} 
              ref={focusRef} 
              onFocus={onFocus} 
              onBlur={onBlur}></div>;
};
export default KeyboardShortcuts;
