import React, { useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Checkbox from '@material-ui/core/Checkbox';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ToolTip from '@material-ui/core/Tooltip'

import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp';
import FolderIcon from '@material-ui/icons/Folder';
import FolderOpenIcon from '@material-ui/icons/FolderOpen';
import HomeIcon from '@material-ui/icons/Home';
import KeyboardArrowRightIcon from '@material-ui/icons/KeyboardArrowRight';

import useTranslation from '../hooks/useTranslation';

const selectFromMap = (map, arr) => {
  var result = arr.reduce(function(o, name) { return o && o[name]; }, map);
  return result
}

const useStyles = makeStyles((theme) => ({
  fileButton: {
    width: "100%",
    justifyContent: "left"
  },
  homeRow: {
    "& button": {
      color: theme.palette.primary.main,
    },
    display: "flex",
    alignItems: "center",
    flexWrap: 'wrap'
  }
}));

const FileBrowser = (props) => {
  const {
    rootPath,
    filesMap,
    fileType,
    fileFilter,
    selectedFiles,
    onFileOpen,
    onSelectFile,
  } = props;
  const { t } = useTranslation();

  const [ path, setPath ] = useState([]);
  useEffect(() => { setPath([]) }, [filesMap])

  const handleGoUp = () => {
    path.length > 0 ? setPath(path.slice(0,-1)) : void(0)
  };
  const handleFileOpen = (fullPath) => {
    if(onFileOpen) {
      onFileOpen(fullPath,fileType);
    }
  };
  const handleSelectFile = (e) => {
    if(onSelectFile){
      onSelectFile(e)
    }
  };
  const handleHome = () => {
    setPath([])
  };

  const classes = useStyles();
  const currentLevel = selectFromMap(filesMap, path);
  const currentLevelDirs = currentLevel ? Object.keys(currentLevel).filter((key) => currentLevel[key] !== null) : null;
  const currentLevelFiles = currentLevel ? Object.keys(currentLevel).filter((key) => currentLevel[key] === null) : [];
  const filteredFileList = currentLevelFiles.filter(( file ) => file.toLowerCase().includes(fileFilter.toLowerCase()));

  return <>
    <Box>
      <div className={classes.homeRow}>
        <ToolTip title={t("tooltip_FileBrowserHome")}>
          <IconButton
            onClick={handleHome}
            color="primary"
          >
            <HomeIcon/>
          </IconButton>
        </ToolTip>
        <ToolTip title={t("tooltip_FileBrowserGoUp")}>
          <IconButton
            onClick={handleGoUp}
            color="primary"
          >
            <ArrowUpIcon/>
          </IconButton>
        </ToolTip>
        <FolderOpenIcon/>{path.join("/")}
      </div>
      <List>
      {
        currentLevelDirs && currentLevelDirs.map((dir) => {
          return <ListItem key={dir}>
              <Button
                onClick={() => {setPath(path.concat(dir))}}
              >
                <KeyboardArrowRightIcon/>
                <FolderIcon/>
                {dir}
              </Button>
          </ListItem>
        })
      }
      {
        filteredFileList && filteredFileList.map((file) => {
          const fileFullPath = [rootPath].concat(path).concat([file]).join("/");
          return <ListItem key={file}>
              <Checkbox value={fileFullPath} checked={selectedFiles[fileFullPath] ? true : false} onClick={handleSelectFile} />
              <Button
                classes={{ root: classes.fileButton }}
                onClick={() => {handleFileOpen(fileFullPath)}}
              >
                {file}
              </Button>
          </ListItem>
        })
      }
      </List>
    </Box>
  </>;
};
export default FileBrowser;
