import { connect } from 'react-redux';
import produce from 'immer';
import ToolTable from '../components/ToolTable';
import { toMachineUnits, toProgramUnits } from '../hooks/useUnits';
import actions from '../modules/rockhopper/actions';
import { selectToolFlutes, selectToolInSpindle, selectToolTable, selectToolTableDescriptions, selectReadyForMdi, selectUnitsCode } from '../modules/rockhopper/selectors';

export default connect(
  (state, ownProps) => {
    return {
      toolInSpindle: selectToolInSpindle(state),
      toolTable: selectToolTable(state),
      toolTableDescriptions: selectToolTableDescriptions(state),
      toolFlutes: selectToolFlutes(state),
      readyForMdi: selectReadyForMdi(state),
      unitsCode: selectUnitsCode(state),
    }
  },
  (dispatch, ownProps) => ({
    onDispatchMdiCmd: (cmd) => {
      dispatch(actions.put.mdiCmd({ data: [cmd] }));
    },
    onDispatchUpdateHelper: (updateHelper, data) => {
      dispatch( actions.updateHelpers[updateHelper](data) )
    },
    onLoadTool: (toolNumber) => {
      dispatch( actions.put.mdiCmd({ data: [ `M6 T${toolNumber}` ]}));
    },
    onMeasureTool: (toolNumber) => {
      dispatch( actions.put.mdiCmd({ data: [ `M654 T${toolNumber}` ]}));
    },
    onToolTableChanged: (toolTable, toolNumber, toolOffset, toolDiameter) => {
      dispatch( actions.updateHelpers.toolTableRow({ data: [toolNumber, toolOffset, toolDiameter] }));
      dispatch( actions.put.mdiCmd({ data: [ `G10 L1 P${toolNumber} Z${toolOffset} X0 R${0.5*toolDiameter} I0 J0 Q0` ],  }));
    },
    onToolDescriptionChanged: (toolNumber, description, descriptions) => {
      const newDescriptions = produce(descriptions, (draft) => {
        if(toolNumber <= descriptions.length){
          draft.splice(toolNumber - 1, 1, description)
        }
        else{
          let replacement = draft.concat(new Array(toolNumber - 1 - descriptions.length).fill(null));
          replacement.push(description);
          return replacement
        }
      });
      dispatch( actions.updateHelpers.toolTableDescriptions({ data: newDescriptions }));
      dispatch( actions.put.saveClientConfig({ data: [ 'ToolTableDescriptions', newDescriptions ], }));
    },
    onToolFlutesChanged: (newFlutes) => {
      dispatch( actions.updateHelpers.toolFlutes({ data: newFlutes }));
      dispatch( actions.put.saveClientConfig({ data: [ 'ToolFlutes', newFlutes ], }));
    },
  }),
  (stateProps, dispatchProps, ownProps) => {
    const {
      unitsCode,
    } = stateProps;

    const {
      onDispatchMdiCmd,
      onDispatchUpdateHelper,
    } = dispatchProps;

    const onSetToolLengthOffset = (toolNumber, tloDisplayUnits) => {
      const tloMachineUnits = toMachineUnits(tloDisplayUnits, unitsCode);
      onDispatchUpdateHelper('toolLengthOffset', { data: [toolNumber, tloMachineUnits.value] });
      const tloProgramUnits = toProgramUnits(tloDisplayUnits, unitsCode);
      const cmd = `G10 L1 P${toolNumber} Z${tloProgramUnits.value.toFixed(5)}`;
      onDispatchMdiCmd(cmd);
    };

    const onSetToolDiameter = (toolNumber, diameterDisplayUnits) => {
      const diameterMachineUnits = toMachineUnits(diameterDisplayUnits, unitsCode);
      onDispatchUpdateHelper('toolDiameter', { data: [toolNumber, diameterMachineUnits.value] });
      const diameterProgramUnits = toProgramUnits(diameterDisplayUnits, unitsCode);
      const cmd = `G10 L1 P${toolNumber} R${0.5*diameterProgramUnits.value.toFixed(5)}`;
      onDispatchMdiCmd(cmd);
    };

    return {
      onSetToolLengthOffset,
      onSetToolDiameter,
      ...stateProps,
      ...dispatchProps,
      ...ownProps
    }
  }
)(ToolTable);
