import React from 'react';
import Button from '@material-ui/core/Button';
import WarningIcon from '@material-ui/icons/Warning';
import useTranslation from '../hooks/useTranslation';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  warningIcon: {
    marginRight: theme.spacing(1)
  }
}));

function HomeAllButton(props) {
  const {
    isHomed,
    onHomeAll,
    classes
  } = props;

  const { t } = useTranslation();

  const internalClasses = useStyles();
  return <Button variant="contained" color="primary" size="small" classes={ { root: classes.button } } onClick={onHomeAll}>{ isHomed ? null : <WarningIcon className={internalClasses.warningIcon}/>}{ t("homeAll") }</Button>
}

export default HomeAllButton;
