import { connect } from 'react-redux';
import produce from 'immer';
import Mdi from '../components/Mdi';
import pentaActions from '../modules/pentaui/actions';
import { selectMdiSortKey, selectMdiText } from '../modules/pentaui/selectors';
import actions from '../modules/rockhopper/actions';
import { selectMdiHistory, selectMdiFavorites, selectMdiFavoritesMap, selectMdiHistoryInFavoritesMap, selectMdiHistorySorted, selectMdiHistorySortedUnique, selectReadyForMdi, selectKeyboardShortcutsJogModifiers, selectKeyboardShortcutsJog, selectKeyboardShortcutsOther, selectKeyboardShortcuts, selectKeyboardShortcutsMap, selectKeyboardShortcutsMdi } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => ({
  readyForMdi: selectReadyForMdi(state),
  mdiSortKey: selectMdiSortKey(state),
  mdiText: selectMdiText(state),
  mdiHistory: selectMdiHistory(state),
  mdiFavorites: selectMdiFavorites(state),
  mdiFavoritesMap: selectMdiFavoritesMap(state),
  mdiHistoryInFavoritesMap: selectMdiHistoryInFavoritesMap(state),
  mdiHistorySorted: selectMdiHistorySorted(state),
  mdiHistorySortedUnique: selectMdiHistorySortedUnique(state),

  jogModifiers: selectKeyboardShortcutsJogModifiers(state),
  jogKeys: selectKeyboardShortcutsJog(state),
  mdiShortcuts: selectKeyboardShortcutsMdi(state),
  otherShortcuts: selectKeyboardShortcutsOther(state),
  shortcuts: selectKeyboardShortcuts(state),
  shortcutsMap: selectKeyboardShortcutsMap(state),
}), 
(dispatch, ownProps) => ({ 
  onSetMdiText: (cmd) => {
    dispatch( pentaActions.setMdiText(cmd) );
  },
  onSendMdiCmd: (cmd, history) => {
    dispatch(actions.put.mdiCmd({ data: [ cmd ] }));
    const newHistoryItem = {cmd: cmd, time: Date.now()};
    const newHistory = history ? history.concat(newHistoryItem) : [newHistoryItem]
    dispatch( actions.put.saveClientConfig({ data: [ 'MDIHistory', newHistory ], }));
  },
  onAddMdiFavorite: (newFavCmd, currFavs) => {
    const newFav = {cmd: newFavCmd, name: "", keybind: "", time: Date.now()};
    const newFavs = currFavs ? currFavs.concat(newFav) : [newFav]
    dispatch( actions.put.saveClientConfig({ data: [ 'MDIFavorites', newFavs ], }));
  },
  onRemoveMdiFavorites: (cmdSet, currFavsMap) => {
    let newFavs = [];
    for(let [cmd, props] of currFavsMap){
     if( !cmdSet.has(cmd) ){
       newFavs.push({cmd: cmd, name: props.name, time: props.time})
     }
    }
    dispatch( actions.put.saveClientConfig({ data: [ 'MDIFavorites', newFavs ], }));
  },
  onModifyFavorite: (updatedFav, currFavsMap) => {
    let newFavs = [];
    for(let [cmd, props] of currFavsMap){
      if(cmd !== updatedFav.cmd){
        newFavs.push({cmd: cmd, name: props.name, time: props.time, keybind: props.keybind || ""})
      }
    }
    newFavs.push(updatedFav);
    dispatch( actions.put.saveClientConfig({ data: [ 'MDIFavorites', newFavs ], }));
  },
  onNameMdiFavorite: (cmd, newName, currFavs) => {
    const itemIndex = currFavs.findIndex(fav => fav.cmd === cmd );
    const newFavs = produce(currFavs, (draft) => { draft[itemIndex].name = newName } );
    dispatch( actions.put.saveClientConfig({ data: [ 'MDIFavorites', newFavs ], }));
  },
  onSubmitShortcuts: (shortcuts) => {
    dispatch( actions.put.saveClientConfig( { data: [ 'KeyboardShortcuts', shortcuts ] } ) )
  },
}))(Mdi);
