import { connect } from 'react-redux';
import produce from 'immer';
import MachineConfig from '../components/MachineConfig';
import rockhopperActions from '../modules/rockhopper/actions';
import pentaActions from '../modules/pentaui/actions';
import fileQueue from '../modules/rockhopper/file-queue'
import { selectConfigMap, selectConfigOverlayMap, selectCompensation } from '../modules/rockhopper/selectors';
import { selectIsEditingOverlay, selectClientConfigOverlay, selectIsEditingCompensation, selectClientCompensation } from '../modules/pentaui/selectors';
import { FILE_TYPES } from '../modules/rockhopper/constants';
import downloadQueue from '../modules/rockhopper/download-queue';
import { downloadToDisk } from '../modules/rockhopper/file-cache';

export default connect(
  (state, ownProps) => {
    return { 
      ini: selectConfigMap(state),
      isEditingOverlay: selectIsEditingOverlay(state),
      diskOverlay: selectConfigOverlayMap(state),
      clientOverlay: selectClientConfigOverlay(state),
      isEditingCompensation: selectIsEditingCompensation(state),
      diskCompensation: selectCompensation(state),
      clientCompensation: selectClientCompensation(state),
    }
  }, 
  (dispatch, ownProps) => ({ 
    onAddOverlaySection: (overlay, newSection) => {
      if(overlay.hasOwnProperty(newSection)){
        return false;
      }
      const newOverlay = produce(overlay, (draft) => {
        draft[newSection] = {}
      })
      dispatch(pentaActions.overlay.set(newOverlay));
    },
    onChangeOverlay: (overlay) => {
      dispatch(pentaActions.overlay.set(overlay))
    },
    onDeleteOverlaySection: (sectionName) => {
      dispatch(pentaActions.overlay.deleteSection(sectionName));
    },
    onEnableEditingOverlay: (currentDiskOverlay) => {
      dispatch(pentaActions.overlay.enableEditing(currentDiskOverlay));
    },
    onDisableEditingOverlay: () => {
      dispatch(pentaActions.overlay.disableEditing());
    },
    onSaveOverlay: (overlay) => {
      let sections = {};
      let parameters = [];
      Object.keys(overlay).forEach((sectionKey) => {
        sections[sectionKey] = {comment: "", help: ""};
        Object.values(overlay[sectionKey]).forEach((param, idx) => {
          const {key, ...diskValues} = param;
          parameters.push({id: idx, values: diskValues})
        });
      });
      dispatch(pentaActions.overlay.disableEditing());
      dispatch(rockhopperActions.updateHelpers.configOverlay({ data: {sections: sections, parameters: parameters} }) );
      dispatch(rockhopperActions.put.config({ data: {data: {sections: sections, parameters: parameters}} }));
    },
    onEnableEditingCompensation: (currentDiskCompensation) => {
      dispatch(pentaActions.compensation.enableEditing(currentDiskCompensation))
    },
    onDisableEditingCompensation: () => {
      dispatch(pentaActions.compensation.disableEditing())
    },
    onChangeCompensation: (compensation) => {
      dispatch(pentaActions.compensation.set(compensation))
    },
    onSaveCompensation: (compensation) => {
      dispatch(pentaActions.compensation.disableEditing())
      dispatch(rockhopperActions.updateHelpers.compensation({ data: compensation }) );
      dispatch(rockhopperActions.put.setCompensation({ data: { data: compensation } }));
    },
    onUploadFiles: (path, overlay) => {
      fileQueue.addFile(path, FILE_TYPES.CONFIGURATION, overlay);
    },
    onDownloadFiles: (downloadTag) => {
      const now = new Date();
      const dateStr = now.toISOString().split("T")[0];
      downloadQueue.addDownload("calibration" + downloadTag + "_" + dateStr + '.zip', FILE_TYPES.CONFIGURATION, downloadToDisk)
    },
    onRestartServices: () => {
      dispatch(rockhopperActions.put.restart());
    }
  })
)(MachineConfig);
