import actions, { createRockhopperActionType, createFriendlyName } from './actions';
import { handleActions } from 'redux-actions';
import { 
  LOCALSTORAGE_KEY,
  FILE_UPLOAD_QUEUED,
  FILE_UPLOAD_UPLOADING,
  FILE_UPLOAD_ASK_FOR_OVERWRITE,
  FILE_UPLOAD_CANCELED,
  TOOL_ENTRY_INDICES,
} from './constants';
import produce from 'immer';

const createStatusReducers = () => {
  const actionCreators = Object.values(actions.get);
  const exampleActions = actionCreators.map((actionCreator) => actionCreator({}));

  const reducers = exampleActions.reduce((reducerMap, exampleAction) => {
    const {
      name
    } = exampleAction.payload;
    const typePath = exampleAction.type.split('/');
    const typeSuffix = typePath[typePath.length-1];
    const type = createRockhopperActionType("UPDATE", typeSuffix);
    const friendlyName = createFriendlyName(name);
    reducerMap[type] = produce((state, action) => {
      state.status[friendlyName] = action.payload.data;
    });
    return reducerMap;
  }, {});

  return reducers;
};
const createDelayedStatusReducers = () => {
  const actionCreators = Object.values(actions.get);
  const exampleActions = actionCreators.map((actionCreator) => actionCreator({}));

  const reducers = exampleActions.reduce((reducerMap, exampleAction) => {
    const {
      name
    } = exampleAction.payload;
    const typePath = exampleAction.type.split('/');
    const typeSuffix = typePath[typePath.length-1];
    const type = createRockhopperActionType("DELAYED", "UPDATE_IMMEDIATE", typeSuffix);
    const friendlyName = createFriendlyName(name);
    reducerMap[type] = produce((state, action) => {
      state.delayed.status[friendlyName] = action.payload.data;
    });

    const type2 = createRockhopperActionType("DELAYED", "UPDATE", typeSuffix);
    reducerMap[type2] = produce((state, action) => {
      state.delayed.status[friendlyName] = action.payload.data;
    });

    const type3 = createRockhopperActionType("DELAYED", "REGISTER_UPDATE", typeSuffix);
    reducerMap[type3] = produce((state, action) => {
      state.delayed.registered[friendlyName] = action.payload;
    });
    return reducerMap;
  }, {});

  return reducers;
};

const localStorageData = JSON.parse(localStorage.getItem(LOCALSTORAGE_KEY) || "{}");
export const defaultState = {
  // use server in localstorage if available, otherwise use the current hostname, unless it's a localhost address, then use 192.168.6.2
  webSocket: {
    server: localStorageData.server || (document.location.hostname.indexOf("localhost") === -1 ? document.location.hostname : "192.168.6.2"),
    port: localStorageData.port || "8000",
    user: localStorageData.user || "default",
    password: localStorageData.password || "default",
    encrypted: typeof(localStorageData.encrypted) === "undefined" ? false : (localStorageData.encrypted)
  },
  isLoggedIn: false,
  actionEmitters: {},
  features: {},
  status: {},
  delayed: {
    registered: {},
    status: {}
  },
  fileDownload: {},
  fileUpload: {},
  fileCache: { 
    paths: {},
    progress: {}
  }
};

const reducerCreator = (opts={}) => {
  const {
    includeStatusReducers = true,
    includeDelayedStatusReducers = true
  } = opts;
  const statusReducers = includeStatusReducers ? createStatusReducers() : {};
  const delayedStatusReducers = includeDelayedStatusReducers ? createDelayedStatusReducers() : {};
  return handleActions({
    [actions.fileCache.initProgress]: produce((state,action) => {
      const {
        checksum,
        size
      } = action.payload;
      state.fileCache.progress[checksum] = { size, bytesLoaded: 0 };
    }),
    [actions.fileCache.updateProgress]: produce((state,action) => {
      const {
        checksum,
        bytesLoaded
      } = action.payload;
      state.fileCache.progress[checksum].bytesLoaded = bytesLoaded;
    }),
    [actions.fileCache.setPathChecksum]: produce((state,action) => {
      const {
        path,
        checksum
      } = action.payload;
      state.fileCache.paths[path] = checksum;
    }),
    [actions.fileUpload.removeAll]: produce((state,action) => {
      state.fileUpload = {};
    }),
    [actions.fileUpload.remove]: produce((state,action) => {
      const path = action.payload;
      delete state.fileUpload[path];
    }),
    [actions.fileUpload.cancel]: produce((state,action) => {
      const path = action.payload;
      if(state.fileUpload[path]) {
        state.fileUpload[path].state = FILE_UPLOAD_CANCELED;
      }
    }),
    [actions.fileUpload.cancelAll]: produce((state,action) => {
      for(let path in state.fileUpload) {
        state.fileUpload[path].state = FILE_UPLOAD_CANCELED;
      }
    }),
    [actions.fileUpload.progress]: produce((state,action) => {
      const {
        path, progress
      } = action.payload;
      if(state.fileUpload[path]) {
        state.fileUpload[path].progress = progress;
        state.fileUpload[path].state = FILE_UPLOAD_UPLOADING;
      }
    }),
    [actions.fileUpload.finalize]: produce((state,action) => {
      const {
        path
      } = action.payload;
      delete state.fileUpload[path];
    }),
    [actions.fileUpload.askForOverwrite]: produce((state,action) => {
      const {
        path
      } = action.payload;

      state.fileUpload[path].state = FILE_UPLOAD_ASK_FOR_OVERWRITE;
    }),
    [actions.fileUpload.confirmOverwrite]: produce((state,action) => {
      const {
        path
      } = action.payload;

      state.fileUpload[path].overwrite = true;
    }),
    [actions.fileUpload.confirmOverwriteAll]: produce((state,action) => {
      for(let key in state.fileUpload) {
        state.fileUpload[key].overwrite = true;
      }
    }),
    [actions.fileUpload.queued]: produce((state,action) => {
      const {
        path,
        overwrite
      } = action.payload;

      state.fileUpload[path] = {  path, overwrite, progress: 0, state: FILE_UPLOAD_QUEUED };
    }),
    [actions.fileDownload.removeAll]: produce((state,action) => {
      state.fileDownload = {};
    }),
    [actions.fileDownload.remove]: produce((state,action) => {
      const path = action.payload;
      delete state.fileDownload[path];
    }),
    [actions.fileDownload.cancel]: produce((state,action) => {
      const path = action.payload;
      if(state.fileDownload[path]) {
        state.fileDownload[path].state = FILE_UPLOAD_CANCELED;
      }
    }),
    [actions.fileDownload.cancelAll]: produce((state,action) => {
      for(let path in state.fileDownload) {
        state.fileDownload[path].state = FILE_UPLOAD_CANCELED;
      }
    }),
    [actions.fileDownload.progress]: produce((state,action) => {
      const {
        path, progress
      } = action.payload;
      if(state.fileDownload[path]) {
        state.fileDownload[path].progress = progress;
        state.fileDownload[path].state = FILE_UPLOAD_UPLOADING;
      }
    }),
    [actions.fileDownload.queued]: produce((state,action) => {
      const {
        path,
        overwrite
      } = action.payload;

      state.fileDownload[path] = {  path, overwrite, progress: 0, state: FILE_UPLOAD_QUEUED };
    }),
    [actions.setLoggedIn]: produce((state,action) => { 
      const isLoggedIn = action.payload;
      state.isLoggedIn = isLoggedIn;

      if(!isLoggedIn) {
        state.fileUpload = {}
      }
    }),
    [actions.setConnectionDetails]: produce((state,action) => {
      const { 
        server,
        port,
        user, 
        password,
        encrypted 
      } = action.payload;
      state.webSocket.user = user;
      state.webSocket.password = password;
      state.webSocket.server = server;
      state.webSocket.port = port;
      state.webSocket.encrypted = encrypted;
    }),
    [actions.addActionEmitter]: produce((state,action) => {
      const {
        id
      } = action.payload;
      if(!state.actionEmitters[id]) {
        state.actionEmitters[id] = [];
      }
      state.actionEmitters[id].push(action.payload);
    }),
    [actions.removeActionEmitter]: produce((state,action) => {
      const {
        id,
        type
      } = action.payload;
      if(state.actionEmitters[id]) {
        const index = state.actionEmitters[id].findIndex((curActionEmitter) => curActionEmitter.id === id &&
                                                                               curActionEmitter.type === type);
        state.actionEmitters[id].splice(index, 1);
      }
    }),
    [actions.response.getHelpers.programPrefix]: produce((state, action) => {
      state.status.programPrefix = action.payload.data.parameters[0].values.value;
    }),
    [actions.response.getHelpers.features]: produce((state, action) => {
      const features = {};

      if(action.payload.data.parameters) {
        for(let feature of action.payload.data.parameters) {
          features[feature.values.name] = true;
        }
      }
      state.features = features;
    }),
    [actions.updateHelpers.setDisplayUnits]: produce((state,action) => {
      if(!state.status.clientConfig) {
        state.status.clientConfig = {};
      }
      state.status.clientConfig.displayUnits = action.payload;
    }),
    [actions.updateHelpers.toolFlutes]: produce((state, action) => {
      state.status.clientConfig.ToolFlutes = action.payload.data;
    }),
    [actions.updateHelpers.toolTableDescriptions]: produce((state, action) => {
      state.status.clientConfig.ToolTableDescriptions = action.payload.data;
    }),
    [actions.updateHelpers.toolDiameter]: produce((state, action) => {
      const [toolIndex, toolDiameter] = [...action.payload.data];
      state.status.toolTable[toolIndex][TOOL_ENTRY_INDICES.DIAMETER] = toolDiameter;
    }),
    [actions.updateHelpers.toolLengthOffset]: produce((state, action) => {
      const [toolIndex, toolLength] = [...action.payload.data];
      state.status.toolTable[toolIndex][TOOL_ENTRY_INDICES.Z_OFFSET] = toolLength;
    }),
    [actions.updateHelpers.toolTableRow]: produce((state, action) => {
      const [toolIndex] = [...action.payload.data];
      state.status.toolTable[toolIndex] = [null, ...action.payload.data];
    }),
    [actions.updateHelpers.toolTable]: produce((state, action) => {
      state.status.toolTable = [null, ...action.payload.data];
    }),
    [actions.updateHelpers.configOverlay]: produce((state, action) => {
      state.status.configOverlay = action.payload.data;
    }),
    [actions.updateHelpers.compensation]: produce((state, action) => {
      state.status.compensation = action.payload.data;
    }),
    ...statusReducers,
    ...delayedStatusReducers
  }, defaultState);
}

export default reducerCreator;
