const moveComponents = [ "M", 0, 0 ];
export const moveTo = (x,y) => {
  moveComponents[1] = x;
  moveComponents[2] = y;
  return moveComponents.join(" ");
};

const arcComponents = [ "A", 0, 0, 0, 0, 0, 0, 0 ];
export const arcTo = (rx, ry, rot, largeArc, sweep, x, y) => {
  arcComponents[1] = rx;
  arcComponents[2] = ry;
  arcComponents[3] = rot;
  arcComponents[4] = largeArc;
  arcComponents[5] = sweep;
  arcComponents[6] = x;
  arcComponents[7] = y;
  return arcComponents.join(" ");
};

const lineComponents = [ "L", 0, 0 ];
export const lineTo = (x,y) => {
  lineComponents[1] = x;
  lineComponents[2] = y;
  return lineComponents.join(" ");
};
