import React, { useState, useEffect } from 'react';
import Tooltip from '@material-ui/core/Tooltip';

// timestamp is in milliseconds (what is returned from Date.now())
const humanReadableTime = (timestamp) => {
  const now = Date.now();

  const dt = (now-timestamp)/1000; // dt is in seeconds
  const nowDate = new Date(now);
  const thenDate = new Date(timestamp);


  if(dt > 0) {
    if(dt < 10) {
      return "Just now";
    } else if(dt < 60) {
      return "Less than a minute ago";
    } else if(dt < 3600) {
      const minutes = Math.round(dt/60);
      const s = minutes > 1 ? "s" : "";
      return `${minutes} minute${s} ago`;
    } else {
      const days = Math.round(dt/86400);
      if(days < 6) { // if less than 7 days ago
        const nowDay = nowDate.getDay();
        const thenDay = thenDate.getDay();
        const daysAgo = (nowDay-thenDay+7)%7; // number of days ago up to 6 days, but takes into the actual day of the week so 10 hours ago, may be 1 day ago (yesterday) if its 8am

        if(daysAgo === 0) {
          const hours = Math.round(dt/3600);
          const s = hours > 1 ? "s" : "";
          return `${hours} hour${s} ago`;
        } else if(daysAgo === 1) {
          return "Yesterday";
        } else {
          return thenDate.toLocaleDateString("en-US", { weekday: 'long' });
        }
      } else {
        const nowYear = nowDate.getFullYear();
        const thenYear = thenDate.getFullYear();
        const yearsAgo = nowYear-thenYear; // number of years ago, will be 1 if its Jan 1, 2020 now, but then was Dec 31, 2019
        if(yearsAgo === 0) { // if its the same year, then don't put the year
          return thenDate.toLocaleDateString("en-US", { month: 'long', day: 'numeric' });
        } else {
          // otherwise, put month day and year
          return thenDate.toLocaleDateString("en-US", { month: 'long', day: 'numeric', year: 'numeric' });
        }
      }
    }
  } else {
    // in the future
    return thenDate.toLocaleDateString("en-US", { month: 'long', day: 'numeric', year: 'numeric' });
  }
};

const Timestamp = (props) => {
  const {
    timestamp,
    className
  } = props;

  const [ dateString, setDateString ] = useState(humanReadableTime(timestamp));
  useEffect(() => {
    const interval = setInterval(() => {
      setDateString(humanReadableTime(timestamp));
    }, 1000);

    return () => clearInterval(interval);
  });

  return <Tooltip placement="top" title={(new Date(timestamp)).toLocaleString()}><span className={className}>{dateString}</span></Tooltip>
};
export default Timestamp;
