import React from 'react';
import Box from '@material-ui/core/Box';


const TabPanel = (props) => {
  const { 
    children, 
    value, 
    index, 
    p, 
    ...other 
  } = props;  

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={p}>
          <span>{children}</span>
        </Box>
      )}
    </div>
  );
}

export default TabPanel