import { connect } from 'react-redux';
import rockhopperActions from '../modules/rockhopper/actions';
import pentaActions from '../modules/pentaui/actions';
import DRO from '../components/DRO';
import { selectIsMachineHomed, selectPositions, selectG5xIndex, selectWorkOffsets } from '../modules/rockhopper/selectors';
import { selectDroDisplay } from '../modules/pentaui/selectors';


export default connect( 
(state, ownProps) => {
  return { 
    positions: selectPositions(state),
    isHomed: selectIsMachineHomed(state),
    g5xIndex: selectG5xIndex(state),
    workOffsets: selectWorkOffsets(state),
    display: selectDroDisplay(state),
  }
}, 
(dispatch, ownProps) => ({ 
  onSetDisplay: (val) => dispatch(pentaActions.dro.setDisplay(val)),
  onSetCoordSys: (val) => dispatch(pentaActions.dro.setCoordSys(val)),
  onHomeAll: () => dispatch(rockhopperActions.put.homeCmd()),
}))(DRO);
