import localforage from 'localforage';

// The FileCache is both an in-memory and persistent cache for files on a penta machine.
// The in-memory cache is a mutable data store that will be populated as a file is downloaded from Rockhopper. 
// Once a download is complete, it will be persisted to IndexedDB, where it can later be accessed without
// needing to do a full download from Rockhopper.

// Sagas will perform the download and update the in-memory cache as data is received and update the Redux
// store with the proper information about the loading progress. This keeps the file data out of the Redux
// store while allowing sagas/containers access to the necessary information to retrieve file data during a 
// download or after it is completed. Doing it this way allows many small files to be accessed very quickly
// from the cache, and very large files to be loaded and viewed during a long download, both using the same
// interface.

// TODO - The FileCache supports encrypting the data before storing it to IndexedDB, but needs a 64 character hex string
// that represents both a 16 bit HMAC key and a 16 bit AES key. Each user in Rockhopper should have a random 32-bit key
// key associated with them that is sent over after successful login. First, we need to set up the UI to be served over
// https.
export class FileCache {
  constructor() {
    this.maxPersistentCacheSize = 1024*1024*1024; // 1GB
    this.maxMemoryCacheSize = 1024*1024*1024; // 1GB
    this.memoryStore = {};
    this.memoryMetadata = {};
    this.store = localforage.createInstance({
      name: "pentaCache",
      driver: localforage.INDEXEDDB,
      storeName: "pentaStore",
      description: "Stores files locally to avoid unnecessary requests to Rockhopper."
    });
    this.metadata = localforage.createInstance({
      name: "pentaCacheMetadata",
      driver: localforage.INDEXEDDB,
      storeName: "pentaStoreMetadata",
      description: "Stores meta data about files stored in the cache."
    });
  }

  // setKey is a 64 character hex string representing 256 bits of random data (128 bits for the hmac key and 128 bits for the aes key)
  // Each machine (and potentially each user on a machine eventually) should be assigned one and it can be reset to new random bits at any time, in which case all cached items will be invalidated on retrieval.
  async setKey(keyString) {
    if(window.crypto.subtle && keyString && keyString.length === 64) {
      const hmacKeyData = new Uint8Array(16);
      const aesKeyData = new Uint8Array(16);
      for(let i = 0; i < 16; i++) {
        hmacKeyData[i] = parseInt(keyString.slice(i,i+2), 16);
      }
      for(let i = 0; i < 16; i++) {
        aesKeyData[i] = parseInt(keyString.slice(i+16,i+18), 16);
      }

      const hmacKeyPromise = window.crypto.subtle.importKey("raw", hmacKeyData, { name: "HMAC", hash: "SHA-512" }, false, ["sign", "verify"]).then((key) => {
        this.hmacKey = key;
      });
      const aesKeyPromise = window.crypto.subtle.importKey("raw", aesKeyData, "AES-GCM", false, ["encrypt", "decrypt"]).then((key) => {
        this.aesKey = key;
      });

      await Promise.all([ hmacKeyPromise, aesKeyPromise ]);
    }
  }

  async clearKey() {
    this.hmacKey = null;
    this.aesKey = null;
  }

  getTotalMemorySize() {
    let totalSize = 0;
    for(let key in this.memoryMetadata) {
      totalSize += this.memoryMetadata[key].size;
    }

    return totalSize;
  }

  getMemoryItem(checksum) {
    const metadata = this.memoryMetadata[checksum];
    if(metadata) {
      metadata.timestamp = Date.now();
      return this.memoryStore[checksum];
    }
  }

  setMemoryItem(checksum, data) {
    const size = data.byteLength;
    const totalMemorySize = this.getTotalMemorySize()+size;

    this.memoryStore[checksum] = data;

    this.memoryMetadata[checksum] = {
      timestamp: Date.now(),
      size
    };

    if(totalMemorySize > this.maxMemoryCacheSize) {
      this.removeOldestMemoryData(totalMemorySize-this.maxMemoryCacheSize);
    }
  }

  removeOldestMemoryData(reduceByAmount) {
    const allFiles = [];
    for(let key in this.memoryMetadata) {
      allFiles.push([ this.memoryMetadata[key], key ]);
    }

    allFiles.sort((a,b) => { return a[0].timestamp-b[0].timestamp });

    let i = 0;
    let totalRemoved = 0;
    while(totalRemoved < reduceByAmount && i < allFiles.length) {
      const [ value, key ] = allFiles[i];
      totalRemoved += value.size;
      delete this.memoryMetadata[key];
      delete this.memoryStore[key];
      i++;
    }
  }

  async getTotalPersistentSize() {
    let totalSize = 0;
    await this.metadata.iterate((value, key, iterationNumber) => {
      totalSize += value.size;
    }).catch((err) => { console.error(err) });

    return totalSize;
  }

  async getItem(checksum) {
    const data = this.getMemoryItem(checksum);
    if(!data) {
      return Promise.resolve(this.getPersistentItem(checksum)).then((data) => {
        if(data) {
          this.setMemoryItem(checksum, data);
        }
        return data;
      });
    }
    return data;
  }

  async setPersistentItem(checksum, data) {
    const size = data.byteLength;
    const totalPersistentSize = await this.getTotalPersistentSize()+size;
    if(window.crypto.subtle && this.aesKey && this.hmacKey) {
      const iv = window.crypto.getRandomValues(new Uint8Array(16));
      const encryptedData = await window.crypto.subtle.encrypt({
        name: "AES-GCM",
        iv
      }, this.aesKey, data);

      const signature = await window.crypto.subtle.sign("HMAC", this.hmacKey, encryptedData);

      this.store.setItem(checksum, encryptedData);

      this.metadata.setItem(checksum, {
        timestamp: Date.now(),
        iv,
        signature,
        size
      });
    } else {
      this.store.setItem(checksum, data);

      this.metadata.setItem(checksum, {
        timestamp: Date.now(),
        size
      });
    }

    if(totalPersistentSize > this.maxPersistentCacheSize) {
      this.removeOldestPersistentData(totalPersistentSize-this.maxPersistentCacheSize);
    }
  }

  async removeOldestPersistentData(reduceByAmount) {
    const allFiles = [];
    await this.metadata.iterate((value, key, iterationNumber) => {
      allFiles.push([ value, key ]);
    }).catch((err) => { console.error(err) });

    allFiles.sort((a,b) => { return a[0].timestamp-b[0].timestamp });

    let i = 0;
    let totalRemoved = 0;
    while(totalRemoved < reduceByAmount && i < allFiles.length) {
      const [ value, key ] = allFiles[i];
      totalRemoved += value.size;
      this.metadata.removeItem(key);
      this.store.removeItem(key);
      i++;
    }
  }

  async getPersistentItem(checksum) {
    if(checksum) {
      const metadata = await this.metadata.getItem(checksum).then((value) => value).catch(() => null);
      if(metadata) {
        const {
          signature,
          iv,
          size
        } = metadata;
        this.metadata.setItem(checksum, {
          timestamp: Date.now(),
          iv,
          signature,
          size
        });

        if(window.crypto.subtle && this.aesKey && this.hmacKey && signature && iv) {
          return await this.store.getItem(checksum).then(async (value) => {
            const verified = await window.crypto.subtle.verify("HMAC", this.hmacKey, signature, value);
            if(verified) {
              return window.crypto.subtle.decrypt({
                name: "AES-GCM",
                iv
              },
              this.aesKey,
              value);
            }
            return null;
          }).then((data) => {
            return data;
          }).catch(() => null);
        } else {
          if(!signature && !iv) {
            return await this.store.getItem(checksum).then((data) => {
              return data;
            }).catch(() => null);
          }
        }
      }
    }
    return null;
  }
  
  async clear() {
    this.memoryStore = {};
    this.memoryMetadata = {};
    await this.store.dropInstance().then(() => console.log("dropped store"));
    await this.metadata.dropInstance().then(() => console.log("dropped metadata"));
  }
};

const singletonFileCache = new FileCache();

export default singletonFileCache;

export function downloadToDisk(fileCache, checksum, path) {
  fileCache.getItem(checksum).then((b) => {
    const element = document.createElement('a');
    const blob = new Blob([ b ], {type: "text/plain;charset=utf-8"});
    element.setAttribute('href', URL.createObjectURL(blob));
    element.setAttribute('download', path);
    element.click();
  });
  return null;
}