import { connect } from 'react-redux';
import GCodeFileBrowserDialog from '../components/GCodeFileBrowserDialog';
import actions from '../modules/rockhopper/actions';

import { selectGcodeMap, selectProgramPrefix } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => ({
  gcodeMap: selectGcodeMap(state),
  programPrefix: selectProgramPrefix(state),
}), 
(dispatch, ownProps) => ({
  // onFileCopy: (path, fileType) => {
  //   //Currently only used to copy from USB to ncfiles
  //   dispatch(actions.put.fileCopy({ data: [ path, fileType, "~/ncfiles/"  ] }))
  // },
  onFileDelete: (path, fileType) => {dispatch(actions.put.fileDelete({ data: [ path, fileType  ] })) },
  onFileOpen: (path, fileType) => {dispatch(actions.put.programOpenCmd({ data: [ path, fileType ] }))},
}))(GCodeFileBrowserDialog); 
