import React from 'react';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  button: {
    height: 42,
  },
}));

const NAME_DISPLAY_LENGTH = 30;

const MdiQuickAccessButton = (props) => {
  const { 
    name,
    cmd,
    onClick
  } = props;
  
  const classes = useStyles();
  
  const isNameCutoff = name && (name.length > NAME_DISPLAY_LENGTH);

  const displayName = isNameCutoff ? (name.substring(0,30) + "...") : name;
  
  const tooltip = isNameCutoff ? (
    <React.Fragment>
      <Typography color="inherit">{name}</Typography>
      {cmd}
    </React.Fragment>
  ) : (
    <React.Fragment>
      {cmd}
    </React.Fragment>
  )


  return (
    <Tooltip title={tooltip}>
      <span>
      <Button
       {...props}
       className={classes.button}
       onClick={() => onClick(cmd)}
      >
        {displayName}
      </Button>
      </span>
    </Tooltip>
  )
};
export default MdiQuickAccessButton;
