import { useContext } from 'react';
import { UnitsContext } from '../components/UnitsProvider';
import { UNITS } from '../modules/rockhopper/constants';
import convert from 'convert-units';

/*
  toProgramUnits and toMachineUnits are exported as standalone functions,
  outside the useUnits context, to allow use in containers.
  For the units affected by it, this requires passing the G20/G21 modal state
  through the component and back to the container's dispatch methods.
*/
export const toProgramUnits = (unitObject, unitSystemModalCode) => {
  const {unit, value} = unitObject;
  const unitType = UNITS.TYPE_MAP[unit];
  if(unitType === 'length'){
    if(unitSystemModalCode === 'G21'){
      return {
        value: convert(value).from(unit).to('mm'),
        unit: 'mm'
      }
    }
    else if(unitSystemModalCode === 'G20'){
      return {
        value: convert(value).from(unit).to('in'),
        unit: 'in'
      }
    }  
  }
  else if(unitType === 'linearVelocity'){
    if(unitSystemModalCode === 'G21'){
      return {
        value: convert(value).from(unit).to('mm/min'),
        unit: 'mm/min'
      }
    }
    else if(unitSystemModalCode === 'G20'){
      return {
        value: convert(value).from(unit).to('in/min'),
        unit: 'in/min'
      }
    }
  }
  else{
    const constantsKey = unitType.split(/(?=[A-Z])/).map(keyPiece => keyPiece.toUpperCase()).join('_');
    const machineUnit = UNITS.MACHINE_UNITS[constantsKey];
    return { 
      value: convert(value).from(unit).to(machineUnit),
      unit: machineUnit
    }
  }
}

export const toMachineUnits = (unitObject, unitSystemModalCode) => {
  let {unit, value} = unitObject;
  if(unit.startsWith('pu')){
    if(unitSystemModalCode === 'G21'){
      unit = 'mm' + unit.substr(2);
    }
    else if(unitSystemModalCode === 'G20'){
      unit = 'in' + unit.substr(2);
    }
  }
  const unitType = UNITS.TYPE_MAP[unit];
  const constantsKey = unitType.split(/(?=[A-Z])/).map(keyPiece => keyPiece.toUpperCase()).join('_');
  const machineUnit = UNITS.MACHINE_UNITS[constantsKey];
  return {
    value: convert(value).from(unit).to(machineUnit),
    unit: machineUnit
  }
}

const useUnits = () => {
  const {displayUnits,activeCodes} = useContext(UnitsContext);

  const getDisplayUnitStringFromType = (unitType) => {
    let displayUnit = displayUnits[unitType];
    if(displayUnit.startsWith('pu')){
      if(activeCodes.includes('G21')){
        displayUnit = 'mm' + displayUnit.substr(2);
      }
      else if(activeCodes.includes('G20')){
        displayUnit = 'in' + displayUnit.substr(2);
      }
    }
    return displayUnit;
  }

  const getDisplayUnitStringFromUnit = (unit) => {
    if(unit.startsWith('deg')){
      return 'deg' + unit.substr(3);
    }
    else return (unit);
  }

  const toDisplayUnits = (physicalQuantity, unitType=null) => {
    const {unit, value} = physicalQuantity;
    let displayUnit;
    if(unitType){
      displayUnit = displayUnits[unitType];
    }
    else{
      displayUnit = displayUnits[UNITS.TYPE_MAP[unit]];
    }
    if(displayUnit.startsWith('pu')){
      if(activeCodes.includes('G21')){
        displayUnit = 'mm' + displayUnit.substr(2);
      }
      else if(activeCodes.includes('G20')){
        displayUnit = 'in' + displayUnit.substr(2);
      } else {
        // haven't connected yet so we don't know, use inches for now
        displayUnit = 'in' + displayUnit.substr(2);
      }
    }
    return { 
      value: convert(value).from(unit).to(displayUnit),
      unit: displayUnit
    }
  }

  return { displayUnits, getDisplayUnitStringFromType, getDisplayUnitStringFromUnit, toDisplayUnits }
}
export default useUnits;
