import React from 'react';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  svg: {
    overflow: "visible"
  }
}));

const Gauges = (props) => {
  const {
    width,
    height,
    viewBox = "-5 -5 110 110",
    preserveAspectRatio = "xMaxYMax",
    children
  } = props;

  const classes = useStyles();

  return <svg width={width} height={height} viewBox={viewBox} preserveAspectRatio={preserveAspectRatio} className={classes.svg}>
  {children}
  </svg>;
};
export default Gauges;
