import React, { useRef, useCallback, useState } from 'react';
import Panel from './Panel';

import FormGroup from '@material-ui/core/FormGroup';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import TextField from '@material-ui/core/TextField';
import Switch from '@material-ui/core/Switch';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  buttonContainer: {
    textAlign: "right"
  },
  button: {
    marginLeft: theme.spacing(2),
  },
  gapUnder: {
    marginBottom: theme.spacing(2),
  },
  input: {
    paddingTop: theme.spacing(1),
    paddingBottom: theme.spacing(1)
  }
}));

const ConnectionDetails = (props) => {
  const {
    server,
    port,
    user,
    password,
    encrypted,
    onUpdate
  } = props;
  const classes = useStyles();
  const { t } = useTranslation();

  const serverRef = useRef(null);
  const portRef = useRef(null);
  const userRef = useRef(null);
  const passwordRef = useRef(null);

  const [ useEncryption, setUseEncryption ] = useState(encrypted);

  const handleReset = useCallback(() => {
    serverRef.current.value = server;
    portRef.current.value = port;
    userRef.current.value = user;
    passwordRef.current.value = password;
    setUseEncryption(encrypted);
  }, [ server, port, user, password, encrypted, serverRef, portRef, userRef, passwordRef, setUseEncryption ]);

  const handleUpdate = useCallback(() => {
    if(onUpdate) {
      onUpdate(serverRef.current.value,
               portRef.current.value,
               userRef.current.value,
               passwordRef.current.value,
               useEncryption);
    }
  }, [ onUpdate, serverRef, portRef, userRef, passwordRef, useEncryption ]);

  const handleSSLChange = useCallback((e, newValue) => {
    setUseEncryption(newValue);
  }, [ setUseEncryption ]);

  return <Panel label={t("websocket")}>
    <form>
      <TextField autoComplete={"rockhopper server"} classes={ { root: classes.gapUnder} } InputProps={ { classes: { input: classes.input } } } label={t("server")} defaultValue={server} variant="outlined" fullWidth inputRef={serverRef}/><br/>
      <TextField autoComplete={"rockhopper port"} classes={ { root: classes.gapUnder } } InputProps={ { classes: { input: classes.input } } } label={t("port")} defaultValue={port} variant="outlined" fullWidth inputRef={portRef}/><br/>
      <TextField autoComplete={"rockhopper user"} classes={ { root: classes.gapUnder } } InputProps={ { classes: { input: classes.input } } } label={t("username")} defaultValue={user} variant="outlined" fullWidth inputRef={userRef}/><br/>
      <TextField autoComplete={"rockhopper password"} InputProps={ { classes: { input: classes.input } } } type="password" label={t("password")} defaultValue={password} variant="outlined" fullWidth inputRef={passwordRef}/>
      <FormGroup row>
        <FormControlLabel
          control={<Switch checked={useEncryption} onChange={handleSSLChange}/>}
          label={t("useSecureWebSocket")}/>
      </FormGroup>
      <div className={classes.buttonContainer}>
      <Button color="secondary" 
              variant="contained" 
              classes={ { root: classes.button } }
              onClick={handleReset}>{t("reset")}</Button>
      <Button color="primary" 
              variant="contained" 
              classes={ { root: classes.button } }
              onClick={handleUpdate}>{t("update")}</Button>
      </div>
    </form>
  </Panel>;
};

export default ConnectionDetails;
