import React from 'react';

import Gauges from './Gauges';
import SegmentedArcGauge from './Gauges/SegmentedArcGauge';
import ArcText from './Gauges/ArcText';
import ArcHeader from './Gauges/ArcHeader';

import { makeStyles, useTheme } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  centerText: {
    ...theme.typography.rpmText
  },
  gaugeHeader: {
    ...theme.typography.gaugeHeader
  }
}));

const ThreeBarGauge = (props) => {
  const {
    value0 = 0,
    min0 = 0,
    max0 = 1,
    units0 = "",
    decimalPoints0 = 0,
    label0 = "Label 0",
    hideValue0 = false,

    value1 = 0,
    min1 = 0,
    max1 = 1,
    units1 = "",
    decimalPoints1 = 0,
    label1 = "Label 1",
    hideValue1 = false,

    value2 = 0,
    min2 = 0,
    max2 = 1,
    units2 = "",
    decimalPoints2 = 0,
    label2 = "Label 2",
    hideValue2 = false,

    centerValue,
    centerLabel,
    children
  } = props;

  const max_min0 = (max0-min0 < .000001 ? 1 : max0-min0);
  const max_min1 = (max1-min1 < .000001 ? 1 : max1-min1);
  const max_min2 = (max2-min2 < .000001 ? 1 : max2-min2);

  const padding = 3*Math.PI/180;
  const classes = useStyles();
  const theme = useTheme();

  return <Gauges width={"100%"}>
      { /* Gauge 0 */ }
      <SegmentedArcGauge c={ { x: 50, y: 50 } } 
                                 r={48} 
                                 startAngle={150*Math.PI/180} 
                                 endAngle={270*Math.PI/180} 
                                 segments={(value0-min0)/(max_min0)*5} 
                                 segmentRatio={.95} 
                                 segmentWidth={5} 
                                 totalSegments={5} 
                                 paddingStart={padding} 
                                 paddingEnd={padding}/> 
      { hideValue0 ? null : 
      <ArcText c={ {x: 50, y: 50}} 
                       r={50} 
                       startAngle={150*Math.PI/180} 
                       endAngle={270*Math.PI/180} 
                       min={min0} 
                       max={max0} 
                       value={value0} 
                       paddingStart={padding} 
                       paddingEnd={padding} 
                       fontSize={3} 
                       textAnchor={"end"} 
                       units={units0}
                       decimalPoints={decimalPoints0}/> }
      <ArcHeader c={ { x: 50, y: 50 } } 
                 r={30} 
                 startAngle={150*Math.PI/180} 
                 endAngle={270*Math.PI/180} 
                 text={label0} 
                 paddingStart={padding} 
                 paddingEnd={padding}/> 

      { /* Gauge 1 */ }
      <SegmentedArcGauge c={ { x: 50, y: 50 } } 
                                 r={48} 
                                 startAngle={30*Math.PI/180} 
                                 endAngle={-90*Math.PI/180} 
                                 segments={(value1-min1)/(max_min1)*5} 
                                 segmentRatio={.95} 
                                 segmentWidth={5} 
                                 totalSegments={5} 
                                 paddingStart={padding} 
                                 paddingEnd={padding}/>
      { hideValue1 ? null : 
      <ArcText c={ {x: 50, y: 50}} 
                       r={50} 
                       startAngle={30*Math.PI/180} 
                       endAngle={-90*Math.PI/180} 
                       min={min1} 
                       max={max1} 
                       value={value1} 
                       paddingStart={padding} 
                       paddingEnd={padding} 
                       fontSize={3} 
                       textAnchor={"start"} 
                       units={units1} 
                       decimalPoints={decimalPoints1}/> }
      <ArcHeader c={ { x: 50, y: 50 } } 
                 r={30} 
                 startAngle={-90*Math.PI/180} 
                 endAngle={30*Math.PI/180} 
                 text={label1} 
                 paddingStart={padding} 
                 paddingEnd={padding}/> 

      { /* Gauge 2 */ }
      <SegmentedArcGauge c={ { x: 50, y: 50 } } 
                                 r={48} 
                                 startAngle={30*Math.PI/180} 
                                 endAngle={150*Math.PI/180} 
                                 segments={(value2-min2)/(max_min2)*5} 
                                 segmentRatio={.95} 
                                 segmentWidth={5} 
                                 totalSegments={5} 
                                 paddingStart={padding} 
                                 paddingEnd={padding}/> 
      { hideValue2 ? null :                        
      <ArcText c={ {x: 50, y: 50}} 
                       r={55} 
                       startAngle={30*Math.PI/180} 
                       endAngle={150*Math.PI/180} 
                       min={min2} 
                       max={max2} 
                       value={value2} 
                       paddingStart={padding} 
                       paddingEnd={padding} 
                       fontSize={3} 
                       textAnchor={"middle"} 
                       units={units2} 
                       decimalPoints={decimalPoints2}/> }
      <ArcHeader c={ { x: 50, y: 50 } } 
                 r={30} 
                 startAngle={150*Math.PI/180} 
                 endAngle={30*Math.PI/180} 
                 text={label2} 
                 dy={3} 
                 paddingStart={padding} 
                 paddingEnd={padding}/> 

      <text x={50} 
                     y={50} 
                     textAnchor="middle" 
                     alignmentBaseline="middle" 
                     className={classes.centerText} 
                     fill="white" 
                     stroke={theme.palette.primary.main} 
                     strokeWidth={.2} 
                     style={ { letterSpacing: 0 } }>
       {Math.round(centerValue).toLocaleString("en-US")}
      </text>
      <text x={60} 
            y={60} 
            textAnchor="middle" 
            alignmentBaseline="middle" 
            className={classes.gaugeHeader} 
            style={ { fontSize: 5 }}>
        {centerLabel}
      </text>
      {children}

    </Gauges>;
};
export default ThreeBarGauge;
