theme.palette.error.verylight = "#fcf0f0";

window.theme = theme;

const useStyles = makeStyles((theme) => ({
  mainContent: {
    position: "fixed",
    width: "100%",
    height: "calc( 100% - 86px - 51px )",
    top: 86,
    left: 0,
    overflow: 'scroll'
  }
}));


function AppPage() {
  const classes = useStyles();
  return (
    <ThemeProvider theme={theme}>
      <ConnectedRouter history={history}>
        <TopBar/>
        <div className={classes.mainContent}>
          <Switch>
           { tabOrder.filter((tab) => !tab.alwaysMounted).map((tab) => {
             const {
               component,
               id
             } = tab;
  
             const TabContent = component;
  
             return <Route path={id} key={id}><TabContent/></Route>
           }) }
           <Route exact path="/">{ tabs[DEFAULT_TAB].component }</Route>
          </Switch>
         { tabOrder.filter((tab) => tab.alwaysMounted).map((tab) => {
           const {
             component,
             id
           } = tab;
  
           const TabContent = component;
  
           return <TabContent key={id}/>
         }) }
        </div>
        <BottomBar/>
        <KeyboardShortcuts/>
        <Popups/>
      </ConnectedRouter>
    </ThemeProvider>
    );
};

// loading component for suspense fallback
const Loader = () => (
  <div className="App">
    <img src={logo} className="App-logo" alt="logo" />
    <div>loading...</div>
  </div>
);

function App(props) {
  const {
    dialect,
    displayUnits,
    activeCodes,
  } = props;
  return (
  <UnitsProvider value={{displayUnits, activeCodes}}>
    <DialectProvider value={dialect}>
      <Suspense fallback={<Loader/>} >
        <AppPage/>
      </Suspense>
    </DialectProvider>
  </UnitsProvider>
  );
}

export default App;
