import { connect } from 'react-redux';
import Language from '../components/Language';
import pentaActions from '../modules/pentaui/actions';
import { selectLanguage, selectDialect } from '../modules/pentaui/selectors';


export default connect( 
(state, ownProps) => {
  return {
    language: selectLanguage(state),
    dialect: selectDialect(state),
  }
}, 
(dispatch, ownProps) => ({
  onChangeLanguage: (code) => {
    dispatch( pentaActions.changeLanguage(code) );
  },
  onChangeDialect: (code) => {
    dispatch( pentaActions.changeDialect(code) );
  }
}))(Language);
