import React, { useState, useRef } from 'react';
import Paper from '@material-ui/core/Paper';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import Button from '@material-ui/core/Button';
import { useTheme, makeStyles } from '@material-ui/core/styles';
import Grid from '@material-ui/core/Grid';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import Hidden from '@material-ui/core/Hidden';
import MenuIcon from '@material-ui/icons/Menu';
import ArrowRightIcon from '@material-ui/icons/ArrowRight';
import { ReactComponent as LogoSvg } from "../logo.svg";

import SetupTab from './SetupTab';
//import SimulatorTab from './SimulatorTab';
import ManualTab from './ManualTab';
import ProductionTab from './ProductionTab';
import HistoryTab from './HistoryTab';
import ConfigTab from './ConfigTab';

import { useHistory, useLocation } from 'react-router-dom';
import { useTranslation } from 'react-i18next';

import classnames from 'classnames';

const useStyles = makeStyles((theme) => ({
  active: {
    color: theme.palette.primary.main
  },
  tabButton: {
    ...theme.typography.mainTab
  },
  tabItem: {
    textAlign: "center"
  },
  tabLabelContainer: {
    whiteSpace: "nowrap"
  },
  tabLabel: {
    lineHeight: "24px",
    height: 24,
    verticalAlign: "middle"
  },
  tabText: {
    lineHeight: "24px",
    height: 24,
    verticalAlign: "middle"
  },
  tabIcon: {
    lineHeight: "18px",
    height: 18,
    width: 18,
    marginRight: -theme.spacing(1),
    verticalAlign: "middle"
  },
  tabLine: {
    borderWidth: "1px",
    borderStyle: "solid",
    borderRadius: "1px"
  },
  logoContainer: {
    position: "fixed",
    width: 40,
    height: 55,
    marginLeft: theme.spacing(2),
    marginTop: theme.spacing(2)
  },
  tabsContainer: {
    position: "fixed",
    left: 40,
    width: "calc( 100% - 40px )",
    height: 55,
    marginLeft: theme.spacing(2),
    marginTop: theme.spacing(2)
  }
}));

export const DEFAULT_TAB = "/manual";
export const SETUP_TAB = "/setup";
export const SIMULATOR_TAB = "/simulator";
export const MANUAL_TAB = "/manual";
export const PRODUCTION_TAB = "/production";
export const HISTORY_TAB = "/history";
export const CONFIG_TAB = "/config";

export const tabs = {
  [SETUP_TAB]: { id: SETUP_TAB, label: "setup", canHide: false, component: SetupTab },
//  [SIMULATOR_TAB]: { id: SIMULATOR_TAB, label: "simulator", canHide: true, component: SimulatorTab },
  [MANUAL_TAB]: { id: MANUAL_TAB, label: "manual", canHide: false, component: ManualTab },
  [PRODUCTION_TAB]: { id: PRODUCTION_TAB, label: "production", canHide: false, component: ProductionTab, alwaysMounted: true },
  [HISTORY_TAB]: { id: HISTORY_TAB, label: "history", canHide: true, component: HistoryTab },
  [CONFIG_TAB]: { id: CONFIG_TAB, label: "config", canHide: true, component: ConfigTab }, 
};

export const tabOrder = [ 
  tabs[SETUP_TAB],
  tabs[MANUAL_TAB],
  tabs[PRODUCTION_TAB],
//  tabs[SIMULATOR_TAB],
  tabs[HISTORY_TAB],
  tabs[CONFIG_TAB]
];

const Tabs = (props) => {
  const { t } = useTranslation();
  const history = useHistory();
  const location = useLocation();
  const theme = useTheme();

  const [ menuOpen, setMenuOpen ] = useState(false);
  const buttonRef = useRef(null);
  const isSmall = useMediaQuery(theme.breakpoints.down('xs'));
  const toggleMenuOpen = (e) => {
    setMenuOpen(!menuOpen);
  };

  const tabLocation = location.pathname.split("/")[1];
  const activeTab = tabLocation ? "/" + tabLocation : DEFAULT_TAB ;

  const setTab = (id) => () => {
    history.push(id);
    setMenuOpen(false);
  };
  const handleClose = () => {
    setMenuOpen(false);
  };
  const classes = useStyles();

  return <Paper elevation={0} square>
  <div className={classes.logoContainer}>
  <a target="_blank" rel="noreferrer" href="https://www.pocketnc.com">
    <LogoSvg width="40" alt="Pocket NC" fill={theme.palette.primary.main}/>
  </a>
  </div>
  <div className={classes.tabsContainer}>
  <Grid container>
    { 
      tabOrder.map((tab, i) => {
        const { id, label, canHide } = tab;

        const isActive = id === activeTab;

        return (<React.Fragment key={tab.id}>
          <Hidden smDown={canHide}>
            <Grid item md={2} sm={3} xs={3} className={ classes.tabItem }>
              <Button classes={ { root: classes.tabButton } } onClick={setTab(id)} color={ isActive ? 'primary' : 'default' }>
                <div className={classes.tabLabelContainer}>
                <div className={classes.tabLine} style={ { borderColor: isActive ? theme.palette.primary.main : theme.palette.text.main } }></div>
                <div className={classes.tabLabel}>{ (isActive && !isSmall) ? <ArrowRightIcon className={classes.tabIcon}/> : null } <span className={classes.tabText}>{t(label)}</span></div>
                </div>
              </Button>
            </Grid>
          </Hidden>
        </React.Fragment>);
      })
    }
    <Hidden mdUp>
      <Grid item sm={3} xs={3} className={ classes.tabItem }>
        <Button classes={ { root: classes.tabButton } } onClick={toggleMenuOpen} ref={buttonRef}><MenuIcon/></Button>
        <Menu open={menuOpen} 
              anchorEl={buttonRef.current}
              onClose={handleClose}>
          {
            tabOrder.filter((tab) => tab.canHide).map((tab) => {
              const { id, label } = tab;

              const isActive = id === activeTab;

              return (<MenuItem key={id} onClick={setTab(id)} classes={ { root: classnames(classes.tabButton, {[classes.active]: isActive}) } }>
                 { isActive ? <ArrowRightIcon/> : null } {t(label)}
              </MenuItem>);
            })
          }
        </Menu>
      </Grid>
    </Hidden>
  </Grid>
  </div>
  </Paper>;
};
export default Tabs;
