import React, { useCallback, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Panel from './Panel';
import InfoIcon from '@material-ui/icons/Info';
import WarningIcon from '@material-ui/icons/Warning';
import ErrorIcon from '@material-ui/icons/Error';
import { WARNING, INFO, ERROR } from '../modules/rockhopper/constants';
import { DEBUG } from '../modules/rockhopper/constants';
import classnames from 'classnames';
import Timestamp from './Timestamp';
import produce from 'immer';
import MessageDetails from './MessageDetails';
import Button from '@material-ui/core/Button';
import { useTranslation } from 'react-i18next';

const useStyles = makeStyles((theme) => ({
  moreLessLink: {
    fontSize: '.7em',
    fontStyle: 'italic',
    cursor: "pointer",
    verticalAlign: "text-top"
  },
  message: {
    verticalAlign: "top"
  },
  timestamp: {
    display: "block",
    fontStyle: 'italic',
    marginRight: theme.spacing(2),
    float: "right",
    fontSize: '.7em',
    textAlign: "right",
  },
  list: {
    listStyleType: "none",
    paddingLeft: 0,
    marginTop: 0,
    marginBottom: 0,
    maxHeight: 400,
    overflow: "scroll"
  },
  listItem: {
    padding: theme.spacing(1)
  },
  error: {
    backgroundColor: theme.palette.error.verylight,
  },
  warning: {
    backgroundColor: theme.palette.secondary.verylight,
  },
  info: {
    backgroundColor: theme.palette.primary.verylight,
  },
  errorIcon: {
    color: theme.palette.error.main,
  },
  warningIcon: {
    color: theme.palette.secondary.main,
  },
  infoIcon: {
    color: theme.palette.primary.main,
  },
  avatar: {
    backgroundColor: "rgba(0,0,0,0)",
    padding: 0
  },
}));


// example data for testing
const messages = [ 
                   { id: 1, type: INFO, message: "This is a very long info message that should be longer than we can see, but it wasn't so I'll keep typing.", timestamp: Date.now()-1000*60*60*24*232, details: { hello: "world" } },
                   { id: 2, type: WARNING, message: "This is a warning message", timestamp: Date.now()-1000*60*60*24*20, details: {"meta":{"actionEmitters":["@@rockhopper/RESPONSE/FILE_UPLOAD_CHUNK_0/PYTHON.NGC"]},"code":"?Contains python comment","data":{"message":"Contains python comment: /home/pocketnc/ncfiles/python.ngc","filename":"/home/pocketnc/ncfiles/python.ngc"},"id":"FILE_UPLOAD_CHUNK_0_PYTHON.NGC"} },
                   { id: 3, type: ERROR, message: "This is an error message", timestamp: Date.now()-1000*60*60*24*10, details: {"meta":{"actionEmitters":["@@rockhopper/RESPONSE/FILE_UPLOAD_CHUNK_0/PYTHON.NGC"]},"code":"?Contains python comment","data":{"message":"Contains python comment: /home/pocketnc/ncfiles/python.ngc","filename":"/home/pocketnc/ncfiles/python.ngc"},"id":"FILE_UPLOAD_CHUNK_0_PYTHON.NGC"} }, 
                   { id: 4, type: DEBUG, message: "This is a debug message", timestamp: Date.now()-1000*60*60*24*9, details: {"meta":{"actionEmitters":["@@rockhopper/RESPONSE/FILE_UPLOAD_CHUNK_0/PYTHON.NGC"]},"code":"?Contains python comment","data":{"message":"Contains python comment: /home/pocketnc/ncfiles/python.ngc","filename":"/home/pocketnc/ncfiles/python.ngc"},"id":"FILE_UPLOAD_CHUNK_0_PYTHON.NGC"} } 
                 ];
const numItems = 100;
for(let i = 0; i <= numItems; i++) {
  const type = [ INFO, WARNING, ERROR, DEBUG ][Math.floor(Math.random()*4)];
  messages.push({ id: i+5, type: type, message: "This is a " + type + " message", timestamp: Date.now()-1000*60*60*24*7*((numItems-i)/numItems*(numItems-i)/numItems), details: {"meta":{"actionEmitters":["@@rockhopper/RESPONSE/FILE_UPLOAD_CHUNK_0/PYTHON.NGC"]},"code":"?Contains python comment","data":{"message":"Contains python comment: /home/pocketnc/ncfiles/python.ngc","filename":"/home/pocketnc/ncfiles/python.ngc"},"id":"FILE_UPLOAD_CHUNK_0_PYTHON.NGC"} });
}

const MessageLog = (props) => {
  const {
    messages
  } = props;
  const [ numMessages, setNumMessages ] = useState(50);
  const [ open, setOpen ] = useState({});

  const { t } = useTranslation();
  const classes = useStyles();

  const handleClick = (msg) => (e) => {
    setOpen(produce(open, draft => { draft[msg.id] = !draft[msg.id] }));
  };
  const showMoreMessages = useCallback(() => {
    setNumMessages(numMessages+50);
  }, [ numMessages, setNumMessages ])

  return <Panel label={t("messageLog")}>
    <ul className={classes.list}>
    { 
      (numMessages < messages.length ? messages.slice(0, numMessages) : messages).map((msg) => {
        let icon;
        switch(msg.type) {
          case ERROR:
            icon = <ErrorIcon className={classnames(classes.errorIcon, classes.avatar)}/>
            break;
          case WARNING:
            icon = <WarningIcon className={classnames(classes.warningIcon, classes.avatar)}/>
            break;
          case INFO:
            icon = <InfoIcon className={classnames(classes.infoIcon, classes.avatar)}/>
            break;
          default:
            icon = null;
        }
        return (
          <li key={msg.id} className={classnames(classes.listItem, classes[msg.type])}>
            {icon}
            <Timestamp className={classes.timestamp} timestamp={msg.timestamp}/> <span className={classes.message}>{msg.message}</span>
            { msg.details ? <span className={classes.moreLessLink} onClick={handleClick(msg)}>{ open[msg.id] ? " << " + t("less") : t("more") +  " >>" }</span> : null }
            { open[msg.id] ? <MessageDetails msg={msg}/> : null }
          </li>
        );
      })
    }
    </ul>
  { numMessages < messages.length ? <Button onClick={showMoreMessages}>{t("showOlderMessages")}</Button> : null }
  </Panel>;
};
export default MessageLog;
