import React, { useEffect, useRef, useState, useCallback } from 'react';

import { useTheme, makeStyles } from '@material-ui/styles';
import { useGesture } from 'react-use-gesture';
import { lineTo, moveTo } from './Gauges/util/path';
import useFilter from '../hooks/useFilter';
//import useSpring from '../hooks/useSpring';

const useStyles = makeStyles((theme) => ({
  value: {
  },
  svg: {
    touchAction: "none"
  }
}));

export const HORIZONTAL = "horizontal";
export const VERTICAL = "vertical";

const IncrementalSpinner = (props) => {
  const {
    defaultValue = 0,
    orientation = HORIZONTAL,
    diameter = 100,
    thickness = 30,
    tickDist = 10,
    onChange,
    reverse = false,
    enableWheel = true,
    enableDrag = true
  } = props;

  const theme = useTheme();
  const [ tickOffset, setTickOffset, animatedTickOffset ] = useFilter(defaultValue);
  const [ wholeTickOffset, setWholeTickOffset ] = useState(Math.floor(defaultValue));
  const [ wheelTimeoutID, setWheelTimeoutID ] = useState(null);
  const svgRef = useRef(null);

  const curvature = Math.min(1, Math.max(0, props.curvature || 0));

  let opts = {};
  if(orientation === HORIZONTAL) {
    opts.width = diameter;
    opts.height = thickness;
    if(reverse) {
      opts.startX = diameter;
      opts.startY = 0;
      opts.endX = 0;
      opts.endY = 0;
    } else {
      opts.startX = 0;
      opts.startY = 0;
      opts.endX = diameter;
      opts.endY = 0;
    }
    opts.tickDirX = 0;
    opts.tickDirY = 1;
  } else {
    opts.width = thickness;
    opts.height = diameter;
    if(reverse) {
      opts.startX = 5;
      opts.startY = diameter;
      opts.endX = 5;
      opts.endY = 0;
    } else {
      opts.startX = 5;
      opts.startY = 0;
      opts.endX = 5;
      opts.endY = diameter;
    }
    opts.tickDirX = 1;
    opts.tickDirY = 0;
  }

  const {
    width,
    height,
    startX,
    startY,
    endX,
    endY,
    tickDirX,
    tickDirY
  } = opts;

  const classes = useStyles();

  const tValues = [];
  const minValue = -animatedTickOffset-diameter*.5/tickDist;
  const maxValue = -animatedTickOffset+diameter*.5/tickDist;
  const EPS = .00001;

  const tickThickness = 1;
  for(let tickValue = Math.ceil(minValue); tickValue < maxValue+EPS; tickValue += 1) {
    const flatT = (tickValue-minValue)/(maxValue-minValue);
    const curvedT = (Math.cos(Math.PI*Math.max(0,Math.min(1, flatT)))-1)/(-2);
    const t = (1-curvature)*flatT+curvature*curvedT;

    const flatStrokeWidth = tickThickness;
    const curvedStrokeWidth = Math.cos(Math.PI*Math.max(0,Math.min(1, flatT))-.5*tickThickness)-Math.cos(Math.PI*Math.max(0,Math.min(1, flatT))+.5*tickThickness);
    const strokeWidth = (1-curvature)*flatStrokeWidth+curvature*curvedStrokeWidth;
    tValues.push({ tickValue, t, strokeWidth });
  }

  const lines = tValues.map(({ tickValue, t, strokeWidth }) => {
//    const mag = tickValue%10 === 0 ? 3 : (tickValue%5 === 0 ? 2 : 1);
    const mag = 1;
    const x1 = startX*(1-t)+endX*t;
    const y1 = startY*(1-t)+endY*t;
    const x2 = x1+tickDirX*thickness*.25*mag;
    const y2 = y1+tickDirY*thickness*.25*mag;

    return <line key={tickValue} x1={x1} y1={y1} x2={x2} y2={y2} stroke="black" strokeOpacity={.2} strokeWidth={strokeWidth}/>
  });

  const updateTickOffsetWithDelta = useCallback((delta) => {
    const newTickOffset = tickOffset+(reverse ? -1 : 1)*delta/tickDist;
    setTickOffset(newTickOffset);

    const dOffset = newTickOffset-wholeTickOffset < 0 ? -Math.floor(wholeTickOffset-newTickOffset) : Math.floor(newTickOffset-wholeTickOffset);
    if(Math.abs(dOffset) > 0) {
      setWholeTickOffset(wholeTickOffset+dOffset);
      onChange(dOffset);
    }
  }, [ setTickOffset, tickOffset, tickDist, onChange, reverse, setWholeTickOffset, wholeTickOffset ]); 

  const handleDrag = useCallback(({ event, delta, ...test }) => {
    window.getSelection().removeAllRanges();
    const d = orientation === HORIZONTAL ? delta[0]: delta[1];
    if(wheelTimeoutID) {
      clearTimeout(wheelTimeoutID);
    }
    if(Math.abs(d) > 0) {
      updateTickOffsetWithDelta(d);
    }
  }, [ updateTickOffsetWithDelta, orientation, wheelTimeoutID ]);

  const handleDragEnd = useCallback(({ event }) => {
    setTickOffset(wholeTickOffset);
  }, [ setTickOffset, wholeTickOffset ]);

  const handleWheel = useCallback((state) => {
    const {
      event,
      wheeling,
      delta
    } = state;
    if(wheeling) {
      event.preventDefault();
    }
    const d = delta[1]; // wheel events are generally y deltas
    updateTickOffsetWithDelta(.25*d);
    if(wheelTimeoutID) {
      clearTimeout(wheelTimeoutID);
    }
    setWheelTimeoutID(setTimeout(() => {
      setTickOffset(wholeTickOffset);
    }, 2000));
  }, [ setTickOffset, wholeTickOffset, updateTickOffsetWithDelta, setWheelTimeoutID, wheelTimeoutID ]);

  const gestureCallbacks = {};
  if(enableDrag) {
    gestureCallbacks.onDrag = handleDrag;
    gestureCallbacks.onDragEnd = handleDragEnd;
  }

  if(enableWheel) {
    gestureCallbacks.onWheel = handleWheel;
  }

  useGesture(gestureCallbacks, { domTarget: svgRef, eventOptions: { passive: false } });

  useEffect(() => {
    return () => {
      clearTimeout(wheelTimeoutID);
    }
  }, [ wheelTimeoutID ]);

  const indicatorWidth = 10;
  const indicatorHeight = 5;
  const indicatorPath = orientation === HORIZONTAL ? 
    [ moveTo(width*.5-indicatorWidth*.5,height), lineTo(width*.5, height-indicatorHeight), lineTo(width*.5+indicatorWidth*.5, height), "Z" ]
    :
    [ moveTo(0, height*.5-indicatorWidth*.5), lineTo(indicatorHeight, height*.5), lineTo(0, height*.5+indicatorWidth*.5), "Z" ];
  return <svg ref={svgRef} width={width} height={height} viewBox={"0 0 " + width + " " + height} className={classes.svg}>
  {lines}
  <path d={indicatorPath.join(" ")} fill={theme.palette.primary.main}/>
  </svg>;
};
export default IncrementalSpinner;
