import { useContext } from 'react';
import { useTranslation } from 'react-i18next';
import { DialectContext } from '../components/DialectProvider';

const useTranslationHook = () => {
  const { t, i18n, ready } = useTranslation();
  const dialect = useContext(DialectContext);

  const tDialect = (key) => {
    const dialectKey = dialect + ":" + key;
    const dialectTranslation = t(dialectKey);
    if(dialectTranslation !== key) {
      return dialectTranslation;
    }

    return t(key);
  };

  return { t: tDialect, i18n, ready };
};
export default useTranslationHook;
