import { connect } from 'react-redux';
import KeyboardShortcutsConfig from '../components/KeyboardShortcutsConfig';
import rockhopperActions from '../modules/rockhopper/actions';
import { selectKeyboardShortcuts, 
  selectKeyboardShortcutsAxisSelectJog,
  selectKeyboardShortcutsJogModifiers,
  selectKeyboardShortcutsJog, 
  selectKeyboardShortcutsOther, 
  selectKeyboardShortcutsMap,
  selectTrajCoordinates,
} from '../modules/rockhopper/selectors';

export default connect(
  (state, ownProps) => {
    return {
      shortcuts: selectKeyboardShortcuts(state),
      axisSelectJogShortcuts: selectKeyboardShortcutsAxisSelectJog(state),
      jogModifiers: selectKeyboardShortcutsJogModifiers(state),
      jogKeys: selectKeyboardShortcutsJog(state),
      otherShortcuts: selectKeyboardShortcutsOther(state),
      shortcutsMap: selectKeyboardShortcutsMap(state),
      trajCoords: selectTrajCoordinates(state),
    }
  },
  (dispatch, ownProps) => ({
    onSubmitShortcuts: (shortcuts) => {
      dispatch( rockhopperActions.put.saveClientConfig( { data: [ 'KeyboardShortcuts', shortcuts ] } ) )
    },
  })
)(KeyboardShortcutsConfig)