import { connect } from 'react-redux';
import Units from '../components/Units';
import actions from '../modules/rockhopper/actions';
import { selectDisplayUnits } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => ({
  displayUnits: selectDisplayUnits(state),
}),
(dispatch, ownProps) => ({ 
  onSetDisplayUnits: (newDisplayUnits) => {
    dispatch( actions.updateHelpers.setDisplayUnits(newDisplayUnits) );
    dispatch( actions.put.saveClientConfig({ data: [ 'displayUnits', newDisplayUnits ] }));
  },
}))(Units);
