export const createWebSocket = (server, port, encrypted) => {
  try {
    return new WebSocket(`ws${ encrypted ? 's' : '' }://${server}:${port}/websocket/`);
  } catch {
    return null;
  }
};

const sendWebSocketMessage = (socket, message) => {
  socket.send(JSON.stringify(message));
};

