export const loginToRockhopper = (socket, user, password) => {
  return new Promise((resolve, reject) => {
    const id = LOGIN_ID;
    const onOpen = () => {
      sendWebSocketMessage(socket, cmds.login(user, password));
    };

    const onClose = (e) => {
      resolve(false);
    };

    const onMessage = (e) => {
      const data = JSON.parse(e.data);

      if(data.code === OK_CODE && data.id === id) {
        resolve(true);
        socket.removeEventListener("open", onOpen);
        socket.removeEventListener("close", onClose);
        socket.removeEventListener("message", onMessage);
      }

      if(data.code !== OK_CODE && data.id === id) {
        console.log(LOGIN_FAILED_MSG, data);
        resolve(false);
      }
    };

    socket.addEventListener("open", onOpen);
    socket.addEventListener("close", onClose);
    socket.addEventListener("message", onMessage);
  });
};

export const closeWebSocket = (socket) => {
  if(socket.readyState !== WebSocket.CLOSED && socket.readyState !== WebSocket.CLOSING) {
    socket.close();
  }
};

const createRockhopperEventChannel = (socket) => {
  return eventChannel(emit => {
    let responseReceived = true;

    const onMessage = (e) => {
      const data = JSON.parse(e.data);

      responseReceived = true;
      if(data.id !== HEARTBEAT_ID) {
        emit(data);
      }
    };

    const onClose = () => {
      emit(new Error(SOCKET_CLOSED_MSG));
    };

    socket.addEventListener("message", onMessage);
    socket.addEventListener("close", onClose);

    const interval = setInterval(() => {
      if(responseReceived) {
        // If we have heard back from the server since our last
        // heartbeat, then we're still connected. Send another one.
        const id = HEARTBEAT_ID;
        const name = HEARTBEAT_GET_NAME;
        sendWebSocketMessage(socket, cmds.get(id, name));
        responseReceived = false;
      } else {
        // If we haven't heard back from our heartbeat message 
        // then we've disconnected, so end the event channel.
        emit(new Error(HEARTBEAT_TIMEOUT_MSG));
      }
    }, HEARTBEAT_PERIOD);

    return () => {
      socket.removeEventListener("message", onMessage);
      socket.removeEventListener("close", onClose);
      clearInterval(interval);
    };
  });
};

// messages is a mutable object that will contain
// watch messages keyed by their id.
// It will always be updated with the latest received
// watch messages. This is to address performance issues
// where handling all the watch messages was bogging
// down the UI. Every 50ms we issue the update actions
// with the latest messsages. If the UI ever gets bogged
// down, it will end up discarding old messages rather
// than get even more behind.
export function *updateWatchedData(messages) {
  while(true) {
    const {
      actionEmitters
    } = yield select(selectRockhopper);
    const ids = Object.keys(messages);
    for(let id of ids) {
      const msg = messages[id];

      if(actionEmitters[msg.id]) {
        for(let actionEmitter of actionEmitters[msg.id]) {
          yield put(rockhopperMessage(actionEmitter.type, msg));
        }
      }

      if(msg.meta && msg.meta.actionEmitters) {
        for(let actionEmitter of msg.meta.actionEmitters) {
          yield put(rockhopperMessage(actionEmitter, msg));
        }
      }
      delete messages[id];
    }
    yield delay(50);
  }
}

export function *receiveRockhopperMessages(socket) {
  const rockhopperChannel = yield call(createRockhopperEventChannel, socket);

  const queuedWatchMessages = {};
  yield fork(updateWatchedData, queuedWatchMessages);

  while(true) {
    try {
      const msg = yield take(rockhopperChannel);
      const {
        actionEmitters
      } = yield select(selectRockhopper);

      if(msg.id.startsWith("WATCH_")) {
        // We watch a number of message types and the UI can get bogged down
        // so use this mutable data structure to keep track of the latest messages
        // for any given id, so that we can update them only once every 50ms in
        // updateWatchedData above.
        queuedWatchMessages[msg.id] = msg;
      } else {
        if(actionEmitters[msg.id]) {
          for(let actionEmitter of actionEmitters[msg.id]) {
            yield put(rockhopperMessage(actionEmitter.type, msg));
          }
        }

        if(msg.meta && msg.meta.actionEmitters) {
          for(let actionEmitter of msg.meta.actionEmitters) {
            yield put(rockhopperMessage(actionEmitter, msg));
          }
        }
      }

      if(msg.id === "WATCH_ERROR" && msg.data) {
        if(actions.messages[msg.data.type]) {
          yield put(actions.messages[msg.data.type](msg.data.text, msg));
        } else {
          yield put(actions.messages.error(msg.data.text, msg));
        }

        yield put(actions.put.clearError());
      }

      if(msg.code !== OK_CODE || (msg.data && msg.data.notify)) {
        const type = (msg.data.notify && msg.data.notify.type) || "error";
        const message = (msg.data && msg.data.message) ||
                        (msg.data && msg.data.notify && msg.data.notify.message) ||
                        (msg.data && msg.data.notify && msg.data.notify.text) ||
                        msg.code.replace("?", "");
        yield put(actions.messages[type](message, msg));
      }
    } catch(e) {
      console.log("caught error", e);
      rockhopperChannel.close();
    }
  }
}

export function *sendRockhopperMessage(socket, action) {
  yield call(sendWebSocketMessage, socket, action.payload);
}

export function *sendRockhopperMessages(socket) {
  yield takeEvery([
    actions.cmds.raw,
    actions.cmds.get,
    actions.cmds.watch,
    actions.cmds.put,
    actions.cmds.listGet,
    actions.cmds.listPut,
    ...Object.values(actions.watch),
    ...Object.values(actions.unwatch),
    ...Object.values(actions.get),
    ...Object.values(actions.put),
    ...Object.values(actions.getHelpers),
    ...Object.values(actions.putHelpers)
  ], sendRockhopperMessage, socket);
}

export function *connectToRockhopper() {
  const {
    server,
    port,
    user,
    password,
    encrypted
  } = yield select(selectWebSocket);

  const socket = yield call(createWebSocket, server, port, encrypted);

  if(socket) {
  //  yield call(registerDebugCallbacks, socket);
    const loggedIn = yield call(loginToRockhopper, socket, user, password);
    if(loggedIn) {
      yield put(actions.messages.info("Connected to machine."));
      const sendTask = yield fork(sendRockhopperMessages, socket);
      const receiveTask = yield fork(receiveRockhopperMessages, socket);
      yield put(actions.setLoggedIn(true));

      yield join(receiveTask);
      yield cancel(sendTask);
      yield put(actions.messages.warning("Lost connection with machine."));
    }
    yield call(closeWebSocket, socket);
  }
};

export function* alwaysReconnect() {
  const connectionDetailsChannel = yield actionChannel(actions.setConnectionDetails);
  while(1) {
    const result = yield race({ updateConnectionDetails: take(connectionDetailsChannel),
                                connection: call(connectToRockhopper)});
    if(result.updateConnectionDetails) {
      // connection details changed, store them  to local storage,
      // then reconnect
      yield call(storeConnectionDetailsToLocalStorageSaga);
    } else {
      // otherwise the connection failed (either errored,  closed, or never successfully connected)
      // delay a bit before trying again
      yield delay(1000);
    }
    yield put(actions.setLoggedIn(false));
  }
}

const storeConnectionDetailsToLocalStorage = (server, port, user, password, encrypted) => {
  localStorage.setItem(LOCALSTORAGE_KEY, JSON.stringify({ server, port, user, password, encrypted }));
};

export function* storeConnectionDetailsToLocalStorageSaga() {
  const {
    server,
    port,
    user,
    password,
    encrypted
  } = yield select(selectWebSocket);
  yield call(storeConnectionDetailsToLocalStorage, server, port, user, password, encrypted);
}

export function* watchFeatureDependentStatusItems() {
  // submit the request for supported features
  yield put(actions.getHelpers.features());

  // wait for the response
  yield take(actions.response.getHelpers.features);

  // The reducer should take care of storing the features, so select them here
  const features = yield select(selectFeatures);

  for(let key in actions.watch) {
    const requiresFeature = statusItemToRequiresFeature[key];
    if(requiresFeature && features[requiresFeature]) {
      const actionCreator = actions.watch[key];
      yield put(actionCreator())
    }
  }
}

export function* watchItem(item) {
  const {
    name,
    indexed,
    maxIndex,
    json
  } = item;

  if(indexed) {
    for(let index = 0; index <= maxIndex; index++) {
      yield put(actions.watch({ name, data: { index }, meta: { actionEmitters: [ actions.receiveWatchData.toString() ], name, json, index }}));
    }
  } else {
    yield put(actions.watch({ name, meta: { actionEmitters: [ actions.receiveWatchData.toString() ], name, json }}));
  }
}

