// yields substrings of data
export function* fileUploadArrayBufferChunkGenerator(data) {
  const size = data.byteLength;
  const numChunks = Math.ceil(size/FILE_UPLOAD_CHUNK_SIZE);

  for(let i = 0; i < numChunks; i++) {
    const start = i === 0;
    const end = i === numChunks-1;
    const chunkData = new Uint8Array(data.slice(i*FILE_UPLOAD_CHUNK_SIZE, (i+1)*FILE_UPLOAD_CHUNK_SIZE));
    yield { start, end, data: chunkData, progress: (i+1)/numChunks };
  }
}

// yields chunks as Promises which return a slice of the blob or file
export function* fileUploadBlobChunkGenerator(data) {
  const size = data.size;
  const numChunks = Math.ceil(size/FILE_UPLOAD_CHUNK_SIZE);

  for(let i = 0; i < numChunks; i++) {
    const start = i === 0;
    const end = i === numChunks-1;
    yield new Promise((resolve, reject) => {
      const reader = new FileReader();
      const blob = data.slice(i*FILE_UPLOAD_CHUNK_SIZE, (i+1)*FILE_UPLOAD_CHUNK_SIZE);
      reader.onload = (e) => {
        if(e.target.error === null) {
          resolve({
            start,
            end,
            data: new Uint8Array(e.target.result),
            progress: (i+1)/numChunks
          });
        } else {
          reject(e.target.error);
        }
      };
      reader.readAsArrayBuffer(blob);
    });
  }
}

export function* performFileUpload(channel, needsPermissionChannel, path, fileType, data) {
  const fileUpload = yield select(selectFileUpload, path);
  let size = 0;
  let dataType = null;
  if(typeof(data) === "string") {
    const textEncoder = new TextEncoder("utf-8");
    data = textEncoder.encode(data);
    size = data.byteLength;
    dataType = "arraybuffer";
  } else if(Object.prototype.toString.call(data) === "[object ArrayBuffer]") {
    size = data.byteLength;
    dataType = "arraybuffer";
  } else if(data instanceof Blob) {
    // otherwise assume a blob/file object
    size = data.size;
    dataType = "blob";
  } else {
    console.error("fileQueue can only upload object of type string, ArrayBuffer, File or Blob");
  }
  if(fileUpload && dataType) {
    // Initiate a file upload, which will return an error if the file exists
    // and we haven't explicitly told it to overwrite
    const initID = "FILE_UPLOAD_INIT_" + path.toUpperCase();
    const responseType = createRockhopperActionType("RESPONSE", "FILE_UPLOAD_INIT", path.toUpperCase());
    yield put(actions.cmds.put({ id: initID, meta: addActionEmitterToMeta({}, responseType), data: [ path, fileType, size, fileUpload.overwrite ], name: "file_upload_init" }));
    const initResponse = yield take(responseType);

    if(initResponse.payload.code === OK_CODE) {
      // if fileUploadInit was successful, we can begin uploading

      // save a buffer for storing in the cache when we're done
      const buffer = new ArrayBuffer(size);
      const dataView = new Uint8Array(buffer);
      let currentIndex = 0;

      let chunkGenerator;
      if(dataType === "arraybuffer") {
        // if the data is an arraybuffer, break it up into Uint8Array chunks
        chunkGenerator = fileUploadArrayBufferChunkGenerator(data);
      } else if(dataType === "blob") {
        // if the data is a blob, we can break it up into promises
        // that return Uint8Array chunks
        chunkGenerator = fileUploadBlobChunkGenerator(data);
      }

      // indicate that we've started uploading
      yield put(actions.fileUpload.progress({ path, fileType, progress: 0 }));

      // spark is an object for incrementally calculating MD5 checksums.
      // The server will also calculate incremental MD5 checksums and will
      // error if they don't match up at the end.
      const spark = new SparkMD5.ArrayBuffer();
      let chunkNumber = 0;
      for(let chunkPromise of chunkGenerator) {
        const chunk = yield Promise.resolve(chunkPromise);
        const {
          data: chunkData,
          progress
        } = chunk;

        const arrayOfStrings = new Array(chunkData.byteLength);
        for(let i = 0; i < chunkData.byteLength; i++) {
          dataView[currentIndex+i] = chunkData[i];
          arrayOfStrings[i] = String.fromCharCode(chunkData[i]);
        }
        currentIndex += chunkData.byteLength;

        const base64EncodedChunkData = btoa(arrayOfStrings.join(""));

        spark.append(chunkData);

        const chunkID = "FILE_UPLOAD_CHUNK_" + chunkNumber + "_" + path.toUpperCase();
        const chunkResponseType = createRockhopperActionType("RESPONSE", "FILE_UPLOAD_CHUNK_" + chunkNumber, path.toUpperCase());
        yield put(actions.cmds.put({ id: chunkID, name: "file_upload_chunk", data: [ path, fileType, base64EncodedChunkData ], meta: addActionEmitterToMeta({}, chunkResponseType) }));
        const response = yield take(chunkResponseType);
        if(response.payload.code === OK_CODE) {
          yield put(actions.fileUpload.progress({ path, fileType, progress }));
        } else {
          console.error("File upload error", response);
          yield put(actions.messages.error(`Failed to upload file: ${path}`, response.payload));
          yield put(actions.fileUpload.cancel({ path, fileType }));
        }

        chunkNumber++;
      }

      const checksum = spark.end();
      fileCache.setPersistentItem(checksum, buffer);

      const finalizeID = "FILE_UPLOAD_FINALIZE_" + path.toUpperCase();
      const finalizeResponseType = createRockhopperActionType("RESPONSE", "FILE_UPLOAD_FINALIZE", path.toUpperCase());
      yield put(actions.cmds.put({ id: finalizeID, meta: addActionEmitterToMeta({}, finalizeResponseType), name: "file_upload_finalize", data: [ path, fileType, checksum ] }));

      const finalizeResponse = yield take(finalizeResponseType);
      if(finalizeResponse.payload.code === OK_CODE) {
        yield put(actions.fileUpload.finalize({ path, fileType }));
      } else {
        yield put(actions.messages.error(`Failed to finalize upload: ${path}`, finalizeResponse.payload));
        yield put(actions.fileUpload.cancel({ path, fileType }));
        console.error("File upload error", finalizeResponse);
      }
    } else if(initResponse.payload.code === FILE_EXISTS_CODE) {
      // If the response is a file exists error, we need to ask for permission to overwrite.
      // Rather than block the worker from progressing on other files, we'll dispatch an action
      // to the redux store to indicate that we need permission for this file and add the file
      // data to the needsPermissionChannel, where it will sit until confirmed.

      // If a confirmOverwriteAll action was performed, then the reducer
      // set overwrite to true, so we need to check the redux store
      // before choosing which channel to put the file data back onto.
      const fileUpload = yield select(selectFileUpload, path);

      if(fileUpload.overwrite) {
        yield put(channel, { path, fileType, data });
      } else {
        yield put(actions.fileUpload.askForOverwrite({ path, fileType }));
        yield put(needsPermissionChannel,  { path, fileType, data });
      }
    } else {
      console.error("fileUploadInit error", initResponse);
      yield put(actions.messages.error(`Failed to initialize file upload: ${path}`, initResponse.payload));
      yield put(actions.fileUpload.cancel({path, fileType}));
    }
  }

  return true; // return true to indicate we've finished first to the race effect
}

export function* monitorFileUploadStatus({path, fileType}) {
  const cancelActionChannel = yield actionChannel([ actions.fileUpload.cancel,
                                                    actions.fileUpload.cancelAll ]);
  while(true) {
    yield take(cancelActionChannel);
    const fileUpload = yield select(selectFileUpload, path);
    if(!fileUpload || fileUpload.state === FILE_UPLOAD_CANCELED) {
      break;
    }
  }
  return true; // return true to indicate we've finished first to the race effect
}

// This is a file upload worker saga that takes events
// with file path and file data off the channel argument.
// It fires off two sagas in parallel, one that monitors
// whether the upload was canceled and the other is the
// upload process. The upload process can finish on successful
// upload, or if an overwrite permission is needed from the user,
// in which case the file data will be pushed between other
// work queues.
export function* fileUploadWorker(channel, needsPermissionChannel) {
  while(true) {
    const { path, fileType, data } = yield take(channel);
    yield race({
      error: call(monitorFileUploadStatus, { path, fileType} ),
      complete: call(performFileUpload, channel, needsPermissionChannel, path, fileType, data)
    });
  }
}

// The fileUploadNeedsPermissionWorker monitors the needsPermissionChannel
// for files that were added to the file upload queue, but need the user
// to verify they should be overwritten. We can't lose track of any File
// objects as they are only available to us if the user selects them, so 
// once we have them we need to hold on to them until they are processed. 
// So, we keep track of any File objects (file data in general,
// as they could be strings, too) as we we encounter them, while also
// waiting for any actions that could push them back to the worker queue
// or cancel them (confirm, confirmAll, cancel and cancelAll).
export function* fileUploadNeedsPermissionWorker(channel, needsPermissionChannel) {
  const waitingForPermission = {};

  const cancelActionChannel = yield actionChannel(actions.fileUpload.cancel);
  const cancelAllActionChannel = yield actionChannel(actions.fileUpload.cancelAll);
  const confirmOverwriteActionChannel = yield actionChannel(actions.fileUpload.confirmOverwrite);
  const confirmOverwriteAllActionChannel = yield actionChannel(actions.fileUpload.confirmOverwriteAll);

  while(true) {
    const { 
      newFileNeedsPermission,
      cancel,
      cancelAll,
      confirm,
      confirmAll } = yield race({ newFileNeedsPermission: take(needsPermissionChannel),
                                  cancel: take(cancelActionChannel),
                                  cancelAll: take(cancelAllActionChannel),
                                  confirm: take(confirmOverwriteActionChannel),
                                  confirmAll: take(confirmOverwriteAllActionChannel)});

    if(confirm) {
      const { path, fileType } = confirm.payload;
      const data = waitingForPermission[path];
      if(data) {
        delete waitingForPermission[path];
        yield put(channel, { path, fileType, data });
      }
    } else if(confirmAll) {
      const paths = Object.keys(waitingForPermission);
      for(let path of paths) {
        const data = waitingForPermission[path];
        delete waitingForPermission[path];
        yield put(channel, { path, data });
      }
    } else if(cancel) {
      const { path } = cancel.payload;
      if(waitingForPermission[path]) {
        delete waitingForPermission[path];
      }
    } else if(cancelAll) {
      const paths = Object.keys(waitingForPermission);
      for(let path of paths) {
        delete waitingForPermission[path];
      }
    } else if(newFileNeedsPermission) {
      const { path, data } = newFileNeedsPermission;
      waitingForPermission[path] = data;
    }
  }
}

export function* fileUploadCancelAllWorker(action) {
  const fileUploads = yield select(selectFileUploads);
  for(let upload in fileUploads) {
    const { path, fileType } = fileUploads[upload];
    yield put(actions.put.fileUploadCancel({ data: [ path, fileType ] }));
  }
  yield put(actions.fileUpload.removeAll());
}
export function* fileUploadCancelWorker(action) {
  const { path, fileType } = action.payload;

  yield put(actions.put.fileUploadCancel({ data: [ path, fileType ] }));
  yield put(actions.fileUpload.remove(path));
}

// This is the main saga that monitors the file queue.
//
// Any time fileQueue.addFile is called, an event
// is appended to fileQueue.channel, which this saga
// monitors and sequentially uploads files to rockhopper.
//
// Doing it this way allows File objects to be passed
// directly to the saga as File objects shouldn't appear
// in actions, as they aren't encodable without reading
// the whole file.
export function* fileUploadQueue() {
  const fileQueueChannel = fileQueue.channel;
  const fileUploadWorkerChannel = yield call(channel);
  const fileUploadNeedsPermissionChannel = yield call(channel);

  const numWorkers = 10;
  for(let i = 0; i < numWorkers; i++) {
    yield fork(fileUploadWorker, fileUploadWorkerChannel, fileUploadNeedsPermissionChannel);
  }
  yield fork(fileUploadNeedsPermissionWorker, fileUploadWorkerChannel, fileUploadNeedsPermissionChannel);
  yield takeEvery(actions.fileUpload.cancel, fileUploadCancelWorker);
  yield takeLatest(actions.fileUpload.cancelAll, fileUploadCancelAllWorker);

  while(true) {
    // Monitor for files queued via the FileQueue singleton
    const { path, fileType, data, overwrite } = yield take(fileQueueChannel);

    const isLoggedIn = yield select(selectIsLoggedIn);
    if(isLoggedIn) {
      const fileUploads = yield select(selectFileUploads);

      // The first time fileQueue.addFile is called for a specific file
      // it won't exists in the state tree as we bipassed the redux store
      // in order to allow File objects to be passed along.
      if(!fileUploads[path]) {
        // dispatch an action to the redux store so UI knows
        // about file upload
        yield put(actions.fileUpload.queued({ path, fileType, overwrite }));

        // Put it on the worker channel, so a worker
        // can perform the upload. 
        yield put(fileUploadWorkerChannel, { path, fileType, data });
      }
    } else {
      yield flush(fileUploadWorkerChannel);
      yield flush(fileUploadNeedsPermissionChannel);
    }
  }
}

export function* handleDownloadCurrentProgram(action) {
  const path = action.payload.data;
  var ftype;
  if(path.startsWith('/media/')){
    ftype = FILE_TYPES.USB_PROGRAM;
  }
  else ftype = FILE_TYPES.PROGRAM
  if(path) {
    yield downloadQueue.addDownload(path, ftype)
  }
}

export function* downloadCurrentProgram() {
  yield takeLatest(actions.update.program, handleDownloadCurrentProgram);
}

 /*
 This body of this method started with a copy-paste from handleDownloadCurrentProgram, and was modified from there. Any errors or oddities are probably from
 me misunderstanding something. - Jake
 */
export function* performFileDownload(path, fileType, callback) {
  const fileDownload = yield select(selectFileDownload, path);
  if(fileDownload) {
    // Initiate a file upload, which will return an error if the file exists
    // and we haven't explicitly told it to overwrite
    const initID = "FILE_DOWNLOAD_INIT_" + path.toUpperCase();
    const responseType = createRockhopperActionType("RESPONSE", "FILE_DOWNLOAD_INIT", path.toUpperCase());
    yield put(actions.cmds.put({ id: initID, meta: addActionEmitterToMeta({}, responseType), data: [ path, fileType ], name: "file_download_init" }));
    const initResponse = yield take(responseType);

    if(initResponse.payload.code === OK_CODE) {
      const {
        size,
        checksum
      } = initResponse.payload.data;

      yield put(actions.fileCache.setPathChecksum(path, checksum));
      yield put(actions.fileCache.initProgress(checksum, size));
      const cachedData = yield call([fileCache, fileCache.getItem], checksum);
      if(cachedData) {
        yield put(actions.fileCache.updateProgress(checksum, size));
        yield put(actions.put.fileDownloadCancel({ data: [ path, fileType ] }));
      } else {
        const buffer = new ArrayBuffer(size);      // CAREFUL! This is intentially a mutable object.
        fileCache.setMemoryItem(checksum, buffer); // Components can get access to this object (though
                                                   // they shouldn't mutate it) by using the fileCache 
                                                   // singleton and the desired checksum. They can do 
                                                   // this in order to show portions of the file as it 
                                                   // is being downloaded. Containers can select the 
                                                   // checksum and download progress data from the 
                                                   // redux store to know how much of the data can be
                                                   // shown.

        const fileBuffer = new Uint8Array(buffer);
        let maxIndex = 0;

        const numChunks = Math.ceil(size/FILE_DOWNLOAD_CHUNK_SIZE);
        for(let i = 0; i < numChunks; i++) {
          const chunkID = "FILE_DOWNLOAD_CHUNK_" + i + "_" + path.toUpperCase();
          const chunkResponseType = createRockhopperActionType("RESPONSE", "FILE_DOWNLOAD_CHUNK_" + i, path.toUpperCase());
          yield put(actions.cmds.put({ id: chunkID, name: "file_download_chunk", data: [ path, fileType, FILE_DOWNLOAD_CHUNK_SIZE ], meta: addActionEmitterToMeta({}, chunkResponseType) }));
          const response = yield take(chunkResponseType);
          if(response.payload.code === OK_CODE) {
            const decodedString = atob(response.payload.data.chunk);
            for(let i = 0; i < decodedString.length; i++) {
              fileBuffer[i+maxIndex] = decodedString.charCodeAt(i);
            }
            maxIndex += decodedString.length;
            yield put(actions.fileCache.updateProgress(checksum, maxIndex));
          } else {
            console.error("File download error", response);
            yield put(actions.messages.error(`Failed to download file: ${path}`, response.payload));
            yield put(actions.put.fileDownloadCancel({ data: [ path, fileType ] }));
            break;
          }
        }

        yield put(actions.fileCache.updateProgress(checksum, size));
        fileCache.setPersistentItem(checksum, buffer);
      }
      if( callback ){
        callback(fileCache, checksum, path)
      }
    } 
    else {
      console.error("fileDownloadInit error", initResponse);
      yield put(actions.messages.error(`Failed to initialize file download: ${path}`, initResponse.payload));
      yield put(actions.fileDownload.cancel({path, fileType}));
    }
  }

  return true; // return true to indicate we've finished first to the race effect 
}

export function* monitorFileDownloadStatus({path, fileType}) {
  const cancelActionChannel = yield actionChannel([ actions.fileDownload.cancel,
                                                    actions.fileDownload.cancelAll ]);
  while(true) {
    yield take(cancelActionChannel);
    const fileDownload = yield select(selectFileDownload, path);
    if(!fileDownload || fileDownload.state === FILE_UPLOAD_CANCELED) {
      break;
    }
  }
  return true; // return true to indicate we've finished first to the race effect
}

export function* fileDownloadWorker(channel) {
  while(true) {
    const { path, fileType, callback } = yield take(channel);
    yield race({
      error: call(monitorFileDownloadStatus, { path, fileType} ),
      complete: call(performFileDownload, path, fileType, callback)
    });
  }
}

export function* fileDownloadCancelAllWorker(action) {
  const fileDownloads = yield select(selectFileDownloads);
  for(let download in fileDownloads) {
    const { path, fileType } = fileDownloads[download];
    yield put(actions.put.fileDownloadCancel({ data: [ path, fileType ] }));
  }
  yield put(actions.fileDownload.removeAll());
}
export function* fileDownloadCancelWorker(action) {
  const { path, fileType } = action.payload;

  yield put(actions.put.fileDownloadCancel({ data: [ path, fileType ] }));
  yield put(actions.fileDownload.remove(path));
}


// This is the main saga that monitors the file download queue.
//
// Any time downloadQueue.addDownload is called, an event
// is appended to downloadQueue.channel, which this saga
// monitors and sequentially downloads files from rockhopper.
export function* fileDownloadQueue() {
  const downloadQueueChannel = downloadQueue.channel;
  const fileDownloadWorkerChannel = yield call(channel);

  const numWorkers = 10;
  for(let i = 0; i < numWorkers; i++) {
    yield fork(fileDownloadWorker, fileDownloadWorkerChannel);
  }
  yield takeEvery(actions.fileDownload.cancel, fileDownloadCancelWorker);
  yield takeLatest(actions.fileDownload.cancelAll, fileDownloadCancelAllWorker);

  while(true) {
    // Monitor for files queued via the DownloadQueue singleton
    const { path, fileType, callback } = yield take(downloadQueueChannel);

    const isLoggedIn = yield select(selectIsLoggedIn);
    if(isLoggedIn) {
      const fileDownloads = yield select(selectFileDownloads);

      // The first time downloadQueue.addDownload is called for a specific file
      // it won't exists in the state tree as we bypassed the redux store
      // in order to allow File objects to be passed along.
      if(true || !fileDownloads[path]) {
        // dispatch an action to the redux store so UI knows
        // about file download
        yield put(actions.fileDownload.queued({ path, fileType, callback }));

        // Put it on the worker channel, so a worker
        // can perform the download. 
        yield put(fileDownloadWorkerChannel, { path, fileType, callback });
      }
    } else {
      yield flush(fileDownloadWorkerChannel);
    }
  }
}

export function* delayAction(time, action) {
  yield delay(time);
  yield put(action);
}

export function* delayActionWithChannel(time, channel) {
  yield delay(time);
  const action = yield take(channel);
  yield put(action);
}

export function* delayedPuts() {
  const putChannel = yield actionChannel(Object.values(actions.delayed.put));
  const timerTasks = {};
  const timerChannels = {};
  while(true) {
    const putAction = yield take(putChannel);

    const {
      type,
      payload
    } = putAction;

    const typePath = type.split("/");
    const typeSuffix = typePath[typePath.length-1];
    const friendlyName = createFriendlyName(typeSuffix);

    if(!(type in timerChannels)) {
      timerChannels[type] = yield call(channel, buffers.sliding(1));
    }
    const {
      id,
      name,
      meta,
      ...data
    } = payload;
    yield put(timerChannels[type], actions.put[friendlyName]({ data, meta }));
    if(!(type in timerTasks) || !timerTasks[type].isRunning()) {
      timerTasks[type] = yield fork(delayActionWithChannel, 50, timerChannels[type]);
    }
  }
}

export function* delayedUpdates() {
  const updateChannel = yield actionChannel(Object.values(actions.update).concat( Object.values(actions.delayed.update)));
  const timerTasks = {};
  const delayedUpdateTypePrefix = createRockhopperActionType("DELAYED", "UPDATE");
  while(true) {
    const updateAction = yield take(updateChannel);
    const registered = yield select(selectRegisteredDelayedUpdates);

    const {
      type,
      payload
    } = updateAction;

    const typePath = type.split("/");
    const typeSuffix = typePath[typePath.length-1];
    const friendlyName = createFriendlyName(typeSuffix);

    if(friendlyName in registered) {
      if(friendlyName in timerTasks && timerTasks[friendlyName].isRunning()) {
        yield cancel(timerTasks[friendlyName]);
        timerTasks[friendlyName] = yield fork(delayAction, registered[friendlyName], actions.delayed.updateImmediate[friendlyName](payload));
      } else {
        if(type.startsWith(delayedUpdateTypePrefix)) {
          timerTasks[friendlyName] = yield fork(delayAction, registered[friendlyName], actions.delayed.updateImmediate[friendlyName](payload));
        } else {
          yield put(actions.delayed.updateImmediate[friendlyName](payload))
        }
      }
    }
  }
}
