import React from 'react';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/styles';

import SubmittableTextField from './SubmittableTextField';
import SubmittableDialog from './SubmittableDialog';


const useStyles = makeStyles((theme) => ({
  container: {
    height: 40,
  },
  openDialogButton: {
    border: "none",
    height: "100%",
    float: "right",
    textAlign: "center",
    padding: 0,
    borderTopLeftRadius: 0,
    borderBottomLeftRadius: 0,
    minWidth: 32,
    paddingLeft: 4,
    paddingRight: 2,
  },
  textField: {
    display: "flex",
    height: "100%",
  },
}));

const ModalSubmittableTextField = (props) => {
  const {
    value,
    modalValue,
    onSubmitTextField,
    onSubmitModal,
    disabled,
    buttonDom,
    buttonProps,
    containerProps,
    modalProps,
    textFieldProps
  } = props;

  const classes = useStyles(props);

  const [modalOpen, setModalOpen] = React.useState(false);

  const handleCloseModal = () => {
    setModalOpen(false);
  };

  const handleSubmitModal = (value) => {
    if(onSubmitModal){
      onSubmitModal(value);
    };
    setModalOpen(false);
  };

  const handleSubmitTextField = (e) => {
    if(onSubmitTextField){
      onSubmitTextField(e.target.value);
    };
  };

  return (
    <>
    <SubmittableDialog
      open={modalOpen}
      value={modalValue}
      onSubmit={handleSubmitModal}
      onClose={handleCloseModal}
      {...modalProps}
    />
    <Box
      className={classes.container}
      {...containerProps}
    >
      <Button
          className={classes.openDialogButton}
          {...buttonProps}
          disabled={disabled}
          onClick={() => {
            if(!disabled){
              setModalOpen(true);
            }
          }}
        >
        {buttonDom}
        </Button>
        <SubmittableTextField
          className={classes.textField}
          disabled={disabled}
          value={value}
          onSubmit={handleSubmitTextField}
          {...textFieldProps}
        />
      </Box>
    </>
  )
};
export default ModalSubmittableTextField;
