import React from 'react';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';

import { makeStyles } from '@material-ui/styles';

import SubmittableTextField from './SubmittableTextField';
import { DialogActions, DialogContent } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1)
  } 
}));

const SubmittableDialog = (props) => {
  const {
    open,
    value,
    onSubmit,
    onClose,
    title,
    textFieldLabel,
    submitBtnLabel,
    cancelBtnLabel,
  } = props;

  const classes = useStyles(props);

  const handleClose = () => {
    if(onClose) {
      onClose()
    }
  };

  const handleSubmit = (e) => {
    if(onSubmit) {
      e.preventDefault();
      const textField = textFieldRef.current;
      const text =  textField["text"].value;
      onSubmit(text);
    }
  };

  const textFieldRef = React.useRef(null);

  return (
    <>
    <Dialog open={open} onClose={handleClose}>
      <DialogTitle >{title}</DialogTitle>
      <DialogContent>
        <Box>
          <form ref={textFieldRef} autoComplete="off" onSubmit={(e) => e.preventDefault()}>
            <SubmittableTextField
              {...props.textFieldProps}
              defaultValue={value}
              onSubmit={(val,e) => { handleSubmit(val,e) }}
              label={textFieldLabel}
              variant="outlined"
              name={"text"}
            />
          </form>
        </Box>
      </DialogContent>
      <DialogActions>
        <Box>
          <Button
            onClick={(e) => { handleSubmit(e) } }
            variant="contained" 
            color="primary"
            className={classes.button}
          >
            {submitBtnLabel}
          </Button>
          <Button 
            onClick={handleClose}
            variant="contained" 
            color="primary"
            className={classes.button}
          >
            {cancelBtnLabel}
          </Button>
        </Box>
      </DialogActions>
    </Dialog>
    </>
  )
};
export default SubmittableDialog;
