import { connect } from 'react-redux';
import WorkOffsets from '../components/WorkOffsets';
import actions from '../modules/rockhopper/actions';
import { toMachineUnits, toProgramUnits }  from '../hooks/useUnits';

import { 
  selectG5xIndex,
  selectToolInSpindle,
  selectToolTable,
  selectReadyForMdi,
  selectWorkOffsets,
  selectUnitsCode,
} from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => ({
    g5xIndex: selectG5xIndex(state),
    workOffsets: selectWorkOffsets(state),
    toolInSpindle: selectToolInSpindle(state),
    toolTable: selectToolTable(state),
    readyForMdi: selectReadyForMdi(state),
    unitsCode: selectUnitsCode(state),
}), 
(dispatch, ownProps) => ({
  
  onDispatchMdiCmd: (cmd) => {
    dispatch(actions.put.mdiCmd({ data: [cmd] }));
  },
  onSetG5xIndex: (cmd) => {
    dispatch(actions.put.mdiCmd({ data: [cmd] }));
  },
  onClearG92: () => {
    dispatch(actions.put.mdiCmd({ data: ["G92.1"] }))
  },
  onDisableG92: () => {
    dispatch(actions.put.mdiCmd({ data: ["G92.2"] }))
  },
  onRestoreG92: () => {
    dispatch(actions.put.mdiCmd({ data: ["G92.3"] }))
  },
  onDispatchUpdateHelper: (updateHelper, data) => {
    dispatch( actions.updateHelpers[updateHelper](data) )
  },

}),
(stateProps, dispatchProps, ownProps) => {

  const {
    workOffsets,
    g5xIndex,
    toolInSpindle,
    unitsCode,
  } = stateProps;
  
  const {
    onDispatchMdiCmd,
    onDispatchUpdateHelper,
  } = dispatchProps;


  const onSetG5xOffset = (newOffset, axisData) => {
    const offsetProgramUnits = toProgramUnits(newOffset, unitsCode);
    const cmd = `G10 L2 P${g5xIndex}${axisData.axis}${offsetProgramUnits.value.toFixed(5)}`;
    onDispatchMdiCmd(cmd);
  };
  const onSetDroViaG5x = (newDro, axisData) => {
    const droProgramUnits = toProgramUnits(newDro, unitsCode);
    const cmd = `G10 L20 P${g5xIndex}${axisData.axis}${droProgramUnits.value.toFixed(5)}`
    onDispatchMdiCmd(cmd);
  };
  const onSetG5xOffsetAll = (newOffsets) => {
    const offsetsProgramUnits = newOffsets.map((offset) => toProgramUnits(offset,unitsCode));
    let cmd = `G10 L2 P${g5xIndex}`;  
    workOffsets.map((axisData,index) => cmd += axisData.axis + String(offsetsProgramUnits[index].value.toFixed(5)));
    onDispatchMdiCmd(cmd);
  };
  const onSetDroAllViaG5x = (newDro) => {
    const droProgramUnits = newDro.map((dro) => toProgramUnits(dro,unitsCode));
    let cmd = `G10 L20 P${g5xIndex} `;
    workOffsets.map((axisData,index) => cmd += axisData.axis + String(droProgramUnits[index].value.toFixed(5)));
    onDispatchMdiCmd(cmd);
  };
  const onSetG92Offset = (newOffset, axisData) => {
    const offsetMachineUnits = toMachineUnits(newOffset,unitsCode);
    const cmdValueMachineUnits = Number( (axisData.position.value + axisData.g92Offset.value - offsetMachineUnits.value) );
    const offsetProgramUnits = toProgramUnits({value: cmdValueMachineUnits, unit: offsetMachineUnits.unit},unitsCode);
    const cmd = `G92 ${axisData.axis}${offsetProgramUnits.value.toFixed(5)}`;
    onDispatchMdiCmd(cmd);
  };
  const onSetDroViaG92 = (newDro, axisData) => {
    const droProgramUnits = toProgramUnits(newDro, unitsCode);
    const cmd = `G92 ${axisData.axis}${droProgramUnits.value.toFixed(5)}`;
    onDispatchMdiCmd(cmd);
  };
  const onSetDroAllViaG92 = (newDro) => {
    const droProgramUnits = newDro.map((dro) => toProgramUnits(dro,unitsCode));
    let cmd = 'G92 ';
    workOffsets.map((axisData,index) => cmd += axisData.axis + String(droProgramUnits[index].value.toFixed(5)));
    onDispatchMdiCmd(cmd);
  };
  const onSetTlo = (newTlo) => {
    const newTloMachineUnits = toMachineUnits(newTlo, unitsCode);
    onDispatchUpdateHelper('toolLengthOffset', { data: [toolInSpindle, newTloMachineUnits.value] });
    const newTloProgramUnits = toProgramUnits(newTlo, unitsCode);
    const cmd = `G10 L1 P${toolInSpindle} Z${newTloProgramUnits.value.toFixed(5)}`;
    onDispatchMdiCmd(cmd);
    onDispatchMdiCmd('G43');
  };
  const onSetDroViaTlo = (newDro,axisData) => {
    const newDroMachineUnits = toMachineUnits(newDro, unitsCode);
    const newTloMachineUnits = {
      value: axisData.actualPosition.value - axisData.g5xOffset.value - axisData.g92Offset.value - newDroMachineUnits.value,
      unit: newDroMachineUnits.unit
    }
    onDispatchUpdateHelper('toolLengthOffset', { data: [toolInSpindle, newTloMachineUnits.value] });
    const newDroProgramUnits = toProgramUnits(newDro, unitsCode);
    const cmd = `G10 L10 P${toolInSpindle} Z${newDroProgramUnits.value.toFixed(5)}`
    onDispatchMdiCmd(cmd);
    onDispatchMdiCmd('G43');
  };
  return { 
    onSetG5xOffset,
    onSetDroViaG5x,
    onSetG5xOffsetAll,
    onSetDroAllViaG5x,
    onSetG92Offset,
    onSetDroViaG92,
    onSetDroAllViaG92,
    onSetTlo,
    onSetDroViaTlo,
    ...stateProps,
    ...dispatchProps,
    ...ownProps,
  }
})(WorkOffsets);
