import React from 'react';
import { connect } from 'react-redux';
import TopBar from '../components/TopBar';
import rockhopperActions from '../modules/rockhopper/actions';
import { selectEstop, selectIsMachineHomed } from '../modules/rockhopper/selectors';
import WarningIcon from '@material-ui/icons/Warning';
import ErrorIcon from '@material-ui/icons/Error';

export default connect( 
(state, ownProps) => ({
  isInEstop: selectEstop(state),
  isHomed: selectIsMachineHomed(state)
}), 
(dispatch, ownProps) => {
  return ({ 
  onHome: () => dispatch(rockhopperActions.put.homeCmd()),
  onAbort: () => dispatch(rockhopperActions.put.abortCmd()),
  onDisableEstop: () => dispatch(rockhopperActions.put.estopCmd({ data: [ false ]}))
}) },
(stateProps, dispatchProps, ownProps) => {
  return ({
    leftLabelKey: "homeAll",
    rightLabelKey: stateProps.isInEstop ? "estop" : "reset",
    leftColor: "primary",
    rightColor: stateProps.isInEstop ? "error" : "secondary",
    onLeftButtonClick: dispatchProps.onHome,
    onRightButtonClick: stateProps.isInEstop ? dispatchProps.onDisableEstop : dispatchProps.onAbort,
    leftIcon: stateProps.isHomed ? null : <WarningIcon fontSize="large"/>,
    rightIcon: stateProps.isInEstop ? <ErrorIcon fontSize="large"/> : null
  })
})(TopBar);
