import React, { useState } from 'react';
import { arcDetails } from './util/math';
import { moveTo, arcTo } from './util/path';
//import { useTheme } from '@material-ui/core/styles';
import { v4 as uuidv4 } from 'uuid';
import classnames from 'classnames';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  gaugeHeader: {
    ...theme.typography.gaugeHeader
  }
}));

const ArcHeader = (props) => {
  const {
    x1,
    y1,
    x2,
    y2,
    x3,
    y3,
    text,
    paddingStart = 0,
    paddingEnd = 0,
    dy = 0,
    className
  } = props;

  const [ pathId ] = useState(uuidv4());

  //const theme = useTheme();

  const missingPoints = (typeof(x1) === "undefined" ||
                         typeof(y1) === "undefined" ||
                         typeof(x2) === "undefined" ||
                         typeof(y2) === "undefined" ||
                         typeof(x3) === "undefined" ||
                         typeof(y3) === "undefined");
  const {
    c,
    r, 
    startAngle,
    endAngle
  } = missingPoints ? props : arcDetails(x1,y1,x2,y2,x3,y3);

  const classes = useStyles();

  const angle = endAngle-startAngle;

  const paddingStartAngle = angle > 0 ? paddingStart : -paddingStart;
  const paddingEndAngle = angle > 0 ? paddingEnd : -paddingEnd;

  const a = angle-paddingStartAngle-paddingEndAngle;

  const startX = Math.cos(startAngle+paddingStartAngle);
  const startY = Math.sin(startAngle+paddingStartAngle);
  const endX = Math.cos(endAngle-paddingEndAngle);
  const endY = Math.sin(endAngle-paddingEndAngle);

  const largeArc = a > Math.PI ? 1 : 0;
  const sweepFlag = a > 0 ? 1 : 0;
  const arcPathInstructions = [ moveTo(r*startX, r*startY),
                                arcTo(r, r, 0, largeArc, sweepFlag, r*endX, r*endY)
                                ];

  return (
  <g>
    <defs>
      <path id={pathId} d={arcPathInstructions.join(" ")} transform={"translate(" + c.x + "," + c.y + ")"} fill="none" stroke="black" strokeWidth=".1"/>
    </defs>
    <text dy={dy} className={classnames(classes.gaugeHeader, className)} textAnchor="middle" >
      <textPath href={"#" + pathId} startOffset="50%">
      {text}
      </textPath>
    </text>
  </g>
  );
};
export default ArcHeader;
