import React from 'react';
import produce from 'immer';
import { useTranslation } from 'react-i18next';

import { makeStyles } from '@material-ui/styles';
import Panel from './Panel';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';
import Select from '@material-ui/core/Select';
import MenuItem from '@material-ui/core/MenuItem';

//import {UNITS} from '../modules/rockhopper/constants';

const useStyles = makeStyles((theme) => ({
  formControl: {
    marginRight: theme.spacing(1)
  }
}));

const Units = (props) => {
  const {
    displayUnits,
    onSetDisplayUnits,
  } = props;

  const { t } = useTranslation();
  const classes = useStyles();
  const handleChange = (e, unitType) => {
    const newDisplayUnits = produce(displayUnits, (draft) => {
      draft[unitType] = e.target.value
    })
    onSetDisplayUnits(newDisplayUnits);
  };
  const handleChangeLengthUnit = (e) => {
    const newDisplayUnits = produce(displayUnits, (draft) => {
      draft.length = e.target.value
      draft.linearVelocity = e.target.value + "/" + displayUnits.time;
    })
    onSetDisplayUnits(newDisplayUnits);
  };
  const handleChangeTimeUnit = (e) => {
    const newLinearVelocityUnits = displayUnits.length + "/" + e.target.value;
    const newAngularVelocityUnits = displayUnits.angle + "/" + e.target.value;
    const newDisplayUnits = produce(displayUnits, (draft) => {
      draft.time = e.target.value; 
      draft.linearVelocity = newLinearVelocityUnits;
      draft.angularVelocity = newAngularVelocityUnits;
    })
    onSetDisplayUnits(newDisplayUnits);
  };
  const handleChangeSurfaceSpeedUnit = (e) => {
    const newDisplayUnits = produce(displayUnits, (draft) => {
      draft.surfaceSpeed = e.target.value
    })
    onSetDisplayUnits(newDisplayUnits);
  };

  return <Panel label={t("units")}>
    <FormControl className={classes.formControl}>
      <InputLabel id="length-label">{t("lengthUnits")}</InputLabel>
      <Select
        labelId="length-label"
        id="length-select"
        value={displayUnits.length}
        onChange={handleChangeLengthUnit}>
        <MenuItem value={"in"}>{("in")}</MenuItem>
        <MenuItem value={"mm"}>{("mm")}</MenuItem>
        <MenuItem value={"pu"}>{t("programUnits")}</MenuItem>
      </Select>
    </FormControl>
    <FormControl className={classes.formControl}>
      <InputLabel id="time-label">{t("time")}</InputLabel>
      <Select
        labelId="time-label"
        id="time-select"
        value={displayUnits.time}
        onChange={handleChangeTimeUnit}>
        <MenuItem value="s">{("s")}</MenuItem>
        <MenuItem value={"min"}>{("min")}</MenuItem>
      </Select>
    </FormControl>
    <FormControl className={classes.formControl} style={{minWidth: 100}}>
      <InputLabel id="surfaceSpeed-label">{t("surfaceSpeed")}</InputLabel>
      <Select
        labelId="surfaceSpeed-label"
        id="surfaceSpeed-select"
        value={displayUnits.surfaceSpeed}
        onChange={handleChangeSurfaceSpeedUnit}>
        <MenuItem value={"ft/min"}>{("ft/min")}</MenuItem>
        <MenuItem value={"in/min"}>{("in/min")}</MenuItem>
        <MenuItem value={"m/min"}>{("m/min")}</MenuItem>
        <MenuItem value={"mm/min"}>{("mm/min")}</MenuItem>
      </Select>
    </FormControl>
    <FormControl className={classes.formControl}>
      <InputLabel id="pressure-label">{t("pressure")}</InputLabel>
      <Select
        labelId="pressure-label"
        id="pressure-select"
        value={displayUnits.pressure}
        onChange={(e) => { handleChange(e, 'pressure')}}>
        <MenuItem value="psi">{t("psi")}</MenuItem>
        <MenuItem value={"MPa"}>{t("MPa")}</MenuItem>
      </Select>
    </FormControl>
    <FormControl className={classes.formControl}>
      <InputLabel id="temperature-label">{t("temperature")}</InputLabel>
      <Select
        labelId="temperature-label"
        id="temperature-select"
        value={displayUnits.temperature}
        onChange={(e) => { handleChange(e, 'temperature')}}>
        <MenuItem value="F">{"F"}</MenuItem>
        <MenuItem value="C">{"C"}</MenuItem>
      </Select>
    </FormControl>
  </Panel>;
};
export default Units;
