import { connect } from 'react-redux';
import GCodeEditor from '../components/GCodeEditor';
import { selectCurrentLine, selectCurrentFileProgress, selectProgram, selectCurrentFileChecksum } from '../modules/rockhopper/selectors';
import { FILE_TYPES } from '../modules/rockhopper/constants';
import downloadQueue from '../modules/rockhopper/download-queue';
import { downloadToDisk } from '../modules/rockhopper/file-cache';

export default connect( 
(state, ownProps) => ({
  file: selectProgram(state),
  checksum: selectCurrentFileChecksum(state),
  progress: selectCurrentFileProgress(state),
  line: selectCurrentLine(state)
}), 
(dispatch, ownProps) => ({ 
  onDownloadProgram: (path) => {
    downloadQueue.addDownload(path, FILE_TYPES.PROGRAM, downloadToDisk)
  }
}))(GCodeEditor);
