import React from 'react';

import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Button from '@material-ui/core/Button'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import MenuOpenIcon from '@material-ui/icons/MenuOpen';

import Grid from '@material-ui/core/Grid'
import Hidden from '@material-ui/core/Hidden';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import { makeStyles } from '@material-ui/styles';

import useTranslation from '../hooks/useTranslation';
import useUnits from '../hooks/useUnits';

import ModalSubmittableTextField from './ModalSubmittableTextField';
import Panel from './Panel';

import { UNITS } from '../modules/rockhopper/constants';


const useStyles = makeStyles((theme) => ({
  headerRow: {
    [theme.breakpoints.only('xs')] : {
      fontSize: '1.0rem',
    },
    [theme.breakpoints.up('sm')] : {
      fontSize: '.8rem',
    },
    [theme.breakpoints.up('md')] : {
      fontSize: '1.0rem',
    },
  },
  container: {
    minWidth: '21rem', //336px at 16px=1rem
    textAlign: "center",

    fontSize: '1.0rem',
    fontWeight: 'bold',
    // [theme.breakpoints.only('xs')] : {
    //   fontSize: '1.2rem',
    // },
    // [theme.breakpoints.up('sm')] : {
    //   fontSize: '1.3rem',
    // },
    // [theme.breakpoints.up('md')] : {
    //   fontSize: '1.1rem',
    // },
    // [theme.breakpoints.up('lg')] : {
    //   fontSize: '1.2rem',
    // },
    // [theme.breakpoints.up('xl')] : {
    //   fontSize: '1.3rem',
    // },
  },
  menuOpenButton: {
    borderWidth: 2,
  },
  offsetItemContainer: {
    borderColor: theme.palette.primary.main,
    borderRadius: 5,
    borderStyle: 'solid',
    borderWidth: 2,
    height: 38
  },
  offsetItemDropDown: {
    color: theme.palette.primary.main
  },
  offsetItemTextField: {
    ...theme.typography.dataText,
    ...theme.typography.tableCell,
    verticalAlign: 'middle',
    whiteSpace: "nowrap",
  },
  offsetItemTextFieldContainer: {
    display: 'flex', 
    width: '70%',
    [theme.breakpoints.down('sm')] : {
      width: '60%',
    },
  },
  offsetItemButton: {
    borderColor: theme.palette.primary.main,
    borderTopLeftRadius: 0,
    borderBottomLeftRadius: 0,
    border: "none",
    borderWidth: 2,
    minHeight: '100%',
    
    [theme.breakpoints.only('sm')] : {
      fontSize: '1.0rem',
    },
    [theme.breakpoints.up('md')] : {
      minWidth: 45,
      fontSize: '1.0rem',
    },
    [theme.breakpoints.up('lg')] : {
      fontSize: '1.2rem',
    },
    [theme.breakpoints.up('lg')] : {
      fontSize: '1.4rem',
    }
  },
  offsetItemButtonContainer : {
    display: 'inline-block', 
    height: '100%',
    width: '30%', 
    [theme.breakpoints.down('sm')] : {
      width: '40%',
    },
  },
  overrideTextFieldInputInput: {
    ...theme.typography.dataText,
  },
  overrideTextFieldInputRoot: {
    borderTopRightRadius: 0,
    borderBottomRightRadius: 0,
    paddingLeft: 0,
    paddingRight: 0
  },
  root: {
    height: "100%",
    width: "100%",
  },
  text: {
    ...theme.typography.tableCell,
  },
  spacer: {
    color: theme.palette.primary.main,
    width: "1rem"
  },
}));

const g5xSystems = [
  'G54', 'G55', 'G56', 'G57', 'G58', 'G59', 'G59.1', 'G59.2', 'G59.3'
];

const g5xLabel = (index) => {
  if(index > 0) {
    return g5xSystems[index-1];
  }

  return "G5x";
};

const WorkOffsets = (props) => {
  const {
    g5xIndex,
    workOffsets,
    toolInSpindle,
    toolTable,
    readyForMdi,
    
    onClearG92,
    onDisableG92,
    onRestoreG92,

    onSetG5xIndex,

    onSetG5xOffset,
    onSetG5xOffsetAll,
    onSetDroViaG5x,
    onSetDroAllViaG5x,
    onSetG92Offset,
    onSetDroViaG92,
    onSetDroAllViaG92,
    onSetTlo,
    onSetDroViaTlo,
  } = props;

  const { t } = useTranslation();
  const { toDisplayUnits } = useUnits();
  const classes = useStyles();

  const [g5xMenuAnchor, setG5xMenuAnchor] = React.useState(null);
  const [g92MenuAnchor, setG92MenuAnchor] = React.useState(null);
  const [mobileMenuAnchor, setMobileMenuAnchor] = React.useState(null);

  //G5x related handlers
  const handleSetG5xIndex = (event) => {
    const num = parseInt(event.target.value);
    if(onSetG5xIndex && ! isNaN(num)){
      onSetG5xIndex( g5xSystems[num] );
    }
    setG5xMenuAnchor(null);
  };
  const handleSetG5xOffset = (value, axisData) => {
    const num = parseFloat(value);
    if (onSetG5xOffset && ! isNaN(num)){
      onSetG5xOffset({value: num , unit: toDisplayUnits(axisData.g5xOffset).unit }, axisData);
    }
  };
  const handleSetDroViaG5x = (value, axisData) => {
    const num = parseFloat(value);
    if(onSetDroViaG5x && ! isNaN(num)){
      onSetDroViaG5x({value: num, unit: toDisplayUnits(axisData.g5xOffset).unit}, axisData)
    }
  };
  const handleSetG5xOffsetAll = (value) => {
    const num = parseFloat(value);
    if(onSetG5xOffsetAll && ! isNaN(num)){
      const newOffsetsDisplayUnits = workOffsets.map((offset) => ({value: num, unit: toDisplayUnits(offset.g5xOffset).unit}) );
      onSetG5xOffsetAll(newOffsetsDisplayUnits);
    }
  };
  const handleSetDroAllViaG5x = (value) => {
    const num = parseFloat(value);
    if(onSetDroAllViaG5x && ! isNaN(num)){
      const newDroDisplayUnits = workOffsets.map((offset) => ({value: num, unit: toDisplayUnits(offset.g5xOffset).unit}) );
      onSetDroAllViaG5x(newDroDisplayUnits);
    }
  };
  
  //G92 related handlers
  const handleClearG92 = (event) => {
    if(onClearG92){
      onClearG92()
    };
    setG92MenuAnchor(null);
  };
  const handleDisableG92 = (event) => {
    if(onDisableG92){
      onDisableG92()
    };
    setG92MenuAnchor(null);
  };
  const handleRestoreG92 = (event) => {
    if(onRestoreG92){
      onRestoreG92()
    }
    setG92MenuAnchor(null);
  };
  const handleSetG92Offset = (value, axisData) => {
    const num = parseFloat(value);
    if(onSetG92Offset && ! isNaN(num)){
      onSetG92Offset({value: num, unit: toDisplayUnits(axisData.g92Offset).unit }, axisData);
    }
  };
  const handleSetDroViaG92 = (value, axisData) => {
    const num = parseFloat(value);
    if(onSetDroViaG92 && ! isNaN(num)){
      onSetDroViaG92({value: num, unit: toDisplayUnits(axisData.g92Offset).unit }, axisData);
    }
  };
  const handleSetDroAllViaG92 = (value) => {
    const num = parseFloat(value);
    if(onSetDroAllViaG92 && ! isNaN(num)){
      const newDroDisplayUnits = workOffsets.map((offset) => toDisplayUnits({value: num, unit: offset.g92Offset.unit}));
      onSetDroAllViaG92(newDroDisplayUnits);
    }
  };
  //Tool length related handlers
  const handleSetTlo = (value) => {
    const num = parseFloat(value);
    if(onSetTlo && ! isNaN(num)){
      onSetTlo( {value: num, unit: toDisplayUnits(toolTable[toolInSpindle][2]).unit} );
    }
  }
  const handleSetDroViaTlo = (value, axisData) => {
    const num = parseFloat(value);
    if(onSetDroViaTlo && ! isNaN(num)){
      onSetDroViaTlo( {value: num, unit: toDisplayUnits(toolTable[toolInSpindle][2]).unit}, axisData );
    }
  };

  //Menu handlers
  const openG5xMenu = (event) => {
    setG5xMenuAnchor(event.currentTarget);
    setMobileMenuAnchor(null);
  };
  const openG92Menu = (event) => {
    setG92MenuAnchor(event.currentTarget);
    setMobileMenuAnchor(null);
  };
  const openMobileMenu = (event) => {
    setMobileMenuAnchor(event.currentTarget);
    setG92MenuAnchor(null);
    setG5xMenuAnchor(null);
  }

  const anchorRef = React.useRef(null);  

  return <Panel label={t("workOffsets")}>
    <Menu
      id="g5x-menu"
      anchorEl={g5xMenuAnchor}
      keepMounted
      open={Boolean(g5xMenuAnchor)}
      onClose={() => {setG5xMenuAnchor(null)}}
    >
      <MenuItem  
        key="g5x-zero"
        onClick={() => { handleSetDroAllViaG5x(0); setG5xMenuAnchor(null); }}
      >
        {t("zeroDro")}
      </MenuItem>
      <MenuItem  
        key="g5x-clear"
        onClick={() => { handleSetG5xOffsetAll(0); setG5xMenuAnchor(null); }}
      >
        {t("clearCurrentSystem")}
      </MenuItem>
      {g5xSystems.map((system, index) => (
        <MenuItem 
          key={"g5x-set-" + system}
          onClick={handleSetG5xIndex}
          value={index}
        >
          {t("use") + " " + g5xSystems[index]}
        </MenuItem>
      ))}
    </Menu>
    
    <Menu
      id="g92-menu"
      anchorEl={g92MenuAnchor}
      keepMounted
      open={Boolean(g92MenuAnchor)}
      onClose={() => {setG92MenuAnchor(null)}}
    >
      <MenuItem  
        key='g92-zero'
        onClick={() => { handleSetDroAllViaG92(0); setG92MenuAnchor(null); }}
      >{t("zeroDro")}</MenuItem>
      <MenuItem  
        key='g92-clear'
        onClick={handleClearG92}
      >{t("g92Clear")}</MenuItem>
      <MenuItem  
        key='g92-disable'
        onClick={handleDisableG92}
      >{t("g92Suspend")}</MenuItem>
      <MenuItem  
        key='g92-restore'
        onClick={handleRestoreG92}
      >{t("g92Restore")}</MenuItem>
    </Menu>

    <Menu
      id="mobile-menu"
      anchorEl={mobileMenuAnchor}
      keepMounted
      open={Boolean(mobileMenuAnchor)}
      onClose={() => {setMobileMenuAnchor(null)}}
    >
      <MenuItem 
        key="open-g92-menu"
        onClick={openG92Menu}
      >G92</MenuItem>
      <MenuItem 
        key="open-g5x-menu"
        onClick={openG5xMenu}
      >{g5xLabel(g5xIndex)}</MenuItem>
    </Menu>

    <Grid container spacing={1} className={classes.container}>
      <Grid item container justify="flex-start" alignItems="center" direction="row" spacing={0} className={classes.headerRow}>
        <Grid item container justify="space-between" direction="row" xs={4} md={4}>
          <Grid item xs={3} md={2} className={classes.spacer}></Grid>
          <Grid item xs={3} md={4}>{t("dro")}</Grid>
          <Grid item xs={3} md={2} className={classes.spacer}></Grid>
          <Grid item xs={3} md={4}>{t("absolutePosition")}</Grid>
        </Grid>
        <Grid item className={classes.spacer} xs={1} sm={1} ></Grid>
        <Hidden mdDown>
          <Grid item sm={2}>
            <Button 
              className={classes.menuOpenButton}
              color='primary' 
              endIcon={<ExpandMoreIcon/>}
              disabled={!readyForMdi.isReady}
              variant='outlined'
              ref={anchorRef} 
              onClick={openG5xMenu} 
            >
              {g5xLabel(g5xIndex)}
            </Button>
          </Grid>
          <Grid item className={classes.spacer}></Grid>
          <Grid item sm={2}>
            <Button
              className={classes.menuOpenButton}
              color='primary'
              endIcon={<ExpandMoreIcon/>}
              disabled={!readyForMdi.isReady}
              variant='outlined'
              onClick={openG92Menu}
            >
              G92
            </Button>
          </Grid>
          <Grid item align="center" className={classes.spacer}></Grid>
          <Grid item  sm={2}>
            {toolInSpindle ? ( t("tlo") + ": " + t("tool") + toolInSpindle) : t("noActiveTool") }
          </Grid>
        </Hidden>
        <Hidden lgUp>
          <Grid item xs={4}>
            <Button 
              className={classes.menuOpenButton}
              color='primary' 
              startIcon={<ExpandMoreIcon/>}
              disabled={!readyForMdi.isReady}
              variant='outlined'
              ref={anchorRef} 
              onClick={openMobileMenu} 
            >
              {t("offset_plural")}
            </Button>
          </Grid>
        </Hidden>
      </Grid>
      { workOffsets.map((axisData) => {
        const positionDisplayUnits = toDisplayUnits(axisData.position);
        const decimalPoints = UNITS.DECIMALS(positionDisplayUnits.unit);
        return (
        <Grid item container justify="flex-start" alignItems="center" direction="row" spacing={0} key={axisData.axis}>
          <Hidden mdDown>
            <Grid item container justify="space-between" direction="row" xs={7} sm={4}>
              <Grid  item className={classes.spacer} xs={1} sm={2}>{ axisData.axis }</Grid>
              <Grid item className={classes.text} xs={4} sm={4}>
                { positionDisplayUnits.value.toFixed(decimalPoints) + " " + positionDisplayUnits.unit }
              </Grid>
              <Grid item className={classes.spacer} xs={1} sm={2}>=</Grid>
              <Grid item className={classes.text} xs={4} sm={4}>
                { toDisplayUnits(axisData.actualPosition).value.toFixed(decimalPoints) }
              </Grid>
            </Grid>
            <Grid item className={classes.spacer} xs={2} sm md={1}>-</Grid>
            <Grid item sm={2}>
              <ModalSubmittableTextField
                value={toDisplayUnits(axisData.g5xOffset).value.toFixed(decimalPoints)}
                modalValue={"0.0000"}
                onSubmitTextField={(value) =>  handleSetG5xOffset(value, axisData)}
                onSubmitModal={(value) => handleSetDroViaG5x(value, axisData)}
                disabled={!readyForMdi.isReady}
                buttonDom={
                  <>
                    <div style={{fontSize: 6}}>DRO</div>
                    <MenuOpenIcon />
                  </>
                }
                buttonProps={{
                  color: "primary",
                }}
                containerProps={{
                  className: classes.offsetItemContainer
                }}
                modalProps={{
                  title: `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} ${g5xLabel(g5xIndex)}`,
                  textFieldLabel: t('newDroValue'),
                  submitBtnLabel: t('set'),
                  cancelBtnLabel: t('cancel'),
                }}
                textFieldProps={{
                  InputProps: {
                    disableUnderline: true,
                    classes: {
                      input: classes.overrideTextFieldInputInput,
                      root: classes.root,
                    }
                  }
                }}
              />
            </Grid>
            <Grid item align="center"className={classes.spacer}>-</Grid>
            <Grid item container justify="center" direction="row" sm={2}>
              <ModalSubmittableTextField
                value={ toDisplayUnits(axisData.g92Offset).value.toFixed(decimalPoints) }
                modalValue={"0.0000"}
                onSubmitTextField={(value) => handleSetG92Offset(value, axisData)}
                onSubmitModal={(value) => handleSetDroViaG92(value, axisData)}
                disabled={!readyForMdi.isReady}
                buttonDom={
                  <>
                    <div style={{fontSize: 6}}>DRO</div>
                    <MenuOpenIcon />
                  </>
                }
                buttonProps={{
                  color: "primary",
                }}
                containerProps={{
                  className: classes.offsetItemContainer
                }}
                modalProps={{
                  title: `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} G92`,
                  textFieldLabel: t('newDroValue'),
                  submitBtnLabel: t('set'),
                  cancelBtnLabel: t('cancel'),
                }}
                textFieldProps={{
                  InputProps: {
                    disableUnderline: true,
                    classes: {
                      input: classes.overrideTextFieldInputInput,
                      root: classes.root,
                    }
                  }
                }}
              />
            </Grid>
            <Grid item align="center" className={classes.spacer}> 
                {axisData.index === 2 ?  "-" : "" }
            </Grid>
            <Grid item container justify="flex-start" direction="row" sm={2}>
              <Grid item>
              { 
                (axisData.index !== 2) ? "" : 
                (
                  <ModalSubmittableTextField
                    value={toDisplayUnits(axisData.toolOffset).value.toFixed(decimalPoints)}
                    modalValue={"0.0000"}
                    onSubmitTextField={(value) => handleSetTlo(value)}
                    onSubmitModal={(value) => handleSetDroViaTlo(value,axisData)}
                    disabled={!readyForMdi.isReady}
                    buttonDom={
                      <>
                        <div style={{fontSize: 6}}>DRO</div>
                        <MenuOpenIcon />
                      </>
                    }
                    buttonProps={{
                      color: "primary",
                    }}
                    containerProps={{
                      className: classes.offsetItemContainer
                    }}
                    modalProps={{
                      title: `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} TLO`,
                      textFieldLabel: t('newDroValue'),
                      submitBtnLabel: t('set'),
                      cancelBtnLabel: t('cancel'),
                    }}
                    textFieldProps={{
                      InputProps: {
                        disableUnderline: true,
                        classes: {
                          input: classes.overrideTextFieldInputInput,
                          root: classes.root,
                        }
                      }
                    }}
                  />
                )
              }
              </Grid>
            </Grid>
          </Hidden>
          <Hidden lgUp>
            <Grid item xs={12}>
              <Accordion >
                <AccordionSummary
                  expandIcon={<ExpandMoreIcon color="primary"/>}
                >
                  <Grid item container justify="space-between" direction="row" xs={12}>
                    <Grid  item className={classes.spacer} xs={1}>{ axisData.axis}</Grid>
                    <Grid item className={classes.text} xs={3}>
                      { toDisplayUnits(axisData.position).value.toFixed(decimalPoints) + " " + (toDisplayUnits(axisData.position).unit)}
                    </Grid>
                    <Grid item className={classes.spacer} xs={1}>=</Grid>
                    <Grid item className={classes.text} xs={3}>
                      { toDisplayUnits(axisData.actualPosition).value.toFixed(decimalPoints) }
                    </Grid>
                    <Grid item className={classes.spacer} xs={1}>-</Grid>
                    <Grid item className={classes.text} xs={3}>
                      { toDisplayUnits({ value: axisData.actualPosition.value - axisData.position.value, unit: axisData.position.unit }).value.toFixed(decimalPoints)}
                    </Grid>
                  </Grid>
                </AccordionSummary>
                <AccordionDetails>
                  <Grid container direction="column" alignItems="flex-end" spacing={1}>
                    <Grid item container direction="row" justify="center">
                      <Grid item xs={2} className={classes.spacer}>{g5xLabel(g5xIndex)}</Grid>
                      <Grid item container justify="flex-start" direction="row" xs={4}>
                        <ModalSubmittableTextField
                          value={ toDisplayUnits(axisData.g5xOffset).value.toFixed(decimalPoints) }
                          modalValue={"0.0000"}
                          onSubmitTextField={(value) => handleSetG5xOffset(value, axisData)}
                          onSubmitModal={(value) => handleSetDroViaG5x(value, toDisplayUnits(axisData.g5xOffset), axisData)}
                          disabled={!readyForMdi.isReady}
                          buttonDom={
                            <>
                              <div style={{fontSize: 6}}>DRO</div>
                              <MenuOpenIcon />
                            </>
                          }
                          buttonProps={{
                            color: "primary",
                          }}
                          containerProps={{
                            className: classes.offsetItemContainer
                          }}
                          modalProps={{
                            title: `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} ${g5xLabel(g5xIndex)}`,
                            textFieldLabel: t('newDroValue'),
                            submitBtnLabel: t('set'),
                            cancelBtnLabel: t('cancel'),
                          }}
                          textFieldProps={{
                            InputProps: {
                              disableUnderline: true,
                              classes: {
                                input: classes.overrideTextFieldInputInput,
                                root: classes.root,
                              }
                            }
                          }}
                        />
                      </Grid>
                    </Grid>
                    <Grid item container direction="row" justify="center">
                      <Grid item xs={2} className={classes.spacer}>G92</Grid>
                      <Grid item container justify="flex-start" direction="row" xs={4}>
                        <ModalSubmittableTextField
                          value={toDisplayUnits(axisData.g92Offset).value.toFixed(decimalPoints)}
                          modalValue={"0.0000"}
                          onSubmitTextField={(value) => handleSetG92Offset(value, axisData)}
                          onSubmitModal={(value) => handleSetDroViaG92(value, axisData)}
                          disabled={!readyForMdi.isReady}
                          buttonDom={
                            <>
                              <div style={{fontSize: 6}}>DRO</div>
                              <MenuOpenIcon />
                            </>
                          }
                          buttonProps={{
                            color: "primary",
                          }}
                          containerProps={{
                            className: classes.offsetItemContainer
                          }}
                          modalProps={{
                            title: `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} G92`,
                            textFieldLabel: t('newDroValue'),
                            submitBtnLabel: t('set'),
                            cancelBtnLabel: t('cancel'),
                          }}
                          textFieldProps={{
                            InputProps: {
                              disableUnderline: true,
                              classes: {
                                input: classes.overrideTextFieldInputInput,
                                root: classes.root,
                              }
                            }
                          }}
                        />
                      </Grid>
                    </Grid>
                    <Grid item container direction="row" justify="center">
                    { axisData.index === 2 ? (
                      <>
                      <Grid item xs={2} className={classes.spacer}>{t("tlo")}</Grid>
                      <Grid item container justify="flex-start" direction="row" xs={4}>
                        <ModalSubmittableTextField
                          value={toDisplayUnits(axisData.toolOffset).value.toFixed(decimalPoints)}
                          modalValue={"0.0000"}
                          onSubmitTextField={(value) => handleSetTlo(value)}
                          onSubmitModal={(value) => handleSetDroViaTlo(value,axisData)}
                          disabled={!readyForMdi.isReady}
                          buttonDom={
                            <>
                              <div style={{fontSize: 6}}>DRO</div>
                              <MenuOpenIcon />
                            </>
                          }
                          buttonProps={{
                            color: "primary",
                          }}
                          containerProps={{
                            className: classes.offsetItemContainer
                          }}
                          modalProps={{
                            title: `${t('set')} ${(axisData.axis).toUpperCase()} ${t('dro')} ${t('via')} TLO`,
                            textFieldLabel: t('newDroValue'),
                            submitBtnLabel: t('set'),
                            cancelBtnLabel: t('cancel'),
                          }}
                          textFieldProps={{
                            InputProps: {
                              disableUnderline: true,
                              classes: {
                                input: classes.overrideTextFieldInputInput,
                                root: classes.root,
                              }
                            }
                          }}
                        />
                      </Grid>
                    </>
                    ) : "" }
                  </Grid>
                  </Grid>
                </AccordionDetails>
              </Accordion>
            </Grid>
          </Hidden>
        </Grid>
      )})}
    </Grid>
  </Panel>;
};
export default WorkOffsets;
