const IncrementalFloatingPointSpinner = (props) => {
  const {
    value,
    onChange,
    fractionalDigits,
    label,
    wholeDigits,
    highlighted,
    maxOpenIndex,
    openIndex = -1,
    onSetOpenIndex,
    unit,
    onHome
  } = props;

  const { getDisplayUnitStringFromUnit } = useUnits();

  const size = 30;
  
  const classes = useStyles();

//  const handleDismiss = useCallback(() => {
//    if(onSetOpenIndex) {
//      onSetOpenIndex(-1);
//    }
//  }, [ onSetOpenIndex ]);

  const str = value.toFixed(fractionalDigits);
  const [ whole, fractional ] = str.split(".");

  const wholeChars = whole.split("");
  while(wholeChars.length < wholeDigits) {
    wholeChars.unshift("");
  }

  const fractionalChars = fractional.split("");

  const chars = [];
  wholeChars.forEach((c, i) => {
    const digit = wholeChars.length-1-i;
    const charIndex = fractionalChars.length+digit+1;
    chars.push({ char: c, active: charIndex <= maxOpenIndex, increment: Math.pow(10, digit), tickDist: size*.5, charIndex });
  });

  chars.push({ char: ".", active: false, increment: 0, tickDist: size*.5 });

  fractionalChars.forEach((c, digit) => {
    const charIndex = fractionalChars.length-digit;
    chars.push({ char: c, active: charIndex <= maxOpenIndex, increment: Math.pow(10, -(digit+1)), tickDist: size*.5, charIndex });
  });

  const elements = chars.map(({ char, active, increment, tickDist, charIndex}, i) => {
    const handleChange = (ticks) => {
      if(onChange) {
        onChange(ticks*increment);
      }
    };
    const spinner = charIndex === openIndex ? 
      <IncrementalSpinner reverse={true} defaultValue={parseInt(char)} thickness={size} diameter={size} tickDist={tickDist} curvature={1} orientation="vertical" onChange={handleChange} />
      : null;
    return <span key={i} 
                 className={classnames({ [classes.digit]: true, [classes.active]: active, [classes.open]: openIndex === charIndex })} 
                 onClick={(e) => { if(active) { if(onSetOpenIndex) { onSetOpenIndex(charIndex) } }}}
                 style={{ fontSize: size, lineHeight: size + "px", width: size, height: size }}>
      {char}
      <div className={classes.spinner}>
      {spinner}
      </div>
    </span>;
  });

  return <div className={classes.container}>
  <span className={classnames(classes.label, { [classes.activeLabel]: highlighted, [classes.inactiveLabel]: !highlighted })}>{label}</span>
  <span className={classnames(classes.digits, { [classes.activeDigits]: highlighted, [classes.inactiveDigits]: !highlighted })}>
    {elements}
    <span className={classnames(classes.units)}>
      <span className={classes.axisUnitDegree}>
        {getDisplayUnitStringFromUnit(unit)}
      </span>
    </span>
{/*    <span className={classes.units} style={ { display: openIndex === -1 ? "none" : "inline-block" }}>
    <IconButton size="small" classes={{ root: classes.dismiss }} onClick={handleDismiss}><DismissIcon/></IconButton>
    </span> */}
  </span>
  <span className={classes.homeButton}>
    <IconButton onClick={onHome} size="small"><HomeIcon/></IconButton>
  </span>
  </div>;
};
export default IncrementalFloatingPointSpinner;
