import React, { useCallback } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Panel from './Panel';
import {IconButton, Button } from '@material-ui/core';
import PlayArrowOutlinedIcon from '@material-ui/icons/PlayArrowOutlined';
import PauseOutlinedIcon from '@material-ui/icons/PauseOutlined';
import SkipNextIcon from '@material-ui/icons/SkipNextOutlined';
import useTranslation from '../hooks/useTranslation';
import Slider from '@material-ui/core/Slider';
import ResetIcon from '@material-ui/icons/RotateLeft';
import { MACHINE_STATUS } from '../modules/rockhopper/constants';

const useStyles = makeStyles((theme) => ({
  button: {
    marginBottom: theme.spacing(1),
    marginRight: theme.spacing(1)
  },
  sliderContainer: {
    position: "absolute",
    width: "calc(100% - 54px)",
    bottom: theme.spacing(1),
    left: 0
  },
  resetContainer: {
    position: "absolute",
    width: 40,
    height: 40,
    right: 0,
    bottom: 4+theme.spacing(1)
  },
  labelContainer: {
    position: "absolute",
    bottom: 36,
    ...theme.typography.statusItem,
    fontSize: 14
  },
  valueContainer: {
    position: "absolute",
    bottom: 0,
    ...theme.typography.statusItem,
    fontSize: 16
  },
  slider: {
  },
  row: {
    position: "relative",
    width: "100%",
    height: 40 + theme.spacing(2),
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3)
  }
}));

const Control = (props) => {
  const {
    onFeedHold,
    onCycleStart,
    onStep,
    maxVel,
    maxRapid,
    onSetMaxVelocity,
    feedrate,
    onSetFeedrate,
    spindlerate,
    onSetSpindlerate,
    machineStatus,
    onSetMaxRapid
  } = props;

  const handleChangeMaxVelocity = useCallback((e, v) => {
    if(onSetMaxVelocity && v !== maxVel) {
      onSetMaxVelocity(v, v-maxVel);
    }
  }, [ onSetMaxVelocity, maxVel ]);

  const handleResetMaxVel = useCallback((e) => {
    if(onSetMaxVelocity) {
      onSetMaxVelocity(100);
    }
  }, [ onSetMaxVelocity ]);

  const handleChangeMaxRapid = useCallback((e, v) => {
    if(onSetMaxRapid && v !== maxRapid) {
      onSetMaxRapid(v);
    }
  }, [ onSetMaxRapid, maxRapid ]);

  const handleResetMaxRapid = useCallback((e) => {
    if(onSetMaxRapid) {
      onSetMaxRapid(100);
    }
  }, [ onSetMaxRapid ]);

  const handleChangeFeedrate = useCallback((e, v) => {
    if(onSetFeedrate && v !== feedrate) {
      onSetFeedrate(v, v-feedrate);
    }
  }, [ onSetFeedrate, feedrate ]);

  const handleResetFeedrate = useCallback((e) => {
    if(onSetFeedrate) {
      onSetFeedrate(100);
    }
  }, [ onSetFeedrate ]);

  const handleChangeSpindlerate = useCallback((e, v) => {
    if(onSetSpindlerate && v !== spindlerate) {
      onSetSpindlerate(v, v-spindlerate);
    }
  }, [ onSetSpindlerate, spindlerate ]);

  const handleResetSpindlerate = useCallback((e) => {
    if(onSetSpindlerate) {
      onSetSpindlerate(100);
    }
  }, [ onSetSpindlerate ]);

  const { t } = useTranslation();

  const classes = useStyles();
  
  return (
    <Panel label={t("control")}>
    <div>
      <Button size="small" onClick={onCycleStart} variant="contained" color="primary" classes={{ root: classes.button }} disabled={machineStatus === MACHINE_STATUS.MDI || machineStatus === MACHINE_STATUS.RUNNING}><PlayArrowOutlinedIcon/>{ t("cycleStart") }</Button>
      <Button size="small" onClick={onFeedHold} variant="contained" color="secondary" classes={{ root: classes.button }} disabled={machineStatus === MACHINE_STATUS.IDLE}><PauseOutlinedIcon/>{ t("feedHold") }</Button>
      <Button size="small" onClick={onStep} variant="contained" color="primary" classes={{ root: classes.button }} disabled={machineStatus !== MACHINE_STATUS.PAUSED && machineStatus !== MACHINE_STATUS.RUNNING}><SkipNextIcon/>{ t("step") }</Button>
    </div>
    <div className={classes.row}>
      <div className={classes.sliderContainer}><Slider orientation="horizontal" value={maxVel} max={100} min={0} step={1} valueLabelDisplay="off" classes={{ root: classes.slider}} onChange={handleChangeMaxVelocity}/></div>
      <div className={classes.resetContainer}><IconButton onClick={handleResetMaxVel}><ResetIcon/></IconButton></div>
      <div className={classes.labelContainer}>{t("maxVelocity")}</div>
      <div className={classes.valueContainer}>{maxVel}%</div>
    </div>
    <div className={classes.row}>
      <div className={classes.sliderContainer}><Slider orientation="horizontal" value={maxRapid} max={100} min={0} step={1} valueLabelDisplay="off" classes={{ root: classes.slider}} onChange={handleChangeMaxRapid}/></div>
      <div className={classes.resetContainer}><IconButton onClick={handleResetMaxRapid}><ResetIcon/></IconButton></div>
      <div className={classes.labelContainer}>{t("maxRapidRate")}</div>
      <div className={classes.valueContainer}>{maxRapid}%</div>
    </div>
    <div className={classes.row}>
      <div className={classes.sliderContainer}><Slider orientation="horizontal" value={feedrate} max={200} min={0} step={1} valueLabelDisplay="off" classes={{ root: classes.slider}} onChange={handleChangeFeedrate}/></div>
      <div className={classes.resetContainer}><IconButton onClick={handleResetFeedrate}><ResetIcon/></IconButton></div>
      <div className={classes.labelContainer}>{t("feedRate")}</div>
      <div className={classes.valueContainer}>{feedrate}%</div>
    </div>
    <div className={classes.row}>
      <div className={classes.sliderContainer}><Slider orientation="horizontal" value={spindlerate} max={200} min={0} step={1} valueLabelDisplay="off" classes={{ root: classes.slider}} onChange={handleChangeSpindlerate}/></div>
      <div className={classes.resetContainer}><IconButton onClick={handleResetSpindlerate}><ResetIcon/></IconButton></div>
      <div className={classes.labelContainer}>{t("spindleRate")}</div>
      <div className={classes.valueContainer}>{spindlerate}%</div>
    </div>
    </Panel>
  )
};

export default Control;
