import React, {useState} from 'react';

import Box from '@material-ui/core/Box';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogTitle from '@material-ui/core/DialogTitle';

import { makeStyles } from '@material-ui/styles';

import { DialogActions, DialogContent } from '@material-ui/core';
import useTranslation from '../hooks/useTranslation';

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1)
  },
  displayDiv: {
    display: "flex",
    justifyContent: "center",
  }
}));

const KeyboardCaptureDialog = (props) => {
  const {
    open,
    value,
    onSubmit,
    onClose,
    title,
    submitBtnLabel,
    cancelBtnLabel,
    modifiersOnly,
    singleKeyOnly,
    reservedKeys,
    reservedCombinations,
  } = props;
  const classes = useStyles(props);
  const { t } = useTranslation();
  const [keys, setKeys] = useState(value ? value.split("+") : []);
  const [reservedKeyAlert, setReservedKeyAlert] = useState(false);
  const [reservedCombinationAlert, setReservedCombinationAlert] = useState(false);
  const [modifiersOnlyAlert, setModifiersOnlyAlert] = useState(false);
  const [singleKeyOnlyAlert, setSingleKeyOnlyAlert] = useState(false);
  const [nonModifierNeededAlert, setNonModifierNeededAlert] = useState(false);
  const [submitDisabled, setSubmitDisabled] = useState(false);

  const handleKeyDown = (e) => {
    let disableSubmit = false;

    if(reservedKeys.has(e.key)){
      setReservedKeyAlert(true);
      disableSubmit = true;
    }
    else if(reservedKeyAlert){ setReservedKeyAlert(false) }
    
    if(singleKeyOnly && (e.ctrlKey || e.shiftKey || e.altKey || e.metaKey) ){
      setSingleKeyOnlyAlert(true);
      disableSubmit = true;
    }
    else if(singleKeyOnlyAlert){ setSingleKeyOnlyAlert(false) }
    
    if(modifiersOnly && ! (["Alt", "Shift", "Control", "Meta"].includes(e.key)) ){
      setModifiersOnlyAlert(true);
      disableSubmit = true;
    }
    else if(modifiersOnlyAlert){ setModifiersOnlyAlert(false) }

    if(!modifiersOnly && (["Alt", "Shift", "Control", "Meta"].includes(e.key))){
      setNonModifierNeededAlert(true);
      disableSubmit = true;
    }
    else if(nonModifierNeededAlert){ setNonModifierNeededAlert(false) }

    let newKeys = [];
    if(e.ctrlKey){
      newKeys.push(t("Ctrl"));
    }
    if(e.shiftKey){
      newKeys.push(t("Shift"));
    }
    if(e.altKey){
      newKeys.push(t("Alt"));
    }
    if(e.metaKey){
      newKeys.push(t("Meta"));
    }
    if( ! (["Alt", "Shift", "Control", "Meta"].includes(e.key)) ){
      if(e.nativeEvent.code.startsWith("Digit")){
        newKeys.push(e.nativeEvent.code.slice(5));
      }
      else if(e.nativeEvent.code.startsWith("Key")){
        newKeys.push(e.nativeEvent.code.slice(3));
      }
      else{
        newKeys.push(e.nativeEvent.code);
      }
    }
    if(reservedCombinations.has(newKeys.join("+"))){
      setReservedCombinationAlert(true)
      disableSubmit = true;
    }
    else if(reservedCombinationAlert){ setReservedCombinationAlert(false) }

    if(disableSubmit){
      setSubmitDisabled(true);
    }
    else{
      setSubmitDisabled(false);
    }

    setKeys(newKeys);
  }

  const handleClose = () => {
    if(onClose) {
      onClose()
    }
  };

  const handleSubmit = (e) => {
    if(onSubmit) {
      e.preventDefault();
      onSubmit(keys.join("+"));
    }
  };

  return (
    <>
    <Dialog id="keyboardCapture_dialog" open={open} onClose={handleClose} onKeyDown={handleKeyDown}>
      <DialogTitle >{title}</DialogTitle>
      <DialogContent>
        {reservedKeyAlert && <div style={{backgroundColor: "red"}} severity="error">{t("This non-modifier key is reserved.")}</div>}
        {reservedCombinationAlert && <div style={{backgroundColor: "red"}} severity="error">{t("This key combination is already in use.")}</div>}
        {modifiersOnlyAlert && <div style={{backgroundColor: "red"}} severity="error">{t("This is a modifier-only keybinding.")}</div>}
        {singleKeyOnlyAlert && <div style={{backgroundColor: "red"}} severity="error">{t("This shortcut must be a single non-modifier key")}</div>}
        {nonModifierNeededAlert && <div style={{backgroundColor: "red"}} severity="error">{t("This shortcut must include a non-modifier key")}</div>}
        <div
          onKeyDown={handleKeyDown}
          className={classes.displayDiv}
        >
          {keys.join("+")}
        </div>
      </DialogContent>
      <DialogActions>
        <Box>
          <Button
            id="keyboardCapture_submit"
            onClick={(e) => { handleSubmit(e) } }
            variant="contained" 
            color="primary"
            className={classes.button}
            disabled={submitDisabled}
          >
            {submitBtnLabel}
          </Button>
          <Button 
            id="keyboardCapture_cancel"
            onClick={handleClose}
            variant="contained" 
            color="primary"
            className={classes.button}
          >
            {cancelBtnLabel}
          </Button>
        </Box>
      </DialogActions>
    </Dialog>
    </>
  )
};
export default KeyboardCaptureDialog;
