import { connect } from 'react-redux';
import StatusBar from '../components/StatusBar';
import { selectIsLoggedIn, selectRtcSeconds, selectMachineStatus } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  const connected = selectIsLoggedIn(state);
  const rtcSeconds = selectRtcSeconds(state);
  const machineStatus = selectMachineStatus(state);
  return { connected, rtcSeconds, machineStatus };
}, 
(dispatch, ownProps) => ({ }))(StatusBar);
