const mapIni = (ini) => {
  let map = {};
  if(ini.parameters.length > 0){
    Object.keys(ini.sections).forEach((section) => { map[section] = {} })
    ini.parameters.forEach((param, idx) => { map[param.values.section][idx] = {...param.values, key: param.values.name} });
  }
  return map;
}
export const selectConfigMap = (state) => {
  const config = state.rockhopper.status.config || { parameters: [], sections: {} };
  return mapIni(config);
};
export const selectConfigOverlayMap = (state) => {
  const configOverlay = state.rockhopper.status.configOverlay || { parameters: [], sections: {} };
  return mapIni(configOverlay);
};
