export const selectMachineStatus = (state) => {
  const {
    interpState,
    taskMode
  } = state.rockhopper.status;

  if(interpState === INTERP_STATE.PAUSED) {
    return MACHINE_STATUS.PAUSED;
  }
  if(interpState !== INTERP_STATE.IDLE && taskMode === TASK_MODE.AUTO) {
    return MACHINE_STATUS.RUNNING;
  }

  if(interpState === INTERP_STATE.IDLE) {
    return MACHINE_STATUS.IDLE;
  }

  if(taskMode === TASK_MODE.MDI) {
    return MACHINE_STATUS.MDI;
  }

  return "unknown";
};

