export const makeConfigSectionSelector = (section) => createSelector([ selectConfig ], (config) => {
  return config.parameters.reduce((accum, current) => {
    if(current.values.section === section) {
      accum[current.values.name] = current.values.value;
    }

    return accum;
  }, {});
});
export const makeConfigParameterSelector = (section, param) => createSelector([ selectConfig ], (config) => {
  return config.parameters.reduce((accum, current) => {
    if(current.values.section === section && current.values.name === param) {
      return current.values.value;
    }
    return accum;
  }, null);
});

export const selectTraj = makeConfigSectionSelector("TRAJ");
export const selectTrajCoordinates = makeConfigParameterSelector("TRAJ", "COORDINATES");
