export const selectAxisToJointMap = createSelector([ selectTrajCoordinates ], (coords="") => {
  if(coords) {
    const a2j = {};
    for(let j = 0; j < coords.length; j++) {
      a2j[coords[j].toUpperCase()] = j;
    }
    return a2j;
  }
  return {};
});

export const selectProgramList = createSelector([ selectProgramPrefix, selectLs ], (prefix, ls) => {
  if(ls && prefix) {
    return ls.map((path) => ({ file: path.replace(prefix + "/", ""), path })).sort(
      // performs a natural sort of the file name ('a' comes before 'T' and '8' comes before '11', unlike with default sort order)
      (a, b) => a.file.localeCompare(b.file, navigator.languages[0] || navigator.language, {numeric: true, ignorePunctuation: true})
    );
  }

  return []
});

export const selectKinsType = (state) => state.rockhopper.status.halpinMotionSwitchkinsType;

export const selectActiveCodes = createSelector([ selectGcodes, selectMcodes, selectSettings, selectKinsType, selectToolReleased, selectViceReleased ], (gcodes, mcodes, settings, kinsType, toolReleased, viceReleased) => {
  const codes = [];

  if(gcodes) {
    const sorted_gcodes = gcodes.slice(1).sort((a,b) => a.toString().localeCompare(b.toString(), navigator.languages[0] || navigator.language, { numeric: true, ignorePunctuation: true }))
    sorted_gcodes.forEach((code, i) => {
      if(code > 0) {
        codes.push("G" + (code/10))
      }
    });
  }

  if(mcodes) {
    const sorted_mcodes = mcodes.slice(1).sort((a,b) => a.toString().localeCompare(b.toString(), navigator.languages[0] || navigator.language, { numeric: true, ignorePunctuation: true }))
    sorted_mcodes.forEach((code, i) => {
      if(code > 0) {
        codes.push("M" + code);
      }
    });
  }

  if(toolReleased) {
    codes.push("M" + 160);
  } else {
    codes.push("M" + 161);
  }

  if(viceReleased) {
    codes.push("M" + 162);
  } else {
    codes.push("M" + 163);
  }

  switch(kinsType) {
    case 0:
      codes.push("M429");
      break;
    case 1:
      codes.push("M428");
      break;
    case 2:
      codes.push("M430");
      break;
    default:
      break;
  }

  if(settings) {
    codes.push("F" + settings[1].toFixed(0));
    codes.push("S" + settings[2]);
  }

  return codes;
});

export const selectUnitsCode = createSelector([ selectActiveCodes ], (activeCodes) => {
  if(activeCodes){
    if(activeCodes.includes('G21')){
      return 'G21'
    }
    else if(activeCodes.includes('G20')){
      return 'G20'
    }
    else return false;
  }
});

export const selectProgram = (state) => { 
  const file = state.rockhopper.status.program;
  const prefix = state.rockhopper.status.programPrefix;
  if(file && file.startsWith(prefix)) {
    return file.replace(prefix + "/", "");
  }
  return file;
};


