export const selectFileUploadsThatNeedOverwritePermission = (state) => {
  const needsOverwritePermission = [];
  for(let key in state.rockhopper.fileUpload) {
    if(state.rockhopper.fileUpload[key].state === FILE_UPLOAD_ASK_FOR_OVERWRITE && !state.rockhopper.fileUpload[key].overwrite) {
      needsOverwritePermission.push(state.rockhopper.fileUpload[key]);
    }
  }
  return needsOverwritePermission;
};

export const selectAxes = createSelector([ selectAxisMask, selectTrajCoordinates ], (axisMask = 0, coords="") => {
  const axes = [];
  const axisNames = "XYZABCUVW";
  for(let index = 0; index < 9; index++) {
    if(axisMask & (1 << index)) {
      const axis = axisNames[index];
      const jointIndex = coords ? coords.indexOf(axis) : -1;
      axes.push({ axis, index, jointIndex });
    }
  }

  return axes;
});

export const selectIsMachineHomed = createSelector(
  [ selectJoints, selectHomed ],
  (joints, homed) => {
    if(joints && homed){
      for(let i = 0; i < joints; i++) {
        if(!homed[i]) {
          return false;
        }
      }
    }
    return true;
  }
);

export const selectAxesData = (state) => state.rockhopper.status.axesData;

export const selectFeedPower = createSelector([ selectAxesData ], (axes) => {
  if(axes) {
    return {
      value: 1/(5/25.4)*Math.abs(axes[0].velocity)*Math.PI*2*axes[0].torque*2+ 
              1/(5/25.4)*Math.abs(axes[1].velocity)*Math.PI*2*axes[1].torque*2+ 
              1/(5/25.4)*Math.abs(axes[2].velocity)*Math.PI*2*axes[2].torque*3.5+ 
              Math.abs(axes[3].velocity)*4080/26*Math.PI/180*axes[3].torque*3.5+
              Math.abs(axes[4].velocity)*4080/26*Math.PI/180*axes[4].torque*2,
      unit: UNITS.MACHINE_UNITS.POWER
    }
  }
  return { value: 0, unit: UNITS.MACHINE_UNITS.POWER }
});

// computed data can be generated with a selector
export const selectPositions = createSelector([ selectG5xOffset,
                                                selectG92Offset,
                                                selectToolOffset,
                                                selectIsMachineHomed,
                                                selectAxesData ], 
                                              ( g5xOffset,
                                                g92Offset,
                                                toolOffset,
                                                homed,
                                                axesData) => {
  if(axesData &&
     g5xOffset &&
     g92Offset &&
     toolOffset) {
      
       return axesData.map(({ velocity, axis, index, jointIndex, actualPosition, jointActualPosition, dtg, torque }) => ({
        axis,
        index,
        jointIndex,
        actualPosition: { value: actualPosition, unit: UNITS.AXES[axis] },
        jointActualPosition: { value: jointActualPosition, unit: UNITS.AXES[axis]},
        velocity: { 
          value: velocity, 
          unit: (UNITS.AXES[axis] + "/s") 
        },
        position: { 
          value: homed ? actualPosition-g5xOffset[index]-g92Offset[index]-toolOffset[index] : (jointIndex > -1 ? jointActualPosition : 0),
          unit: UNITS.AXES[axis]
        },
        torque,
        dtg: {
          value: homed ? dtg : 0,
          unit: UNITS.AXES[axis]
        }
      }));
    } else {
      return [];
    }
});

