export const selectSpindlePower = createSelector([ selectRockhopper ], (state) => {
  const {
    measuredSpindleCurrent = 0,
    measuredSpindleVoltage = 0
  } = state.status;

  const current = measuredSpindleCurrent || 0;
  const voltage = measuredSpindleVoltage || 0;

  return {value: current*voltage, unit: UNITS.MACHINE_UNITS.POWER} ; // power in watts or volt-Amps
});

export const selectSpindleLoad = createSelector([ selectSpindlePower, selectMeasuredSpindleSpeed ], (power, spindleSpeed) => {
  const maxPower = 2500;
  const maxRPM = 40000;
  const maxPowerAtRPM = spindleSpeed.value/maxRPM*maxPower;

  if(maxPowerAtRPM > 0) {
    return power.value/maxPowerAtRPM;
  }
  return 0;
});

