export const selectToolTable = createSelector([selectRawToolTable,  selectDisplayUnits ], (rawToolTable, units) => {
  const lengthUnit = UNITS.MACHINE_UNITS.LENGTH;
  const angleUnit = UNITS.MACHINE_UNITS.ANGLE;
  //In Machinekit, the columns in the tool table are:  
  //id, xoffset, yoffset, zoffset, aoffset, boffset, coffset, uoffset, voffset, woffset, diameter, frontangle, backangle, orientation. 
  //The id and orientation are integers and the rest are floats.
  const unitsForTool = [null, lengthUnit, lengthUnit, lengthUnit, angleUnit, angleUnit, angleUnit, null, null, null, lengthUnit, angleUnit, angleUnit, null];
  const toolTable = !rawToolTable ? null : (
    rawToolTable.slice(1,53).map(toolRow => { return toolRow.map(((toolDatum,index) => (
      unitsForTool[index] ? (
        {value: toolDatum, unit: unitsForTool[index]}
      ) : toolDatum
    ) )) })
  );
  return toolTable
});


