//CLIENT_CONFIG contains two arrays for MDI, one for history and one for favorites
//The arrays contain objects with a few different properties, but the objects in both
//contain a "cmd" property holding the raw string of the MDI command
function mdiCmdArrayToSet(mdiArr) {
  const mdiSet = new Set();
  mdiArr.forEach((mdiObj) => {
    mdiSet.add(mdiObj["cmd"]);
    return null
  });
  return mdiSet;
};

//Turns the array of MDI favorites into a Map, the "cmd" property becomes the key.
//The purpose is to make deleting multiple items at once simple
export const selectMdiFavoritesMap = createSelector([selectMdiFavorites], (mdiFavorites) => {
  const favsMap = new Map();
  for(let fav of mdiFavorites){
    favsMap.set( fav.cmd, { name: fav.name, time:fav.time, keybind: fav.keybind } )
  };
  return favsMap;
});

export const selectKeyboardShortcutsMap =  createSelector(
  [ 
    selectKeyboardShortcutsJogModifiers,
    selectKeyboardShortcutsJog,
    selectKeyboardShortcutsAxisSelectJogMap,
    selectKeyboardShortcutsOtherMap,
    selectKeyboardShortcutsMdiMap,
  ],
  (
    jogModifiers,
    jogKeys,

    axisSelectJogMap,
    otherShortcutsMap,
    mdiShortcutsMap,
  ) => {
    if( !jogModifiers || !jogKeys || !axisSelectJogMap || !otherShortcutsMap){
      return null;
    }
    let map = {};
    const jogContinuousPrefix = jogModifiers.continuous ? jogModifiers.continuous + "+" : "";
    Object.keys(jogKeys).forEach((key) => {
      if(jogKeys[key] !== ""){
        map[jogContinuousPrefix + jogKeys[key]] = key;
        if(jogModifiers.incremental !== ""){
          map[jogModifiers.incremental + "+" + jogKeys[key]] = key + "incremental";
        }
        if(jogModifiers.slow !== ""){
          map[jogModifiers.slow + "+" + jogKeys[key]] = key + "slow";
        }
      }
    });
    return {...map, ...axisSelectJogMap, ...otherShortcutsMap, ...mdiShortcutsMap}
  }
);

//Creates a map using MDI history and favorites arrays. 
//The keys are the "cmd" properties in history
//Each key has a single property "isFav", which is T/F if an identical string is found in the favorites array.
export const selectMdiHistoryInFavoritesMap = createSelector([selectMdiHistory, selectMdiFavorites], (mdiHistory, mdiFavorites) => {
  const histSet = mdiCmdArrayToSet(mdiHistory);
  const favSet = mdiCmdArrayToSet(mdiFavorites);
  const histInFavsMap = new Map();
  for(let histItem of histSet){
    histInFavsMap.set( histItem, { isFav: favSet.has(histItem) } )
  };
  return histInFavsMap;
});

export const selectMdiHistorySorted = createSelector([selectMdiHistory], (mdiHistory) => {
  return Object.values(mdiHistory).sort((a,b) => b.time - a.time);
})

export const selectMdiHistorySortedUnique = createSelector([selectMdiHistorySorted], (sorted) => {
  var uniqueSet = new Set();
  var uniqueArr = [];
  for(let histItem of sorted){
    const cmd = histItem.cmd.toLowerCase();
    if( ! uniqueSet.has(cmd) ){ 
      uniqueSet.add(cmd);
      uniqueArr.push(histItem);
    }
  }
  return uniqueArr
})

