import { connect } from 'react-redux';
import rockhopperActions from '../modules/rockhopper/actions';
import HomeAllButton from '../components/HomeAllButton';
import { selectIsMachineHomed } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  return { 
    isHomed: selectIsMachineHomed(state),
  }
}, 
(dispatch, ownProps) => ({ 
  onHomeAll: () => dispatch(rockhopperActions.put.homeCmd()),
}))(HomeAllButton);
