import { connect } from 'react-redux';
import ConnectionDetails from '../components/ConnectionDetails';
import actions from '../modules/rockhopper/actions';
import { selectWebSocket } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  const {
    server,
    port,
    user,
    password,
    encrypted
  } = selectWebSocket(state);
  return { server, port, user, password, encrypted }
}, 
(dispatch, ownProps) => ({ 
  onUpdate: (server, port, user, password, encrypted) => {
    dispatch(actions.setConnectionDetails(server, port, user, password, encrypted));
  }
}))(ConnectionDetails);
