import React, { useCallback } from 'react';
import Grid from '@material-ui/core/Grid';
import MdiQuickAccessButton from './MdiQuickAccessButton';

const MdiQuickAccessGrid = (props) => {
  const {
    mdiFavorites,
    onClickItem,
  } = props;

  const quickAccessCount = mdiFavorites.reduce((count, fav) => fav.name ? ++count : count, 0);

  const handleClickItem = useCallback((cmd) => {
    if(onClickItem){
      onClickItem(cmd);
    }
  }, [onClickItem]);

  return(
    <Grid container spacing={4}>
      { mdiFavorites &&
          mdiFavorites.map(
            ({cmd, name}, idx) => {
              if(name && name.length > 0){
              
              return (
                <Grid item key={idx} xs={4}>
                  <MdiQuickAccessButton
                    name={name}
                    cmd={cmd}
                    onClick={handleClickItem}
                    size="large"
                    fullWidth={true}
                    variant="contained"
                    color="secondary"
                  />
                </Grid>
              )
              } else return null
            }
          )
      }
      {
        [...Array(Math.max(0, 15 - quickAccessCount))].map((i, idx) => 
          {
            return (
              <Grid item key={idx} xs={4}>
                <MdiQuickAccessButton
                  disabled={true}
                  name={""}
                  cmd={""}
                  onClick={handleClickItem}
                  size="large"
                  fullWidth={true}
                  variant="contained"
                  color="secondary"
                />
              </Grid>
            )
          }
        )
      }
    </Grid>
  )
};

export default React.memo(MdiQuickAccessGrid)
