import { GET_CMD, 
         WATCH_CMD, 
         UNWATCH_CMD, 
         PUT_CMD, 
         LIST_GET_CMD, 
         LIST_PUT_CMD, 
         LOGIN_ID
       } from './constants';

export const get = (id, name, data={}, meta) => ({ id, name, command: GET_CMD, ...data, meta });
export const watch = (id, name, data={}, meta) => ({ id, name, command: WATCH_CMD, ...data, meta });
export const unwatch = (id, name, meta) => ({ id, name, command: UNWATCH_CMD, meta });
export const put = (id, name, data, meta) => ({ id, name, command: PUT_CMD, ...data, meta });
export const listGet = (id, meta) => ({ id, command: LIST_GET_CMD, meta });
export const listPut = (id, meta) => ({ id, command: LIST_PUT_CMD, meta});

export const login = (user, password) => ({ id: LOGIN_ID, user, password, date: new Date().toISOString() });
