import produce from 'immer';
import actions from './actions';
import rockhopperActions from '../rockhopper/actions';
import { handleActions, combineActions } from 'redux-actions';
import { MAX_POPUPS, MESSAGE_LOG_KEY } from './constants';
import { BASIC } from '../rockhopper/constants';
// import i18n from '../../i18n';

const storedMessages = sessionStorage.getItem(MESSAGE_LOG_KEY);

const defaultState = {
  enableKeyboardJogging: true,
  dialect: "industry",
  language: "en",
  mdiSortKey: "time",
  mdiText: "",
  mdiShowUniqueHistoryOnly: false,
  messages: storedMessages ? JSON.parse(storedMessages) : [],
  messageBadges: {
    debug: [],
    info: [],
    warning: [],
    error: []
  },
  popups: [],
  isEditingOverlay: false,
  configOverlay: {},
  isEditingCompensation: false,
  compensation: {},
  axisSelectJog: {
    axis: 'X',
    digitIndex: 1,
  },
  dro: {
    display: "position",
  },
  jog: {
    display: "position",
  }
};

export default handleActions({
  [actions.enableKeyboardJogging]: produce((state,action) => {
    state.enableKeyboardJogging = true;
  }),
  [actions.disableKeyboardJogging]: produce((state,action) => {
    state.enableKeyboardJogging = false;
  }),
  [actions.changeLanguage]: produce((state,action) => {
    // i18n.changeLanguage(action.payload);
    state.language = action.payload;
  }),
  [actions.changeDialect]: produce((state,action) => {
    state.dialect = action.payload;
  }),
  [actions.setMdiSortKey]: produce((state,action) => {
    state.mdiSortKey = action.payload;
  }),
  [actions.setMdiText]: produce((state,action) => {
    state.mdiText = action.payload;
  }),
  [actions.setMdiShowUniqueHistoryOnly]: produce((state,action) => {
    state.mdiShowUniqueHistoryOnly = action.payload;
  }),
  [actions.clearMessageBadge]: produce((state, action) => {
    state.messageBadges[action.payload] = [];
  }),
  [combineActions(
    rockhopperActions.messages.debug,
    rockhopperActions.messages.info,
    rockhopperActions.messages.warning,
    rockhopperActions.messages.error
  )]: produce((state, action) => {
    state.messages.unshift(action.payload);
    switch(action.type) {
      case rockhopperActions.messages.debug.toString():
        state.messageBadges.debug.push(action.payload);
        break;
      case rockhopperActions.messages.info.toString():
        state.messageBadges.info.push(action.payload);
        break;
      case rockhopperActions.messages.warning.toString():
        state.messageBadges.warning.push(action.payload);
        break;
      case rockhopperActions.messages.error.toString():
        state.messageBadges.error.push(action.payload);
        break;
      default:
        break;
    }
  }),
  [actions.popup.show]: produce((state, action) => {
    state.popups.splice(0, 0, action.payload);
    if(state.popups.length > MAX_POPUPS) {
      const numToRemove = state.popups.length-MAX_POPUPS;
      let removed = 0;
      for(let i = state.popups.length-1; i >= 0 && removed < numToRemove; i--) {
        if(state.popups[i].dismiss === BASIC) {
          state.popups.splice(i, 1);
          removed++;
        }
      }
    }
  }),
  [actions.popup.hide]: produce((state, action) => {
    const index = state.popups.findIndex((msg) => msg.id === action.payload.id);
    if(index > -1) {
      state.popups.splice(index, 1);
    }
  }),
  [actions.overlay.enableEditing]: produce((state, action) => {
    state.isEditingOverlay = true;
    state.configOverlay = action.payload;
  }),
  [actions.overlay.disableEditing]: produce((state, action) => {
    state.isEditingOverlay = false;
  }),
  [actions.overlay.deleteSection]: produce((state, action) => {
    delete state.configOverlay[action.payload];
  }),
  [actions.overlay.set]: produce((state, action) => {
    state.configOverlay = action.payload;
  }),
  
  [actions.compensation.enableEditing]: produce((state, action) => {
    state.isEditingCompensation = true;
    state.compensation = action.payload;
  }),
  [actions.compensation.disableEditing]: produce((state, action) => {
    state.isEditingCompensation = false;
  }),
  [actions.compensation.deleteSection]: produce((state, action) => {
    delete state.compensation[action.payload];
  }),
  [actions.compensation.set]: produce((state, action) => {
    state.compensation = action.payload;
  }),

  [actions.axisSelectJog.setAxis]: produce((state, action) => {
    state.axisSelectJog.axis = action.payload;
  }),
  [actions.axisSelectJog.incrementAxis]: produce((state, action) => {
    const {inc, axisSelectJogAxis, trajCoords} = action.payload;
    const currIdx = trajCoords.indexOf(axisSelectJogAxis);
    const newIdx = Math.max(0, Math.min(4,currIdx + inc));
    state.axisSelectJog.axis = trajCoords[newIdx]
  }),
  [actions.axisSelectJog.setDigitIndex]: produce((state, action) => {
    state.axisSelectJog.digitIndex = action.payload;
  }),
  [actions.axisSelectJog.incrementDigitIndex]: produce((state, action) => {
    if(state.axisSelectJog.digitIndex === null && action.payload === 1){
      state.axisSelectJog.digitIndex = 1
    }
    else{
      state.axisSelectJog.digitIndex = Math.max(1, Math.min(4,state.axisSelectJog.digitIndex + action.payload));
    }
  }),
  [actions.dro.setDisplay]: produce((state, action) => {
    state.dro.display = action.payload;
  }),
  [actions.jog.setDisplay]: produce((state, action) => {
    state.jog.display = action.payload;
  }),
}, defaultState);
