import { eventChannel } from 'redux-saga';

// The FileQueue singleton allows components to trigger a file
// upload by calling fileQueue.addFile(path, fileType, data)
// path is a relative path to a filename on the server
// where fileType is a string,
// and data is either a string or a file object.
//
// Since File objects aren't encodable they shouldn't be
// dispatched in an action. The fileQueue uses an eventChannel
// to bypass needing to dispatch an action, while allowing
// a saga to efficiently upload files in the background
// and properly dispatch events necessary for updating the 
// file upload progress in the UI.
export class FileQueue {
  constructor() {
    this.channel = eventChannel( (emit) => {
      // public method for adding a file to the queue
      this.addFile = (path, fileType, data, overwrite=false) => {
        emit({ path, fileType, data, overwrite });
      };

      return () => {};
    });
  }
}

const singletonFileQueue = new FileQueue();

export default singletonFileQueue;
