import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  timestamp: {
    fontWeight: "bold",
    fontSize: ".7em"
  },
  key: {
    fontWeight: "bold",
    fontSize: ".7em"
  },
  ul: {
    listStyleType: "none",
    paddingLeft: theme.spacing(1)
  },
  value: {
    fontSize: ".7em",
    fontStyle: "italic"
  }
}));

const MessageDetails = (props) => {
  const {
    msg
  } = props;

  const classes = useStyles();

  const displayObjectRecurse = (obj) => {
    if(typeof(obj) === 'object' && obj !== null) {
      if(Array.isArray(obj)) {
        // do array
        return <ul className={classes.ul}>
          {
            obj.map((_, i) => (
              <li key={i}><span className={classes.key}>{i}</span> {displayObjectRecurse(obj[i])}</li>
            ))
          }
        </ul>;
      } else {
        // do object
        return <ul className={classes.ul}>
          {
            Object.keys(obj).map((key) => (
              <li key={key}><span className={classes.key}>{key}</span> {displayObjectRecurse(obj[key])}</li>
            ))
          }
        </ul>;
      }
    }
    return <span className={classes.value}>{obj}</span>;
  };

  return <div>
    <span className={classes.timestamp}>{(new Date(msg.timestamp)).toLocaleString()}</span>
    { displayObjectRecurse(msg.details) }
  </div>;
};
export default MessageDetails;
