import React, { useCallback, useState, useRef } from 'react';

import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import UploadIcon from '@material-ui/icons/Publish';
import CircularProgress from '@material-ui/core/CircularProgress';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Tooltip from '@material-ui/core/Tooltip';

import useTranslation from '../hooks/useTranslation';
import GCodeFileBrowserDialog from '../containers/GCodeFileBrowserDialog';


const useStyles = makeStyles((theme) => ({
  realDropBox: {
    cursor: "pointer",
    zIndex: 100,
    width: "100%",
    height: "100%",
    position: "absolute"
  },
  dropBox: {
    cursor: "pointer",
    borderWidth: "2px",
    borderStyle: "dashed",
    borderColor: theme.palette.primary.main,
    margin: theme.spacing(2),
    width: "calc(100% - " + theme.spacing(4) + ")",
    position: "relative"
  },
  dropBoxOver: {
    borderStyle: "solid",
    backgroundColor: theme.palette.primary.light
  },
  fileList: {
    position: "relative",
    margin: theme.spacing(2),
    width: "calc(100% - " + theme.spacing(4) + ")",
    overflow: "scroll"
  },
  fileItem: {
    position: "absolute"
  },
  fileContainer: {
    margin: theme.spacing(2),
    width: "calc(100% - 32px)"
  },
  fileLabel: {
    fontWeight: "bold",
  },
  fileName: {
    whiteSpace: "nowrap",
    overflow: "hidden",
    textOverflow: "ellipsis"
  },
  fileButton: {
    width: "100%",
    justifyContent: "left"
  },
  filter: {
    width: "100%"
  },
  fileDialog: {
    height: "min(600px, 100%)",
    width: "min(500px, 100%)"
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1)
  },
  percentage: {
    marginLeft: theme.spacing(1)
  },
  usbButton: {
    color: theme.palette.primary.main,
    width: "100%",
    justifyContent: "left"
  }
}));

const FileHeaderActions = (props) => {
  const { t } = useTranslation();
  const {
    file, 
    // fileList,
    onAddFile,
    onCancel,
    onCancelAll,
    onConfirm,
    onConfirmAll,
    onWatchGcodeMap,
    onUnwatchGcodeMap,
    // onFileDelete,
    // onFileOpen,
    queuedFiles = [],
    needsOverwritePermission
  } = props;
  const inputRef = useRef(null);
  const handleFileSelect = (e) => {
    for(let file of e.target.files) {
      const path = file.name;
      const data = file;
      onAddFile(path, data);
    }
    e.target.value = "";
  };

  const [ fileDialogOpen, setFileDialogOpen ] = useState(false);
  // const [ selected, setSelected ] = useState({});
  // const filteredFileList = useMemo(() => fileList.filter(({ file }) => file.toLowerCase().includes(filter.toLowerCase())), [filter, fileList]);

  // const handleClearFilter = useCallback((e) => {
  //   setFilter("");
  // }, [ setFilter ]);
  // const handleFilterChange = useCallback((e) => {
  //   setFilter(e.target.value);
  // }, [ setFilter ]);
  const handleCloseFileDialog = useCallback((e) => {
    setFileDialogOpen(false);
    onUnwatchGcodeMap();
  }, [ setFileDialogOpen ]);

  const handleOpenFileDialog = useCallback((e) => {
    setFileDialogOpen(true);
    onWatchGcodeMap();
  }, [ setFileDialogOpen ]);

  const handleClick = useCallback((e) => {
    inputRef.current.click();
  }, [ inputRef ]);

  // const handleSelect = useCallback((e) => {
  //   if(e.target.checked) {
  //     setSelected({ ...selected, [e.target.value]: true });
  //   } else {
  //     setSelected({ ...selected, [e.target.value]: false });
  //   }
  // }, [ selected, setSelected ]);

  // const handleDeleteSelected = useCallback((e) => {
  //   if(onFileDelete) {
  //     const deleteFiles = Object.keys(selected).filter((path) => selected[path]);
  //     deleteFiles.forEach((path) => {
  //       onFileDelete(path);
  //     });
  //     setSelected({});
  //   }
  // }, [ onFileDelete, selected, setSelected ]);

  const handleCancel = useCallback((e) => {
    if(onCancel) {
      onCancel(needsOverwritePermission[0].path);
    }
  }, [ onCancel, needsOverwritePermission ]);

  const handleConfirm = useCallback((e) => {
    if(onConfirm) {
      onConfirm(needsOverwritePermission[0].path);
    }
  }, [ onConfirm, needsOverwritePermission ]);

  const queuedFileList = Object.values(queuedFiles).map((file, index) => ({ ...file, index }));
  queuedFileList.sort((a,b) => { return b.progress-a.progress });
  //const files = queuedFileList.map((file, index) => ({ ...file, index }));
  const fileUploadProgress = queuedFileList.reduce((accum, current) => accum + 1./queuedFileList.length*current.progress, 0)*100;

  const classes = useStyles();

  return <>
  <Tooltip placement="top" title={file || t("noFileLoaded")}>
    <Button onClick={handleOpenFileDialog}><span className={classes.fileName}>{ file || t("noFileLoaded") }</span></Button>
  </Tooltip>
  <GCodeFileBrowserDialog
    open={fileDialogOpen}
    onClose={handleCloseFileDialog}
  />

  <Tooltip placement="top" title="Upload Files"><Button onClick={handleClick}><UploadIcon/></Button></Tooltip>
  <CircularProgress value={fileUploadProgress} variant="determinate" size="1em"/>
  { fileUploadProgress > 0 ? <span className={classes.percentage}>{fileUploadProgress.toFixed(2) + "%"}</span> : null }
  <div style={{ display: "none" }}><input onChange={handleFileSelect} type="file" multiple ref={inputRef}/></div>
  <Dialog open={needsOverwritePermission.length > 0}>
    <DialogTitle>Overwrite existing files?</DialogTitle>
    <DialogContent>
      <DialogContentText>
        { needsOverwritePermission.length > 0 ? needsOverwritePermission[0].path : "" } exists on the server{ needsOverwritePermission.length > 1 ? " (and " + needsOverwritePermission.length + " other file" + (needsOverwritePermission.length > 2 ? "s" : "") +")" : "" }. Overwrite?
      </DialogContentText>
      <DialogActions>
        <Button onClick={handleCancel} color="secondary">Cancel</Button>
        { queuedFileList.length > 1 ? <Button onClick={onCancelAll} color="secondary">Cancel All</Button> : null }
        <Button onClick={handleConfirm} color="primary">Overwrite</Button>
        { queuedFileList.length > 1 ?  <Button onClick={onConfirmAll} color="primary">Overwrite All</Button> : null }
      </DialogActions>
    </DialogContent>
  </Dialog>
  </>;
};
export default FileHeaderActions;
