export const lineLineIntersection = (x1,y1,x2,y2,x3,y3,x4,y4) => {
  const t = ((x1-x3)*(y3-y4)-(y1-y3)*(x3-x4))/((x1-x2)*(y3-y4)-(y1-y2)*(x3-x4));

  return { x: x1+t*(x2-x1), y: y1+t*(y2-y1) };
};

export const centerOfCircle = (x1,y1,x2,y2,x3,y3) => {
  const mx1 = (x1+x2)*.5;
  const my1 = (y1+y2)*.5;

  const mx2 = (x2+x3)*.5;
  const my2 = (y2+y3)*.5;

  const dx1 = (x2-x1);
  const dy1 = (y2-y1);

  const dx2 = (x3-x2);
  const dy2 = (y3-y2);

  const vx1 = -dy1;
  const vy1 = dx1;

  const vx2 = -dy2;
  const vy2 = dx2;

  const px1 = mx1+vx1;
  const py1 = my1+vy1;

  const px2 = mx2+vx2;
  const py2 = my2+vy2;

  return lineLineIntersection(mx1,my1,px1,py1,mx2,my2,px2,py2);
};

export const ccwAngleBetween = (angle1, angle2) => {
  return ((angle2-angle1)%(2*Math.PI) + 2*Math.PI)%(2*Math.PI);
};

export const cwAngleBetween = (angle1, angle2) => {
  let angle = ccwAngleBetween(angle1,angle2);
  return angle-2*Math.PI;
};

// in radians
export const shortAngleBetween = (angle1, angle2) => {
  let angle = ccwAngleBetween(angle1,angle2);
  if(angle > Math.PI) {
    angle -= 2*Math.PI;
  }
  return angle;
};

// in radians
export const longAngleBetween = (angle1, angle2) => {
  let angle = ccwAngleBetween(angle1,angle2);
  if(angle < Math.PI) {
    angle -= 2*Math.PI;
  }
  return angle;
};

export const arcDetails  =  (x1,y1, x2,y2, x3,y3) => {
  const c = centerOfCircle(x1,y1,x2,y2,x3,y3);
  const localX1 = x1-c.x;
  const localY1 = y1-c.y;
  const localX2 = x2-c.x;
  const localY2 = y2-c.y;
  const localX3 = x3-c.x;
  const localY3 = y3-c.y;
  const r = Math.sqrt(localX1*localX1+localY1*localY1);
  let angle1 = Math.atan2(localY1, localX1);
  let angle2 = Math.atan2(localY2, localX2);
  let angle3 = Math.atan2(localY3, localX3);

  const relativeAngle2 = ccwAngleBetween(angle1, angle2);
  const relativeAngle3 = ccwAngleBetween(angle1, angle3);

  let angle = relativeAngle3;
  if(relativeAngle2 > relativeAngle3) {
    angle = cwAngleBetween(angle1, angle3);
  }

  const startAngle = angle1;
  const endAngle = angle1+angle;

  return {
    c, r, startAngle, endAngle, angle
  };
};
