import React from 'react';
import clsx from 'clsx';

import { lighten, makeStyles } from '@material-ui/core/styles';
import Toolbar from '@material-ui/core/Toolbar';
import Typography from '@material-ui/core/Typography';
import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';
import DeleteIcon from '@material-ui/icons/Delete';
import FilterListIcon from '@material-ui/icons/FilterList';

import useTranslation from '../hooks/useTranslation';

const useToolbarStyles = makeStyles((theme) => ({
  root: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1)
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85)
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark
        },
  title: {
    flex: "1 1 100%"
  }
}));

const MdiFavoritesTableToolbar = (props) => {
  const { 
    onDelete,
    onOpenFilterModal,
    selected,
    filter,
  } = props;
  const numSelected = selected ? selected.size : 0;
  
  const { t } = useTranslation();
  const classes = useToolbarStyles();

  const handleDelete = () => {
    if(onDelete){
      onDelete();
    }
  }

  const handleOpenFilterModal = () => {
    if(onOpenFilterModal){
      onOpenFilterModal();
    }
  }
  
  return (
    <Toolbar
      className={clsx(classes.root, {
        [classes.highlight]: numSelected > 0
      })}
    >
      {numSelected > 0 ? (
        <Typography
          className={classes.title}
          color="inherit"
          variant="subtitle1"
          component="div"
        >
          {numSelected} selected
        </Typography>
      ) : (
        <Typography
          className={classes.title}
          variant="h6"
          id="tableTitle"
          component="div"
        >
        </Typography>
      )}
      {
        filter && filter.key ? (
          <Typography
          className={classes.title}
          color="inherit"
          variant="subtitle1"
          component="div"
          >
            {t("filteringBy") + ":" + filter.key + "=" + filter.value}
          </Typography>
        ) : (
        <Typography
          className={classes.title}
          variant="h6"
          id="tableTitle"
          component="div"
        >
        </Typography>
        )
      }

      {numSelected > 0 ? (
        <Tooltip title={t("delete")}>
          <IconButton aria-label="delete"
            onClick={handleDelete}
          >
            <DeleteIcon />
          </IconButton>
        </Tooltip>
      ) : (
        <Tooltip title={t("filterList")}>
          <IconButton aria-label="filter list"
            onClick={handleOpenFilterModal}
          >
            <FilterListIcon />
          </IconButton>
        </Tooltip>
      )}
    </Toolbar>
  );
};

export default MdiFavoritesTableToolbar;
