import React from 'react';
import classnames from 'classnames';

import { useTheme, makeStyles } from '@material-ui/core/styles';

const useStyles = makeStyles((theme) => ({
  primary: {
    color: theme.palette.primary.main,
    lineHeight: "20px"
  },
  secondary: {
    color: theme.palette.secondary.main,
    lineHeight: "20px"
  },
  extra: {
    lineHeight: "20px",
    marginBottom: theme.spacing(1),
    marginLeft: theme.spacing(1),
    ...theme.typography.statusItem,
  }
}));

const TwoLineStatusItem = (props) => {
  const {
    color,
    children
  } = props;

  const classes = useStyles();
  const theme = useTheme();

  const hasClass = !!classes[color];
  const styleObj = {};

  if(!hasClass) {
    if(color) {
      styleObj.color = color;
    } else {
      styleObj.color = theme.palette.common.white;
    }
  }

  return <span className={classnames(classes[color], classes.extra)} style={styleObj}>
  {children}
  </span>;
};
export default TwoLineStatusItem;
