import React from 'react';
import { arcDetails } from './util/math';
import { moveTo, arcTo, lineTo } from './util/path';
import { useTheme } from '@material-ui/core/styles';
import colorInterpolate from 'color-interpolate';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  segment: {
  }
}));

const SegmentedArcGauge = (props) => {
  const {
    x1,
    y1,
    x2,
    y2,
    x3,
    y3,
    totalSegments = 100,
    segmentRatio = 1,
    segments = 0,
    segmentWidth = 20,
    paddingStart = 0,
    paddingEnd = 0,
    every
  } = props;

  const theme = useTheme();

  const classes = useStyles();

  const missingPoints = (typeof(x1) === "undefined" ||
                         typeof(y1) === "undefined" ||
                         typeof(x2) === "undefined" ||
                         typeof(y2) === "undefined" ||
                         typeof(x3) === "undefined" ||
                         typeof(y3) === "undefined");
  const {
    c,
    r, 
    startAngle,
    endAngle
  } = missingPoints ? props : arcDetails(x1,y1,x2,y2,x3,y3);

  const angle = endAngle-startAngle;

  const segmentAngle = (Math.abs(angle)-paddingStart-paddingEnd)/totalSegments;
  const tickAngle = angle > 0 ? segmentAngle*segmentRatio : -segmentAngle*segmentRatio;
  const paddingStartAngle = angle > 0 ? paddingStart : -paddingStart;
  const paddingEndAngle = angle > 0 ? paddingEnd : -paddingEnd;
  const allAngles = [ ];
  const a = angle-tickAngle-paddingStartAngle-paddingEndAngle;
  for(let i = 0; i < totalSegments; i++) {
    allAngles.push(paddingStartAngle + startAngle + i*a/(totalSegments-1));
  }

  const angleToElement = (className, colorFunc) => (angle, index) => {
    let width = segmentWidth;
    if(every) {
      every.forEach((e) => {
        if((index % e.segments) === 0) {
          width = e.width;
        }
      });
    }
    const fraction = (segments > index && Math.floor(segments)-index < .00001) ? segments-index : 1;
//    const tickAngle1 = Math.min(tickAngle, segmentAngle*fraction);
    const tickAngle1 = tickAngle*fraction;
    const tickAngle2 = tickAngle;
    if(Math.abs(tickAngle1) < Math.abs(tickAngle)) {
      const largeArc1 = tickAngle1 > Math.PI ? 1 : 0;
      const sweepFlag1 = tickAngle1 > 0 ? 1 : 0;
      const largeArc2 = (tickAngle2-tickAngle1) > Math.PI ? 1 : 0;
      const sweepFlag2 = (tickAngle2-tickAngle1) > 0 ? 1 : 0;

      const smallR = r-width;

      const cosTickAngle1 = Math.cos(tickAngle1);
      const sinTickAngle1 = Math.sin(tickAngle1);
      const tickX1_1 = r*cosTickAngle1;
      const tickY1_1 = r*sinTickAngle1;
      const tickX2_1 = smallR*cosTickAngle1;
      const tickY2_1 = smallR*sinTickAngle1;

      const cosTickAngle2 = Math.cos(tickAngle2);
      const sinTickAngle2 = Math.sin(tickAngle2);
      const tickX1_2 = r*cosTickAngle2;
      const tickY1_2 = r*sinTickAngle2;
      const tickX2_2 = smallR*cosTickAngle2;
      const tickY2_2 = smallR*sinTickAngle2;

      const pathInstructions1 = [ moveTo(r, 0),
                                 arcTo(r, r, 0, largeArc1, sweepFlag1, tickX1_1, tickY1_1),
                                 lineTo(tickX2_1, tickY2_1),
                                 arcTo(smallR, smallR, 0, largeArc1, 1-sweepFlag1, smallR, 0),
                                 "Z" ];

      const pathInstructions2 = [ moveTo(tickX1_1, tickY1_1),
                                 arcTo(r, r, 0, largeArc2, sweepFlag2, tickX1_2, tickY1_2),
                                 lineTo(tickX2_2, tickY2_2),
                                 arcTo(smallR, smallR, 0, largeArc2, 1-sweepFlag2, tickX2_1, tickY2_1),
                                 "Z" ];
                                 
      let fill1;
      let fill2;
      if(colorFunc) {
        fill1 = colorFunc(index);
        fill2 = colorFunc(index+1);
      }
      return <g key={index}>
      <path d={pathInstructions1.join(" ")} fill={fill1} className={className} transform={"rotate(" + (angle*180/Math.PI) + ")"}/>
      <path d={pathInstructions2.join(" ")} fill={fill2} className={className} transform={"rotate(" + (angle*180/Math.PI) + ")"}/>
      </g>
    } else {
      const largeArc = tickAngle > Math.PI ? 1 : 0;
      const sweepFlag = tickAngle > 0 ? 1 : 0;
      const cosTickAngle = Math.cos(tickAngle);
      const sinTickAngle = Math.sin(tickAngle);
      const tickX1 = r*cosTickAngle
      const tickY1 = r*sinTickAngle
      const smallR = r-width;
      const tickX2 = smallR*cosTickAngle
      const tickY2 = smallR*sinTickAngle
      const pathInstructions = [ moveTo(r, 0),
                                 arcTo(r, r, 0, largeArc, sweepFlag, tickX1, tickY1),
                                 lineTo(tickX2, tickY2),
                                 arcTo(smallR, smallR, 0, largeArc, 1-sweepFlag, smallR, 0),
                                 "Z" ];
                                 
      let fill;
      if(colorFunc) {
        fill = colorFunc(index);
      }
      return <path key={index} d={pathInstructions.join(" ")} fill={fill} className={className} transform={"rotate(" + (angle*180/Math.PI) + ")"}/>
    }
  };

  const colorMap = colorInterpolate([ theme.palette.primary.main, theme.palette.secondary.main ]);
  const colorFunc = (index) => {
    if(index >= segments) {
      return theme.palette.grey[50];
    }
    return colorMap(index/(totalSegments-1));
  };
  const segmentElements = allAngles.map(angleToElement(classes.segment, colorFunc));

  return (
  <g transform={"translate(" + c.x + "," + c.y + ")"}>
  {segmentElements}
  </g>
  );
};
export default SegmentedArcGauge;
