import React, { useState } from 'react';

import { lighten, makeStyles } from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Tooltip from '@material-ui/core/Tooltip';
import HelpIcon from '@material-ui/icons/Help';

import KeyboardCaptureDialog from './KeyboardCaptureDialog';
import MdiFavoritesTableBody from './MdiFavoritesTableBody';
import MdiFavoritesTableToolbar from './MdiFavoritesTableToolbar';
import SubmittableDialog from './SubmittableDialog';
import { SORT_ORDER } from '../modules/pentaui/constants';
import useTranslation from '../hooks/useTranslation';


const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  paper: {
    width: '100%',
    marginBottom: theme.spacing(2),
  },
  table: {
    // minWidth: 750,
  },
  visuallyHidden: {
    border: 0,
    clip: 'rect(0 0 0 0)',
    height: 1,
    margin: -1,
    overflow: 'hidden',
    padding: 0,
    position: 'absolute',
    top: 20,
    width: 1,
  },
  rootToolbar: {
    paddingLeft: theme.spacing(2),
    paddingRight: theme.spacing(1)
  },
  highlight:
    theme.palette.type === "light"
      ? {
          color: theme.palette.secondary.main,
          backgroundColor: lighten(theme.palette.secondary.light, 0.85)
        }
      : {
          color: theme.palette.text.primary,
          backgroundColor: theme.palette.secondary.dark
        },
  title: {
    flex: "1 1 100%"
  }
}));


const MdiFavoritesTable = (props) => {
  const {
    favorites,
    favoritesMap,
    headCells,
    placeholderText,
    onNameFavorite,
    onDelete,
    onClickItem,

    jogKeys,
    jogModifiers,
    shortcutsMap,
    onModifyFavorite,
  } = props;

  const { t } = useTranslation();
  const classes = useStyles();
  const [selected, setSelected] = React.useState(new Set());
  const [sortOrder, setSortOrder] = React.useState(SORT_ORDER.ASCENDING);
  const [sortKey, setSortKey] = React.useState(headCells[0].id);
  const [dialogOpen, setDialogOpen] = React.useState(false);
  const [filter, setFilter] =  React.useState("")
  // const [filterKey, setFilterKey] =  React.useState(headCells[0].id)
  const filterKey = headCells[0].id;
  const [ captureDialogState, setCaptureDialogState ] = useState({open: false, category: null, key: "", value: "", modifiersOnly: false, singleKeyOnly: false});

  const handleSortRequest = (key) => {
    const isAscending = key === sortKey && sortOrder === SORT_ORDER.ASCENDING;
    setSortOrder(isAscending ? 'desc' : 'asc');
    setSortKey(key);
  }

  const handleClickItemCheckbox = (event, key) => {
    let newSet = new Set(selected);
    if(selected.has(key)){
      newSet.delete(key);
      setSelected(newSet);
    }
    else{
      newSet.add(key);
      setSelected(newSet);
    }
  };

  const handleSelectAll = () => {
    if(selected.size < favorites.length){
      let newSet = new Set(selected);
      favorites.forEach((favorite) => newSet.add(favorite.cmd))
      setSelected(newSet);
    }
    else{
      setSelected(new Set());
    }
  }

  const handleNameItem = (e, item) => {
    onNameFavorite(item.cmd, e.target.value)
  }

  const handleDelete = () => {
    onDelete(selected);
  }

  const handleCloseDialog = () => {
    setDialogOpen(false);
  };

  const handleSubmitDialog = (value) => {
    setFilter(value)
    setDialogOpen(false);
  };

  const handleOpenCaptureDialog = (mdiFav) => {
    const newDialogState = { open: true, title: mdiFav.cmd, key:mdiFav.cmd, value: mdiFav.keybind || '', category: "mdi", modifiersOnly: false, singleKeyOnly: false };
    setCaptureDialogState(newDialogState);
  };

  const handleDeleteKeybind = (cmd) => {
    const oldFav = favoritesMap.get(cmd);
    onModifyFavorite({cmd: cmd, name: oldFav.name, time: oldFav.time, keybind: ""});
  };

  const handleSubmitCaptureDialog = (newShortcut) => {
    const oldFav = favoritesMap.get(captureDialogState.key);
    onModifyFavorite({cmd: captureDialogState.key, name: oldFav.name, time: oldFav.time, keybind: newShortcut});
    setCaptureDialogState({...captureDialogState, open: false});
  };


  return (
    <div>
    <TableContainer>
      <Table
        className={classes.table}
        aria-labelledby="tableTitle"
        aria-label="favorites table"
        stickyHeader
      >
      <TableHead>
        <TableRow>
          <TableCell padding="checkbox">
            <Checkbox
              onClick={handleSelectAll}
              // indeterminate={numSelected > 0 && numSelected < rowCount}
              // checked={rowCount > 0 && numSelected === rowCount}
              // onChange={onSelectAllClick}
              inputProps={{ 'aria-label': 'select all favorites' }}
            />
          </TableCell>
          {headCells.map((headCell) =>
          {
            return (
              <TableCell
                key={headCell.id}
                align={'left'}
                // align={headCell.numeric ? 'right' : 'left'}
                padding={headCell.disablePadding ? 'none' : 'default'}
                sortDirection={sortKey === headCell.id ? sortOrder : false}
              >

                <TableSortLabel
                  active={sortKey === headCell.id}
                  direction={sortKey === headCell.id ? sortOrder : 'asc'}
                  onClick={(e) => handleSortRequest(headCell.id)}
                >
                  {headCell.label}
                  {sortKey === headCell.id ? (
                    <span className={classes.visuallyHidden}>
                      {sortOrder === SORT_ORDER.DESCENDING ? 'sorted descending' : 'sorted ascending'}
                    </span>
                  ) : null}
                </TableSortLabel>
                {headCell.tooltip && <Tooltip placement="right" title={<span>{headCell.tooltip}</span>}><HelpIcon/></Tooltip>}
              </TableCell>
            )
          }
          )}
        </TableRow>
      </TableHead>

      <MdiFavoritesTableBody
        favorites={favorites}
        sortOrder={sortOrder}
        sortKey={sortKey}
        filter={filter}
        filterKey={filterKey}
        placeholderText={placeholderText}
        onSelectItem={handleClickItemCheckbox}
        onNameItem={handleNameItem}
        onClickItem={onClickItem}
        onClickEditKeybind={handleOpenCaptureDialog}
        onClickDeleteKeybind={handleDeleteKeybind}
        selected={selected}
      />
    </Table>
  </TableContainer>
  <MdiFavoritesTableToolbar
    onDelete={() => handleDelete(selected)}
    onOpenFilterModal={() => setDialogOpen(true)}
    filter={filter}
    selected={selected}
    numSelected={selected.length}
  />
  <SubmittableDialog
    open={dialogOpen}
    value={""}
    onSubmit={handleSubmitDialog}
    onClose={handleCloseDialog}
    title={t("filterFavorites")}
    textFieldLabel={t("Command")}
    submitBtnLabel={t("Filter")}
    cancelBtnLabel={t("Cancel")}
  />
  <KeyboardCaptureDialog
    open={captureDialogState.open}
    key={captureDialogState.key}
    value={captureDialogState.value}
    modifiersOnly={captureDialogState.modifiersOnly}
    singleKeyOnly={captureDialogState.singleKeyOnly}
    reservedKeys={new Set(Object.values(jogKeys))}
    reservedCombinations={new Set(Object.keys(shortcutsMap).concat(Object.values(jogModifiers)))}
    onSubmit={handleSubmitCaptureDialog}
    onClose={() => { setCaptureDialogState({...captureDialogState, open: false}) } }
    title={t("Keybinding") + ": " + t(captureDialogState.title)}
    submitBtnLabel={t("Set")}
    cancelBtnLabel={t("Cancel")}
  />
    </div>
  )
};

export default React.memo(MdiFavoritesTable)
