import React from 'react';
import StatusItem from './StatusItem';
import Hidden from '@material-ui/core/Hidden';
import Tooltip from '@material-ui/core/Tooltip';
import useTranslation from '../hooks/useTranslation';

//import { makeStyles } from '@material-ui/styles';

//const useStyles = makeStyles((theme) => ({
//}));

// one code from each line will ever be active
const importantCodes = [ "G0", "G1", "G2", "G3",
                         "G17", "G18", "G19", 
                         "G20", "G21", 
                         "G43", "G49",
                         "G54", "G55", "G56", "G57", "G58", "G59", "G59.1", "G59.2", "G59.3",
                         "G90", "G91",
                         "G93", "G94",
                         "M3", "M4", "M5",
                         "M7", "M8", "M9",
                         "M428", "M429", "M430" ];
const ActiveCodesStatusItem = (props) => {
  const {
    activeCodes = ["G1", "G17", "G20" ]
  } = props;

  const { t } = useTranslation();

//  const classes = useStyles();

  const onlyImportantActiveCodes = activeCodes.filter((code) => importantCodes.indexOf(code) !== -1);

  return <StatusItem>
    <Hidden mdDown>
     <Tooltip title={activeCodes.join(" ")} arrow enterTouchDelay={0}>
       <span>{ onlyImportantActiveCodes.join(" ") }</span>
     </Tooltip>
   </Hidden>
   <Hidden lgUp>
     <Tooltip title={activeCodes.join(" ")} arrow enterTouchDelay={0}>
       <span>{t("activeCodes")}</span>
     </Tooltip>
   </Hidden>
  </StatusItem>
};

export default ActiveCodesStatusItem;
