import { connect } from 'react-redux';
import MessageBadgesStatusItem from '../components/MessageBadgesStatusItem';

import { selectMessageBadges } from '../modules/pentaui/selectors';
import actions from '../modules/pentaui/actions';

export default connect( 
(state, ownProps) => {
  return { 
    messageBadges: selectMessageBadges(state)
  }
}, 
(dispatch, ownProps) => ({ 
  onClear: (type) => {
    dispatch(actions.clearMessageBadge(type));
  }
}))(MessageBadgesStatusItem);
