import { connect } from 'react-redux';
import Switch from '@material-ui/core/Switch';
import pentaActions from '../modules/pentaui/actions';
import { selectEnableKeyboardJogging } from '../modules/pentaui/selectors';
import { selectAxes } from '../modules/rockhopper/selectors';
import rockhopperActions from '../modules/rockhopper/actions';

export default connect( 
(state, ownProps) => ({
  checked: selectEnableKeyboardJogging(state),
  axes: selectAxes(state)
}), 
(dispatch, ownProps) => ({ 
  onEnable: () => {
    dispatch(pentaActions.enableKeyboardJogging());
  },
  onDisable: () => {
    dispatch(pentaActions.disableKeyboardJogging());
  },
  onJogStop: (axis) => {
    dispatch(rockhopperActions.putHelpers.jogStop({ axis }));
  }
}),
(stateProps, dispatchProps, ownProps) => {
  const {
    checked,
    axes
  } = stateProps;

  const {
    onEnable,
    onDisable,
    onJogStop
  } = dispatchProps;

  const onChange = (e) => {
    if(e.target.checked) {
      onEnable();
    } else {
      onDisable();
      for(let { index } of axes) {
        onJogStop(index);
      }
    }
  };

  return {
    ...ownProps,
    checked,
    onChange
  }
}
)(Switch);
