import { connect } from 'react-redux';
import Control from '../components/Control.js';
import actions from '../modules/rockhopper/actions';
import { selectMaxLinearVelocity, selectMachineStatus, selectDelayedRapidRatePercentage, selectDelayedMaxVelPercentage, selectDelayedFeedratePercentage, selectDelayedSpindle } from '../modules/rockhopper/selectors';
import { tick } from '../util/sounds';
import produce from 'immer';

export default connect( 
(state, ownProps) => {
  return { 
    maxVel: selectDelayedMaxVelPercentage(state),
    feedrate: selectDelayedFeedratePercentage(state),
    spindle: selectDelayedSpindle(state),
    maxRapid: selectDelayedRapidRatePercentage(state),
    machineStatus: selectMachineStatus(state),
    maxLinearVelocity: selectMaxLinearVelocity(state)
  }
}, 
(dispatch, ownProps) => ({
  onCycleStart: ()   => dispatch( actions.put.cycleStartCmd() ),
  onStep: () => dispatch(actions.put.autoCmd({ data: [ "AUTO_STEP" ] })),
  onFeedHold: () => dispatch( actions.put.feedHoldCmd() ),
  onSetMaxVelocity: (newMaxVel, dv, maxLinearVelocity) => {
    dispatch( actions.delayed.update.halpinHaluiMaxVelocityValue({ data: newMaxVel/100*maxLinearVelocity }));
    dispatch( actions.delayed.put.maxvel({ data: [newMaxVel/100*maxLinearVelocity ] }));
    tick.play();
  },
  onSetFeedrate: (newFeedrate, dv) => {
    dispatch( actions.delayed.update.feedrate({ data: [newFeedrate/100] }));
    dispatch( actions.delayed.put.feedrate({ data: [newFeedrate/100 ]}));
    tick.play();
  },
  onSetMaxRapid: (newMaxRapid, dv) => {
    dispatch( actions.delayed.update.rapidrate({ data: [ newMaxRapid/100 ] }));
    dispatch( actions.delayed.put.rapidrate({ data: [ newMaxRapid/100 ] }));
    tick.play();
  },
  onSetSpindle: (spindle) => {
    dispatch( actions.delayed.update.spindle({ data: spindle }));
    dispatch( actions.delayed.put.spindleoverride({ data: [spindle[0].override] }));
    tick.play();
  }
}),
(stateProps, dispatchProps, ownProps) => {
  const {
    maxVel,
    feedrate,
    spindle,
    maxRapid,
    machineStatus,
    maxLinearVelocity
  } = stateProps;

  const spindlerate = Math.round(100*spindle[0].override);

  const {
    onCycleStart,
    onFeedHold,
    onStep,
    onSetMaxVelocity,
    onSetFeedrate,
    onSetMaxRapid,
    onSetSpindle
  } = dispatchProps;

  return {
    machineStatus,
    maxVel,
    feedrate,
    maxRapid,
    spindlerate,
    onCycleStart,
    onFeedHold,
    onSetFeedrate,
    onSetMaxRapid,
    onStep,
    onSetSpindlerate: (newSpindlerate, dv) => {
      onSetSpindle(
        Object.freeze(produce(spindle, (draft) => {
          draft[0].override = newSpindlerate/100;
        }))
      );
    },
    onSetMaxVelocity: (newMaxVel, dv) => onSetMaxVelocity(newMaxVel, dv, maxLinearVelocity),
  };
})(Control);
