import React from 'react';
import Panel from './Panel';
import ThreeBarGauge from './ThreeBarGauge';

import useTranslation from '../hooks/useTranslation';
import useUnits from '../hooks/useUnits';

const Feed = (props) => {
  const {
    chipLoad,
    maxChipLoad,
    feedrate,
    power,
    surfaceFootage,
    maxSurfaceFootage
  } = props;

  const { t } = useTranslation();
  const { toDisplayUnits } = useUnits();
  const chipLoadDisplayUnits = toDisplayUnits(chipLoad);
  const feedrateDisplayUnits = toDisplayUnits(feedrate);
  const powerDisplayUnits = toDisplayUnits(power);
  const surfaceFootageDisplayUnits = toDisplayUnits(surfaceFootage, 'surfaceSpeed');
  
  const maxPower = 706; // 120+120+120+173+173 - sum of rated Peak Power of each solo motor, should be retrieved from Rockhopper for compatibility with other machines

  const minChipLoad = 0;
  const maxChipLoadDisplay = toDisplayUnits(maxChipLoad).value;
  const minSurfaceFootage = 0;
  const maxSurfaceFootageDisplay = toDisplayUnits({ value: maxSurfaceFootage.value || 1, unit: surfaceFootage.unit }, 'surfaceSpeed').value;

  return <Panel label="Feed">
    <ThreeBarGauge value0={powerDisplayUnits.value}
                   min0={0}
                   max0={maxPower}
                   units0={powerDisplayUnits.unit}
                   decimalPoints0={0}
                   label0={t("power")}

                   value1={chipLoadDisplayUnits.value}
                   min1={minChipLoad}
                   max1={maxChipLoadDisplay}
                   units1={chipLoadDisplayUnits.unit}
                   decimalPoints1={4}
                   label1={t("chipLoad")}

                   value2={surfaceFootageDisplayUnits.value}
                   min2={minSurfaceFootage}
                   max2={maxSurfaceFootageDisplay}
                   units2={surfaceFootageDisplayUnits.unit}
                   decimalPoints2={2}
                   label2={t("surfaceSpeed")}

                   centerValue={feedrateDisplayUnits.value}
                   centerLabel={feedrateDisplayUnits.unit}
                   />
  </Panel>;
};
export default Feed;
