import React, { useRef, useEffect, useState } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import AceEditor from 'react-ace';

import 'ace-builds/src-min-noconflict/mode-gcode';
import 'ace-builds/src-min-noconflict/theme-chrome';
import 'ace-builds/src-min-noconflict/ext-searchbox';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import Tooltip from '@material-ui/core/Tooltip';
import DownloadIcon from '@material-ui/icons/GetApp';
import useMeasure from 'react-use-measure';
import produce from 'immer';
import useTranslation from '../hooks/useTranslation';

import fileCache from '../modules/rockhopper/file-cache';

const newLineCharacter = "\n".charCodeAt(0);
const carriageReturnCharacter = "\r".charCodeAt(0);
const useStyles = makeStyles((theme) => ({
  ace: {
    '&::-webkit-scrollbar': {
      height: ".375rem",
      width: ".375rem"
    },
    '&::-webkit-scrollbar-track': {
      backgroundColor: "transparent"
    },
    '&::-webkit-scrollbar-thumb': {
      backgroundColor: "rgba(0,0,0,.2)"
    },
    '&::-webkit-scrollbar-thumb:vertical': {
      minHeight: "1.5rem"
    },
    '&::-webkit-scrollbar-thumb:horizontal': {
      minWidth: "1.5rem"
    }
  },
  file: {
    fontWeight: "bold"
  },
  progress: {
    marginLeft: theme.spacing(1)
  },
  downloadingProgress: {
    marginLeft: theme.spacing(1)
  }
}))

const GCodeEditor = (props) => {
  const { t } = useTranslation();
  const {
    file,
    checksum,
    progress = { size: 0, bytesLoaded: 0},
    line,
    onDownloadProgram,
  } = props;
  const classes = useStyles();
  const aceRef = useRef(null);
  const [ expanded, setExpanded ] = useState(false);
  const [ ref, bounds ] = useMeasure();
  const [ loadingState, setLoadingState ] = useState({
    checksum: null,
    fileDataIndex: 0,
    buffer: null
  });

  // Ace editor can hang for a second or two when a very large number of lines are 
  // inserted at once (hundreds of thousands to millions), so let's incrementally 
  // insert chunks of lines over time to prevent that from happening.
  useEffect(() => {
    // fetch the ace editor object from react-ace ref
    const editor = aceRef.current.editor;

    // set various options
    editor.container.style.height = expanded ? "800px" : "170px";
    editor.setOption("vScrollBarAlwaysVisible", true);
    editor.setOption("scrollPastEnd", .5);
    editor.setShowPrintMargin(false);
    editor.setReadOnly(true);
    editor.resize(true);
    if(line) {
      editor.gotoLine(line+1, Infinity);
    }

    // The checksum is used to determine if the actual content has changed
    // because the content can be loaded in over time. loadingState.buffer
    // is an ArrayBuffer of the file content and while downloading will be
    // populated over time. progress.bytesLoaded will contain the number of
    // bytes that have been loaded into the buffer and progress.size is the
    // total size of the file.
    if(checksum !== loadingState.checksum) {
      // so when the checksum changes, we need to 
      // reset our internal state to begin loading new data.
      setLoadingState(produce(loadingState, draft => {
        draft.checksum = checksum;
        draft.fileDataIndex = 0;
        draft.buffer = null;
      }));
      return;
    }

    if(loadingState.buffer) {
      const doc = editor.getSession().getDocument();
      const dataView = new Uint8Array(loadingState.buffer);
      // We've successfully fetched a ArrayBuffer object from the fileCache

      let timeoutID;
      const insertData = (from, bytesOfData, loaded, doTimeout) => {
        const maxIndex = Math.min(from+bytesOfData, loaded);
        const textDecoder = new TextDecoder();
        const newLines = [];
        let lineBeginning = from;
        for(let i = from; i < maxIndex; i++) {
          if(dataView[i] === newLineCharacter) {
            // found the end of a line
            if(i-1 >= 0 && dataView[i-1] === carriageReturnCharacter) {
              // ends with \r\n
              newLines.push(textDecoder.decode(new Uint8Array(loadingState.buffer, lineBeginning, i-lineBeginning-1)))
            } else {
              // ends with \n
              newLines.push(textDecoder.decode(new Uint8Array(loadingState.buffer, lineBeginning, i-lineBeginning)))
            }
            lineBeginning = i+1;
          }
        }
        if(doTimeout) {
          timeoutID = setTimeout(() => {
            doc.insertFullLines(doc.getLength()-1, newLines);
            setLoadingState(produce(loadingState, draft => { draft.fileDataIndex = lineBeginning }));
          }, 1);
        } else {
          doc.insertFullLines(doc.getLength()-1, newLines);
          setLoadingState(produce(loadingState, draft => { draft.fileDataIndex = lineBeginning }));
        }
      };
      if(loadingState.fileDataIndex < progress.bytesLoaded) {
        if(loadingState.fileDataIndex === 0) {
          editor.setValue("");
          insertData(loadingState.fileDataIndex, 1000000, progress.bytesLoaded, false);
        } else {
          insertData(loadingState.fileDataIndex, 1000000, progress.bytesLoaded, true);
        }
        return () => clearTimeout(timeoutID);
      }
    } else {
      fileCache.getItem(checksum).then((b) => {
        setLoadingState(produce(loadingState, draft => { draft.buffer = b }));
      });
    }
  }, [ expanded, progress, aceRef, checksum, loadingState, setLoadingState, line ]);

  const handleDownload = () => { onDownloadProgram(file) };

  const toggleExpanded = () => setExpanded(!expanded);
  const downloadingProgress = (100*progress.bytesLoaded/progress.size);
  const aceProgress = 100*(Math.min(loadingState.fileDataIndex, progress.size))/progress.size;
  const loadingProgress = Math.min(downloadingProgress,aceProgress);
  // show loading progress is we're downloading or if we're loading a file greater than 10MB (a 10MB file takes time to
  // load in even if we have it cached in IndexedDB)
  const showLoadingProgress = downloadingProgress < 100 || (aceProgress < 100 && progress.size > 10485760);
  const width = Math.floor(bounds.width || 500).toString();
  return <>
      { showLoadingProgress ? 
        <Tooltip placement="top" title={ loadingProgress.toFixed(2) + "%"}>
          <CircularProgress classes={{ root: classes.progress }} variant="determinate" size={15} thickness={4} value={loadingProgress}/> 
        </Tooltip>
        : null 
      } 
    <div className={classes.ace} ref={ref}>
    <AceEditor mode="gcode" 
               theme="chrome" 
               name="GCodeEditor"
               ref={aceRef}
               width={width}
               />
    <Button onClick={toggleExpanded}>{ expanded ? t("retract") : t("expand") }</Button>
    <Tooltip placement="top" title={t("downloadCurrentFile")}>
    <Button onClick={handleDownload} disabled={showLoadingProgress}><DownloadIcon/></Button>
    </Tooltip>
    </div>
 </>
};
export default GCodeEditor;
