import i18n from 'i18next';
import { initReactI18next } from 'react-i18next';
import Backend from 'i18next-http-backend';
import enCommon from "./locales/en/common.json";
import enIndustry from "./locales/en/industry.json";
import enIntuitive from "./locales/en/intuitive.json";

const resources = {
  en: {
    common: enCommon,
    intuitive: enIntuitive,
    industry: enIndustry,
  },
};

i18n.use(Backend)
  .use(initReactI18next)
  .init({
    lng: "en",
    fallbackLng: "en",
    preload:  ["en"],
    supportedLngs: ["en", "de", "es", "sv", "jp"], 
    ns: ["common", "industry", "intuitive"],
    defaultNS: "common",
    debug: false,
  });

i18n.addResourceBundle("en", "common", resources.en.common);
i18n.addResourceBundle("en", "industry", resources.en.industry);
i18n.addResourceBundle("en", "intuitive", resources.en.intuitive);

export default i18n;
