const Display = (props) => {
//  const {
//    positions
//  } = props;
  
  const { t } = useTranslation();
//  const classes = useStyles();

  const options = [
    { label: "Auto Width", content: "width=device-width, initial-scale=1, maximum-scale=1, user-scalable=no" },
    { label: "Max Width", content: "width=1024" },
    { label: "Max Height", content: "height=1366" },
  ];

  return <Panel label={t("mobileDisplay")}>
    { options.map(({ label, content }) => (
      <div key={label}>
      <Button onClick={() => document.getElementById("viewport").content = content}>{ label }</Button>
      </div >
    )) }
  </Panel>;
};
export default Display;
