import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { WARNING, INFO, ERROR } from '../modules/rockhopper/constants';
import { useTransition, animated } from 'react-spring';
import classnames from 'classnames';
import InfoIcon from '@material-ui/icons/Info';
import WarningIcon from '@material-ui/icons/Warning';
import ErrorIcon from '@material-ui/icons/Error';
import Paper from '@material-ui/core/Paper';

const AnimatedPaper = animated(Paper);

const useStyles = makeStyles((theme) => ({
  container: {
    position: "fixed",
    right: 0,
    bottom: 64
  },
  item: {
    width: 350, // +16=366
    height: 88, // +16=104
    float: "right",
    clear: "right",
    margin: theme.spacing(1),
    padding: theme.spacing(1),
    backgroundColor: theme.palette.common.white,
    borderRadius: 4,
    position: "absolute",
    bottom: 0,
    right: 0
  },
  info: {
    backgroundColor: theme.palette.primary.verylight,
  },
  warning: {
    backgroundColor: theme.palette.secondary.verylight,
  },
  error: {
    backgroundColor: theme.palette.error.verylight,
  },
  infoIcon: {
    color: theme.palette.primary.main,
    float: "left",
    paddingRight: theme.spacing(1)
  },
  warningIcon: {
    color: theme.palette.secondary.main,
    float: "left",
    paddingRight: theme.spacing(1)
  },
  errorIcon: {
    color: theme.palette.error.main,
    float: "left",
    paddingRight: theme.spacing(1)
  },
  message: {
  }
}));
const Popups = (props) => {
  const {
    messages
  } = props;

  const classes = useStyles();

  const indexedMessages = messages.map((msg, index) => ({ ...msg, index }));

  const transitions = useTransition(indexedMessages, msg => msg.id, {
    from: ({ index }) => ({ opacity: 0, transform: "translate(0, -100%)"}),
    enter: ({ index }) => ({ opacity: 1, transform: "translate(0, 0)"}),
    leave: ({ index }) => ({ opacity: 0, transform: "translate(0, 100%)"}),
    update: ({ index }) => ({ bottom: (indexedMessages.length-index-1)*112 }),
  });

  return (
  <div className={classes.container}>
    { 
      transitions.map(({ item: msg, props }) => {
        let icon;
        switch(msg.type) {
          case ERROR:
            icon = <ErrorIcon className={classes.errorIcon}/>
            break;
          case WARNING:
            icon = <WarningIcon className={classes.warningIcon}/>
            break;
          case INFO:
            icon = <InfoIcon className={classes.infoIcon}/>
            break;
          default:
            icon = null;
        }

        return <AnimatedPaper style={props} elevation={2} key={msg.id} className={classnames(classes.item, classes[msg.type])}>
          { icon } <span className={classes.message}>{msg.message}</span>
        </AnimatedPaper>;
      })
    }
  </div>);
};
export default Popups;
