import React from 'react';
import { useTheme, makeStyles } from '@material-ui/core/styles';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import DisconnectedIcon from '@material-ui/icons/Block';
import ConnectedIcon from '@material-ui/icons/RadioButtonUnchecked';
import { useTranslation } from 'react-i18next';
import StatusItem from './StatusItem';
import TwoLineStatusItem from './TwoLineStatusItem';
import ActiveCodesStatusItem from '../containers/ActiveCodesStatusItem';
import MessageBadgesStatusItem from '../containers/MessageBadgesStatusItem';

const useStyles = makeStyles((theme) => ({
  bar: {
  },
  left: {
    zIndex: 200,
    position: "fixed",
    bottom: 0,
    left: 0,
    height: 63
  },
  right: {
    position: "fixed",
    bottom: 0,
    right: 0,
    lineHeight: "66px",
    paddingRight: theme.spacing(1),
    verticalAlign: "middle",
    zIndex: 200,
    height: 63
  },
  paper: {
    backgroundColor: theme.palette.text.primary,
    padding: theme.spacing(1)
  },
  statusSpan: {
    textAlign: "center",
    verticalAlign: "middle",
    display: "inline-block",
    textTransform: "uppercase",
    whiteSpace: 'nowrap',
    lineHeight: "63px"
  },
  statusItem: {
    ...theme.typography.statusItem,
  },
}));

const StatusBar = (props) => {
  const {
    connected,
    rtcSeconds,
    machineStatus
  } = props;
  
  const { t } = useTranslation();
  const classes = useStyles();
  const theme = useTheme();
  const isSM = useMediaQuery(theme.breakpoints.down('sm'));

  const hours = Math.floor(rtcSeconds/60/60) || 0;
  const minutes = Math.floor(rtcSeconds/60-hours*60) || 0;
  const seconds = rtcSeconds-hours*60*60-minutes*60 || 0;

  return <>
    <div className={classes.left}>
    { connected ? <StatusItem color="primary"><ConnectedIcon className={classes.statusSpan}/>{ isSM ? <span className={classes.statusSpan}></span> : <span className={ classes.statusSpan }>{t('connected')}</span> }</StatusItem> :
                  <StatusItem color="secondary"><DisconnectedIcon className={classes.statusSpan}/>{ isSM ? <span className={classes.statusSpan}></span> : <span className={ classes.statusSpan }>{t('disconnected')}</span> }</StatusItem> }
    <ActiveCodesStatusItem/>
    </div>
    <div className={classes.right}>
   {/*  <StatusItem>{ t(machineStatus) }</StatusItem>
     <StatusItem>{ ("0" + hours).slice(-2) }:{ ("0" + minutes).slice(-2)}:{("0" + seconds.toFixed(1)).slice(-4)}</StatusItem> */}
     <TwoLineStatusItem>{ t(machineStatus) }<br/>{ ("0" + hours).slice(-2) }:{ ("0" + minutes).slice(-2)}:{("0" + seconds.toFixed(1)).slice(-4)}</TwoLineStatusItem>
     <MessageBadgesStatusItem/>
    </div>
  </>;
};
export default StatusBar;
