import React, { useState } from 'react';
import produce from 'immer';

import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import Paper from '@material-ui/core/Paper';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableContainer from '@material-ui/core/TableContainer';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import Tooltip from '@material-ui/core/Tooltip';

import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import HelpIcon from '@material-ui/icons/Help';

import { makeStyles } from '@material-ui/styles';

import Panel from './Panel';
import KeyboardCaptureDialog from './KeyboardCaptureDialog';
import useTranslation from '../hooks/useTranslation';
import { SHORTCUTS } from '../modules/rockhopper/constants';


const dividerStyles = makeStyles((theme) => ({
  cell: {
    textAlign: 'center',
    fontWeight: 'bold',
    backgroundColor: theme.palette.secondary.main,
    padding: 8,
  },
  row: {
    lineHeight: '16px',
  }
}));

const DividerRow = (props) => {
  const {
    text,
  } = props;

  const classes = dividerStyles();

  return <TableRow className={classes.row}>
    <TableCell className={classes.cell} colSpan={3}>{text}</TableCell>
  </TableRow>
}

const useStyles = makeStyles((theme) => ({
  bold: {
    fontWeight: "bold",
  },
  tableContainer: {
    maxHeight: 680,
  },
  sectionDivider: {
    lineHeight: "16px",
    height: 16,
    width: 14,
    textAlign: "center",
    fontWeight: "bold",
    padding: 8,
  }
}));

const KeyboardShortcutsConfig = (props) => {
  const {
    shortcuts,
    jogKeys,
    jogModifiers,
    axisSelectJogShortcuts,
    otherShortcuts,
    shortcutsMap,
    trajCoords,

    onSubmitShortcuts
  } = props;

  const classes = useStyles();
  const [ captureDialogState, setCaptureDialogState ] = useState({open: false, category: null, key: "", value: "", modifiersOnly: false, singleKeyOnly: false});
  const { t } = useTranslation();
  const handleOpenCaptureDialog = (newDialogState) => {
    setCaptureDialogState({...newDialogState, open: true});
  };
  const handleSubmitCaptureDialog = (newShortcut) => {
    const newShortcuts = produce(shortcuts, (draft) => { 
      draft[captureDialogState.category] = draft[captureDialogState.category] || {};
      draft[captureDialogState.category][captureDialogState.key] = newShortcut;
    });
    onSubmitShortcuts(newShortcuts);
    // onSetShortcut({key: captureDialogState.key, value: newShortcut})
    setCaptureDialogState({...captureDialogState, open: false});
  };
  const handleRemoveKeybinding = (category, key) => {
    const newShortcuts = produce(shortcuts, (draft) => { draft[category][key] = "" });
    onSubmitShortcuts(newShortcuts);
  }

  return <Panel label="Keybindings">
    <TableContainer component={Paper} className={classes.tableContainer}>
      <Table stickyHeader aria-label="simple table">
        <TableHead>
          <TableRow>
            <TableCell>{t("Command")}</TableCell>
            <TableCell>{t("Keybinding")}</TableCell>
            <TableCell></TableCell>
          </TableRow>
        </TableHead>
        <TableBody>

          <DividerRow text={t("Axis-select Jogging")} className={classes.sectionDivider}/>
          {trajCoords && Object.keys(SHORTCUTS.AXIS_SELECT_JOG).map(key => {
            let displayName;
            if(key.startsWith("select")){
              if(!trajCoords.includes(key.slice(-1))){
                return null;
              }
              displayName = t('selectAxis') + ' ' + t(key.slice(-1));
            } else if(key.startsWith("jog")){
              displayName = t('jog') + ' ' + key.slice(-1);
            }
            else{
              displayName = t(key);
            }
            return(
            <TableRow key={key}>
              <TableCell>{displayName}</TableCell>
              <TableCell className={classes.bold}>{axisSelectJogShortcuts[key]}</TableCell>
              <TableCell>
                <ButtonGroup>
                  <Tooltip title={t('tooltip_EditShortcut')}>
                    <Button id={"edit_" + key} variant="outlined" onClick={() => handleOpenCaptureDialog({ title: t(key), key: key, value: jogModifiers[key], category: "axisSelectJog", modifiersOnly: false, singleKeyOnly: false } ) } >
                      <EditIcon/>
                    </Button>
                  </Tooltip>
                {axisSelectJogShortcuts[key] && (
                  <Tooltip title={t('tooltip_DeleteShortcut')}>
                    <Button variant="outlined" onClick={() => handleRemoveKeybinding('axisSelectJog', key) } >
                      <DeleteIcon/>
                    </Button>
                  </Tooltip>
                )}
                </ButtonGroup>
              </TableCell>
            </TableRow>
            )
          })}

          <DividerRow text={t("jogging")} className={classes.sectionDivider}/>
          {Object.keys(SHORTCUTS.JOG_MODIFIERS).map(key => {
            return(
            <TableRow key={key}>
              <TableCell>{t(key + "JogModifier")}
                {key === "continuous" && <Tooltip title={t('tooltip_ContinuousJog')}><HelpIcon/></Tooltip>}
              </TableCell>
              <TableCell className={classes.bold}>{jogModifiers[key]}</TableCell>
              <TableCell>
                <ButtonGroup>
                  <Tooltip title={t('tooltip_EditShortcut')}>
                    <Button variant="outlined" onClick={() => handleOpenCaptureDialog({ title: t(key + "JogModifier"), key: key, value: jogModifiers[key], category: "jogModifiers", modifiersOnly: true, singleKeyOnly: false } ) } >
                      <EditIcon/>
                    </Button>
                  </Tooltip>
                {jogModifiers[key] && (
                  <Tooltip title={t('tooltip_DeleteShortcut')}>
                    <Button variant="outlined" onClick={() => handleRemoveKeybinding('jogModifiers', key) } >
                      <DeleteIcon/>
                    </Button>
                  </Tooltip>
                )}
                </ButtonGroup>
              </TableCell>
            </TableRow>
            )
          })}
          {trajCoords && Object.keys(SHORTCUTS.JOG).map(key => {
            if(trajCoords.indexOf(key[3]) !== -1){
              return(
              <TableRow key={key}>
                <TableCell>{t("jog") + " " + key.slice(3)}</TableCell>
                <TableCell className={classes.bold}>{jogKeys[key]}</TableCell>
                <TableCell>
                  <ButtonGroup>
                    <Tooltip title={t('tooltip_EditShortcut')}>
                      <Button variant="outlined" onClick={() => handleOpenCaptureDialog({ title: t("jog") + " " + key.slice(3), key: key, value: jogKeys[key], category: "jog", modifiersOnly: false, singleKeyOnly: true })}>
                        <EditIcon/>
                      </Button>
                    </Tooltip>
                  {jogKeys[key] && (
                    <Tooltip title={t('tooltip_DeleteShortcut')}>
                      <Button variant="outlined" onClick={() => handleRemoveKeybinding('jog', key) } >
                        <DeleteIcon/>
                      </Button>
                    </Tooltip>
                  )}
                  </ButtonGroup>
                </TableCell>
              </TableRow>
              )
            }
            else return null
          })}
          <DividerRow text={t("other")} className={classes.sectionDivider}/>
          {Object.keys(SHORTCUTS.OTHER).map(key => {
            return(
            <TableRow key={key}>
              <TableCell>{t(key)}</TableCell>
              <TableCell className={classes.bold}>{otherShortcuts[key]}</TableCell>
              <TableCell>
                <ButtonGroup>
                  <Tooltip title={t('tooltip_EditShortcut')}>
                    <Button variant="outlined" onClick={() => handleOpenCaptureDialog({title: t(key), key: key, value: otherShortcuts[key], category: "other", modifierOnly: false, singleKeyOnly: false})}>
                      <EditIcon/>
                    </Button>
                  </Tooltip>
                {otherShortcuts[key] && (
                  <Tooltip title={t('tooltip_DeleteShortcut')}>
                    <Button variant="outlined" onClick={() => handleRemoveKeybinding('other', key) } >
                      <DeleteIcon/>
                    </Button>
                  </Tooltip>
                )}
                </ButtonGroup>
              </TableCell>
            </TableRow>
            )
          })}
        </TableBody>
      </Table>
    </TableContainer>
    <KeyboardCaptureDialog
      open={captureDialogState.open}
      key={captureDialogState.key}
      value={captureDialogState.value}
      modifiersOnly={captureDialogState.modifiersOnly}
      singleKeyOnly={captureDialogState.singleKeyOnly}
      reservedKeys={new Set(Object.values(jogKeys))}
      reservedCombinations={new Set(Object.keys(shortcutsMap).concat(Object.values(jogModifiers)))}
      onSubmit={handleSubmitCaptureDialog}
      onClose={() => { setCaptureDialogState({...captureDialogState, open: false}) } }
      title={t("Keybinding") + ": " + t(captureDialogState.title)}
      submitBtnLabel={t("Set")}
      cancelBtnLabel={t("Cancel")}
    />
  </Panel>;
};
export default KeyboardShortcutsConfig;
