import React, {useCallback} from 'react';

import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableRow from '@material-ui/core/TableRow';
import Checkbox from '@material-ui/core/Checkbox';
import Button from '@material-ui/core/Button';
import ButtonGroup from '@material-ui/core/ButtonGroup';
import EditIcon from '@material-ui/icons/Edit';
import ClearIcon from '@material-ui/icons/Clear';
import Tooltip from '@material-ui/core/Tooltip';

import { SORT_ORDER } from '../modules/pentaui/constants';
import SubmittableTextField from './SubmittableTextField';
import useTranslation from '../hooks/useTranslation';

function descendingComparator(a, b, orderBy) {
  if (b[orderBy] < a[orderBy]) {
    return -1;
  }
  if (b[orderBy] > a[orderBy]) {
    return 1;
  }
  return 0;
}

function getComparator(order, key) {
  return order === SORT_ORDER.DESCENDING
    ? (a, b) => descendingComparator(a, b, key)
    : (a, b) => -descendingComparator(a, b, key);
}

function stableSort(array, comparator) {
  const stabilizedThis = array.map((el, index) => [el, index]);
  stabilizedThis.sort((a, b) => {
    const order = comparator(a[0], b[0]);
    if (order !== 0) return order;
    return a[1] - b[1];
  });
  return stabilizedThis.map((el) => el[0]);
}

const MdiFavoritesTableBody = (props) =>  {
  const { 
    favorites,
    filter,
    filterKey,
    placeholderText,
    sortOrder,
    sortKey,
    onSelectItem,
    onNameItem,
    onClickItem,
    onClickEditKeybind,
    onClickDeleteKeybind,
    selected,
  } = props;
  const { t } = useTranslation();
  const comparator = getComparator(sortOrder, sortKey)
  const sortedFavorites = stableSort(favorites, comparator)
  
  // const [page, setPage] = React.useState(0);
  // const [dense, setDense] = React.useState(false);
  // const [rowsPerPage, setRowsPerPage] = React.useState(5);

  const isSelected = (key) => selected.has(key);

  // const emptyRows = rowsPerPage - Math.min(rowsPerPage, favorites.length - page * rowsPerPage);

  const handleClickItem = useCallback((val) => {
    if(onClickItem){
      onClickItem(val)
    }
  }, [onClickItem])

  return (
    <TableBody>
      {
        sortedFavorites.map((row, index) => 
        {
          const isItemSelected = isSelected(row.cmd);
          const labelId = `enhanced-table-checkbox-${index}`;
          
          if((row[filterKey]).includes(filter))
          {
            return (
              <TableRow
                hover
                role="checkbox"
                aria-checked={isItemSelected}
                tabIndex={-1}
                key={index}
                selected={isItemSelected}
              >
                <TableCell 
                  padding="checkbox"
                  onClick={(event) => onSelectItem(event, row.cmd)}
                >
                  <Checkbox
                    checked={isItemSelected}
                    inputProps={{ 'aria-labelledby': labelId }}
                  />
                </TableCell>
                <TableCell>
                  <Button onClick={(e) => {handleClickItem(row.cmd)}}>
                    {row.cmd}
                  </Button>
                </TableCell>
                <TableCell>
                  <SubmittableTextField placeholder={placeholderText} value={row.name} onSubmit={(e) => onNameItem(e, row)}/>
                </TableCell>
                <TableCell>
                  <ButtonGroup>
                    <Tooltip title={t('tooltip_EditShortcut')}>
                      <Button variant="outlined" onClick={() => onClickEditKeybind(row)}> 
                        {row.keybind}
                      <EditIcon/>
                      </Button>
                    </Tooltip>
                    <Tooltip title={row.keybind ? t('tooltip_DeleteShortcut') : ""}>
                      <Button variant="outlined" onClick={() => onClickDeleteKeybind( row.cmd ) } disabled={!row.keybind}>
                        <ClearIcon/>
                      </Button>
                    </Tooltip>
                  </ButtonGroup>
                </TableCell>
                <TableCell>{new Date(row.time).toLocaleString("en-US")}</TableCell>
              </TableRow>
            );
          } else return null
        })}
      {/* {emptyRows > 0 && (
        <TableRow style={{ height: (dense ? 33 : 53) * emptyRows }}>
          <TableCell colSpan={6} />
        </TableRow>
      )} */}
    </TableBody>
  );
}
export default MdiFavoritesTableBody;
