import { connect } from 'react-redux';
import App from '../components/App';

import { selectDialect } from '../modules/pentaui/selectors';
import { selectDisplayUnits } from '../modules/rockhopper/selectors';
import { selectActiveCodes } from '../modules/rockhopper/selectors';

export default connect( 
(state, ownProps) => {
  return { 
    dialect: selectDialect(state),
    displayUnits: selectDisplayUnits(state),
    activeCodes: selectActiveCodes(state),
  }
})(App);
