import React, { useState } from 'react';
import StatusItem from './StatusItem';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import ErrorIcon from '@material-ui/icons/Error';
import WarningIcon from '@material-ui/icons/Warning';
import InfoIcon from '@material-ui/icons/Info';
import Menu from '@material-ui/core/Menu';
import MenuItem from '@material-ui/core/MenuItem';
import useTranslation from '../hooks/useTranslation';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  badge: {
  },
  info: {
    color: theme.palette.text.primary,
    backgroundColor: theme.palette.primary.light
  },
  warning: {
    color: theme.palette.text.primary,
    backgroundColor: theme.palette.secondary.light
  },
  error: {
    color: theme.palette.text.primary,
    backgroundColor: theme.palette.error.light
  }
}));

const MessageBadgesStatusItem = (props) => {
  const {
    messageBadges,
    onClear
  } = props;

  const [ anchorEl, setAnchorEl ] = useState(null);
  const [ activeType, setActiveType ] = useState(null);

  const { t } = useTranslation();

  const classes = useStyles();

  const handleClick = (type) => (e) => {
    setAnchorEl(e.currentTarget);
    setActiveType(type);
  };

  const handleClear = () => {
    setActiveType(null);
    setAnchorEl(null);
    if(onClear) {
      onClear(activeType);
    }
  };

  const handleClose = () => {
    setAnchorEl(null);
    setActiveType(null);
  };

  return <><StatusItem>
{ messageBadges.info.length > 0 ?
   <IconButton color="inherit" onClick={handleClick("info")}>
   <Badge badgeContent={messageBadges.info.length} color="primary" classes={ { root: classes.badge }} anchorOrigin={ { horizontal: "right", vertical: "bottom" } }>
      <InfoIcon/>
    </Badge></IconButton> : null }
    { messageBadges.warning.length > 0 ?
   <IconButton color="inherit" onClick={handleClick("warning")}>
    <Badge badgeContent={messageBadges.warning.length} color="secondary" classes={ { root: classes.badge }} anchorOrigin={ { horizontal: "right", vertical: "bottom" } }>
      <WarningIcon/>
    </Badge>
  </IconButton>
    : null }
    { messageBadges.error.length > 0 ?
   <IconButton color="inherit" onClick={handleClick("error")}>
    <Badge badgeContent={messageBadges.error.length} color="error" classes={ { root: classes.badge }} anchorOrigin={ { horizontal: "right", vertical: "bottom" } }>
      <ErrorIcon/>
    </Badge>
  </IconButton>
    : null }
  </StatusItem>
  <Menu id="message-badges-menu" anchorEl={anchorEl} open={Boolean(activeType)} onClose={handleClose}>
    { activeType ? messageBadges[activeType].map((msg,i) => <MenuItem key={i} style={ {opacity: 1 }} disabled classes={ { root: classes[msg.type] } }>{msg.message}</MenuItem>) : null }
    <MenuItem onClick={handleClear}>{ t("clear") }</MenuItem>
  </Menu>
  </>
};
export default MessageBadgesStatusItem;
