import React from 'react';

import Box from '@material-ui/core/Box';
import Paper from '@material-ui/core/Paper';
import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  paper: {
//    border: "1px solid " + theme.palette.grey[100]
  },
  panelHeader: {
    ...theme.typography.panelHeader
  },
  headerBox: {
    position: "relative",
    height: 30,
    marginBottom: theme.spacing(1),
    display: "flex"
  },
  line: {
    borderBottom: "1px solid " + theme.palette.text.primary,
    marginLeft: theme.spacing(2),
    width: "100%",
    height: 25,
    display: "block"
  },
  svg: {
    verticalAlign: "bottom",
    position: "relative",
    bottom: -2,
    left: -3
  }
}));

const Panel = (props) => {
  const {
    label,
    children
  } = props;

  const classes = useStyles();

  const angle = 20*Math.PI/180;
  const width = 80;
  const height = 6;
  const r = 3;
  const cornerR = 3;
  const curveX = r*Math.cos(angle);
  const curveY = r-r*Math.sin(angle);

  const pts = [
    [ "M", 0, cornerR ],
    [ "a", cornerR, cornerR, 0, 0, 1, cornerR, -cornerR ],
    [ "L", 60, 0 ],
    [ "a", r, r, 0, 0, 1, curveX, curveY ],
    [ "l", (height-2*curveY-1)*Math.tan(angle), (height-2*curveY-1)],
    [ "a", r, r, 0, 0, 0, curveX, curveY ],
    [ "L", cornerR, height ],
    [ "a", cornerR, cornerR, 0, 0, 1, -cornerR, -cornerR ],
    [ "Z" ]
  ];

  return <Box m={2}>
    <div className={classes.headerBox}>
      <span className={ classes.panelHeader }>{label}</span>
      <span className={classes.line}>
       <svg width={width} height={height} className={classes.svg}>
         <path d={pts.map((cmd) => cmd.join(" ")).join(" ")} fill="black"/>
       </svg>
      </span>
    </div>
    <Paper elevation={0} classes={ { root: classes.paper } }>
      <Box m={0}>
        {children}
      </Box>
    </Paper>
  </Box>;
};
export default Panel;
