export const UNITS = {
  MACHINE_UNITS : {
    ANGLE: "deg",
    ANGULAR_VELOCITY: "deg/s",
    LENGTH: "in",
    LINEAR_VELOCITY: "in/s",
    POWER: "W",
    PRESSURE: "MPa",
    SPINDLE_SPEED: "rpm",
    SURFACE_SPEED: "ft/min",
    TEMPERATURE: "C",
  },
  AXES: {
    X: 'in',
    Y: 'in',
    Z: 'in',
    A: 'deg',
    B: 'deg',
    C: 'deg',
  },
  TYPE_MAP: {
    'in': 'length',
    'mm': 'length',
    'cm': 'length',
    'pu': 'length',
    'in/s': 'linearVelocity',
    'in/min': 'linearVelocity',
    'ft/s': 'linearVelocity',
    'ft/min': 'linearVelocity',
    'mm/s': 'linearVelocity',
    'mm/min': 'linearVelocity',
    'm/s': 'linearVelocity',
    'm/min': 'linearVelocity',
    'pu/s': 'linearVelocity',
    'pu/min': 'linearVelocity',
    'deg': 'angle',
    'deg/s': 'angularVelocity',
    'deg/min': 'angularVelocity',
    'psi': 'pressure',
    'MPa': 'pressure',
    'F': 'temperature',
    'C': 'temperature',
    'rpm': 'spindleSpeed',
    'W': 'power'
  },
  DECIMALS: (unit) => {
    switch(unit){
      case 'mm':
        return 3;
      case 'mm/s':
        return 3;
      case 'mm/min':
        return 3;
      case 'deg':
        return 3;
      case 'deg/min':
        return 3;
      case 'deg/s':
        return 3;
      default:
        return 4
    }
  },

  lengthOptions: new Set(["in", "mm", "cm", "mu"]),
  angleOptions: new Set(["deg"]),
  pressureOptions: new Set(["MPa", "psi"]),
  temperatureOptions: new Set(["C", "F",]),
  linearVelocityOptions: new Set(["in/s", "in/min", "mm/s", "mm/min"]),
  angularVelocityOptions: new Set(["deg/s", "deg/min"]),
};

