import React, { useState, useCallback, useEffect } from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import IconButton from '@material-ui/core/IconButton';
import InputAdornment from '@material-ui/core/InputAdornment';
import TextField from '@material-ui/core/TextField';
import ToolTip from '@material-ui/core/Tooltip'

import ClearIcon from '@material-ui/icons/Clear';
import InsertDriveFileIcon from '@material-ui/icons/InsertDriveFile';
import UsbIcon from '@material-ui/icons/Usb';

import FileBrowser from '../containers/FileBrowser';
import useTranslation from '../hooks/useTranslation';
import { FILE_TYPES } from '../modules/rockhopper/constants';

const useStyles = makeStyles((theme) => ({
  filter: {
    width: "100%"
  },
  fileDialog: {
    height: "min(600px, 100%)",
    width: "min(500px, 100%)"
  },
  closeButton: {
    position: "absolute",
    right: theme.spacing(1),
    top: theme.spacing(1)
  },
}));

const GCodeFileBrowserDialog = (props) => {
  const {
    open, //this property should be passed in from a parent component
    gcodeMap,
    programPrefix,
    onClose,
    onFileOpen,
    onFileDelete,
    // onFileCopy,
  } = props;

  const { t } = useTranslation();

  const [ inUsb, setInUsb ] = useState(false);
  const [ selected, setSelected ] = useState({});
  useEffect(() => { setSelected([]) }, [open]);
  const [ fileFilter, setFileFilter ] = useState("");

  const handleClearFilter = useCallback((e) => {
    setFileFilter("");
  }, [ setFileFilter ]);
  const handleFilterChange = useCallback((e) => {
    setFileFilter(e.target.value);
  }, [ setFileFilter ]);
  const classes = useStyles();

  const handleToggleLocation = useCallback(() => {
    console.log('hmm')
    setSelected({})
    setInUsb(!inUsb)
  }, [inUsb, setInUsb, setSelected]);

  const handleClose = () => {
    if(onClose){
      onClose();
    }
  };
  const handleFileOpen = (path, fileType) => {
    if(onFileOpen) {
      // const rootPath = inUsb ? usbMap.mountPath : Object.keys(gcodeMap)[0];
      // const fullPath = [rootPath, path].join("/")
      // const fullPath = fileType === FILE_TYPES.USB_PROGRAM ? [usbMap.mountPath, path].join("/") : path;
      onFileOpen(path, fileType);
      handleClose();
    }
  };
  const handleSelect = useCallback((e) => {
    console.log(e.target.value)
    console.log(selected)
    if(e.target.checked) {
      setSelected({ ...selected, [e.target.value]: true });
    } else {
      setSelected({ ...selected, [e.target.value]: false });
    }
  }, [ selected, setSelected ]);
  // const handleCopySelected = useCallback((e) => {
  //   if(onFileCopy) {
  //     const copyFiles = Object.keys(selected).filter((path) => selected[path]);
  //     copyFiles.forEach((path) => {
  //       onFileCopy("/media/usb0/" + path, fileType);
  //     });
  //     setSelected({});
  //   }
  // }, [ fileType, onFileCopy, selected, setSelected ]);
  const handleDeleteSelected = useCallback((e) => {
    if(onFileDelete) {
      const fileType = inUsb ? FILE_TYPES.USB_PROGRAM : FILE_TYPES.PROGRAM;
      const deleteFiles = Object.keys(selected).filter((path) => selected[path]);
      deleteFiles.forEach((path) => {
        onFileDelete(path, fileType);
      });
      setSelected({});
    }
  }, [ inUsb, onFileDelete, selected, setSelected ]);

  return <>
  <Dialog open={open} onClose={handleClose} classes={ { paper: classes.fileDialog }}>
    <DialogTitle>
      <span>{ t("openGCode") }</span>
      <IconButton className={classes.closeButton} onClick={handleClose} size="small"><ClearIcon/></IconButton>
    </DialogTitle>
    <DialogContent>
      <TextField 
        placeholder={t("filter")} 
        value={fileFilter} 
        className={classes.filter} 
        onChange={handleFilterChange} 
        InputProps={{ endAdornment: (fileFilter.length > 0 ? <InputAdornment position="end"><IconButton size="small" onClick={handleClearFilter}><ClearIcon/></IconButton></InputAdornment> : null) }}
      />
      { gcodeMap && gcodeMap.usbPath && (
        <Button classes={{ root: classes.usbButton }} onClick={handleToggleLocation}>{ inUsb ? <>{t('viewInternalFiles')}<InsertDriveFileIcon/></> : <>{t('viewUsbFiles')}<UsbIcon/></> }</Button>
      )}
      <FileBrowser
        //Get the name for the root USB folder from the mountPath property
        fileFilter={fileFilter}
        filesMap={gcodeMap && gcodeMap.usbPath && inUsb ? gcodeMap[gcodeMap.usbPath] : gcodeMap ? gcodeMap[gcodeMap.path] : {} }
        fileType={gcodeMap && gcodeMap.usbPath && inUsb ? FILE_TYPES.USB_PROGRAM : FILE_TYPES.PROGRAM}
        rootPath={gcodeMap && gcodeMap.usbPath && inUsb ? gcodeMap.usbPath : programPrefix}
        // initialPath={[]}
        onFileOpen={(path, fileType) => (handleFileOpen(path, fileType))}
        // onFileDelete={handleDeleteSelected}
        // onHome={onHome}
        onSelectFile={handleSelect}
        selectedFiles={selected}
        // fileFilter={filter}
      />
    </DialogContent>
    <DialogActions>
      {/* <Button color="primary" onClick={handleCopySelected} disabled={Object.keys(selected).filter((path) => selected[path]).length === 0}>{t("copyToDisk")}</Button> */}
      <ToolTip title={t("tooltip_DeleteSelected")}><Button color="secondary" onClick={handleDeleteSelected} disabled={Object.keys(selected).filter((path) => selected[path]).length === 0}>{t("deleteSelected")}</Button></ToolTip>
      <Button onClick={handleClose} >{ t("cancel") }</Button>
    </DialogActions>
  </Dialog>
  </>;
};
export default GCodeFileBrowserDialog;
