import { eventChannel } from 'redux-saga';

export class DownloadQueue {
  constructor() {
    this.channel = eventChannel( (emit) => {
      // public method for adding a file to the queue
      this.addDownload = (path, fileType, callback=null) => {
        emit({ path, fileType, callback });
      };

      return () => {};
    });
  }
}

const singletonDownloadQueue = new DownloadQueue();

export default singletonDownloadQueue;
