import { useState, useEffect } from 'react';

const useFilter = (defaultValue, filter=.7) => {
  const [ value, setValue ] = useState(defaultValue);
  const [ filteredValue, setFilteredValue ] = useState(defaultValue);

  useEffect(() => {
    const performFilteringStep = () => {
      const newFilteredValue = filter*filteredValue+(1-filter)*value;
      if(Math.abs(newFilteredValue-filteredValue) > .000001) {
        setFilteredValue(newFilteredValue);
      }
    };
    const animation = window.requestAnimationFrame(performFilteringStep);
    return () => window.cancelAnimationFrame(animation);
  }, [ value, filteredValue, setFilteredValue, filter ]);

  return [ value, setValue, filteredValue ];
};
export default useFilter;
