import React from 'react';
import { arcDetails } from './util/math';

import { makeStyles } from '@material-ui/styles';

const useStyles = makeStyles((theme) => ({
  gaugeText: {
    ...theme.typography.gaugeText
  }
}));

const ArcText = (props) => {
  const {
    x1,
    y1,
    x2,
    y2,
    x3,
    y3,
    min = 0,
    max = 100,
    value = 100,
    paddingStart = 0,
    paddingEnd = 0,
    decimalPoints = 1,
    textAnchor = "middle",
    alignmentBaseline = "middle",
    fontSize = 1,
    units = ""
  } = props;

  const classes = useStyles();

  const missingPoints = (typeof(x1) === "undefined" ||
                         typeof(y1) === "undefined" ||
                         typeof(x2) === "undefined" ||
                         typeof(y2) === "undefined" ||
                         typeof(x3) === "undefined" ||
                         typeof(y3) === "undefined");
  const {
    c,
    r, 
    startAngle,
    endAngle
  } = missingPoints ? props : arcDetails(x1,y1,x2,y2,x3,y3);

  const angle = endAngle-startAngle;

  const paddingStartAngle = angle > 0 ? paddingStart : -paddingStart;
  const paddingEndAngle = angle > 0 ? paddingEnd : -paddingEnd;

  const t = (Math.min(Math.max(value, min), max)-min)/(max-min < .000001 ? 1 : max-min);
  const a = angle-paddingStartAngle-paddingEndAngle;

  const x = r*Math.cos(startAngle+paddingStartAngle+a*t);
  const y = r*Math.sin(startAngle+paddingStartAngle+a*t);

  return (
  <g transform={"translate(" + c.x + "," + c.y + ")"}>
    <text x={x} y={y} textAnchor={textAnchor} alignmentBaseline={alignmentBaseline} style={ { fontSize: fontSize, letterSpacing: .2 }} className={classes.gaugeText}>{ value.toFixed(decimalPoints) + " " + units }</text>
  </g>
  );
};
export default ArcText;
