import React from 'react';
import { MenuItem, Select } from '@material-ui/core';
import FormControl from '@material-ui/core/FormControl';
import InputLabel from '@material-ui/core/InputLabel';

import { useTranslation } from 'react-i18next';
import { makeStyles } from '@material-ui/styles';

import Panel from './Panel';

const useStyles = makeStyles((theme) => ({
  formControl: {
  },
  select: {
    marginTop: theme.spacing(1),
    marginLeft: theme.spacing(1),
  },
}));

const languages = {
  "en": "English",
  "es": "Espaol",
  "de": "Deutsche",
  "jp": "",
  "sv": "Svenska",
};

const Language = (props) => {
  const {
    // language,
    // onChangeLanguage,
    dialect,
    onChangeDialect,
  } = props;

  const classes = useStyles();
  const { t, i18n } = useTranslation();

  const getCurrentLanguage = () => {
    return languages[i18n.language];
  };

  const changeLanguage = (newLanguageCode) => {
    i18n.changeLanguage(newLanguageCode);
    // onChangeLanguage(newLanguageCode);
  };

  const changeDialect = (newDialect) => {
    onChangeDialect(newDialect);
  };

  const languageMenuItems = Object.keys(languages).map( 
    (code) => (
      <MenuItem
       key={code}
       value={languages[code]}
       onClick={() => {changeLanguage(code)}}
      >
        {languages[code]}
      </MenuItem>
    )
  );

  return <Panel label={t("language")}>
    <FormControl className={classes.formControl}>
      <InputLabel id="current-language-label">{t("language")}</InputLabel>
      <Select 
        labelId="current-language-label"
        value={getCurrentLanguage()}
        className={classes.select}
      >
        {languageMenuItems}
      </Select>
    </FormControl>
    <FormControl className={classes.formControl}>
      <InputLabel id="dialect-label">{t("dialect")}</InputLabel>
      <Select
        labelId="dialect-label"
        value={dialect}
        className={classes.select}
      >
        <MenuItem
          value="industry"
          onClick={() => {changeDialect("industry");}}
        >
          {t("industry")}
        </MenuItem>
        <MenuItem
          value="intuitive"
          onClick={() => {changeDialect("intuitive");}}
        >
          {t("intuitive")}
        </MenuItem>
      </Select>
    </FormControl>
  </Panel>
}
export default Language;
